import React from 'react';

import { Button, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';

import Icon from '../../../common/icon';
import { useMediaQuery } from '../../../common/utils/common';

const EmptyStateWrapper = styled(Stack)(({ theme }) => ({
  borderRadius: '8px',
  border: theme.palette.campaign.new.border,
  backgroundColor: theme.palette.campaign.new.background,
  height: '50vh',
  marginTop: '30px',
  '& button': {
    backgroundColor: theme.palette.primary.main,
    color: '#fff',
    padding: '12.5px 40px!important'
  },
  '& button:hover': {
    backgroundColor: theme.palette.primary.light
  }
}));
const CampaignItemsEmptyState = ({ header, subheader, ctaLabel, ctaAction }) => {
  const matches = useMediaQuery('(max-width:540px)');
  return (
        <EmptyStateWrapper direction='row' align='center' justify='center'>
            <Stack direction='column' align='center' justify='center' spacing={1}>
                <Icon icon='EmptyState' size={80} sx={{ color: '#94a3b8', marginBottom: '30px' }}/>
                <Typography sx={{
                  fontSize: '18px',
                  fontWeight: '700',
                  textAlign: matches ? 'center' : 'start'
                }}>{header}</Typography>
                <Typography
                    sx={{ fontSize: '12px', paddingBottom: '30px', textAlign: matches ? 'center' : 'start' }}>{subheader}</Typography>
                <Button onClick={ctaAction} size='lg'>{ctaLabel}</Button>
            </Stack>

        </EmptyStateWrapper>
  );
};

export default CampaignItemsEmptyState;
