import { configureStore } from '@reduxjs/toolkit';
import { persistReducer } from 'redux-persist';
import autoMergeLevel2 from 'redux-persist/lib/stateReconciler/autoMergeLevel2';
import storage from 'redux-persist/lib/storage';
import thunk from 'redux-thunk';

import { apiSlice } from '../../common/api/campaign';
import { apiLinkSlice } from '../../common/api/links';
import { apiProductSlice } from '../../common/api/products';
import { apiSettingsSlice } from '../../common/api/settings';
import { yithApiProductSlice } from '../../common/api/yith-products';
import campaignSliceReducer from '../slices/editor/campaign-slice';
import panelSliceReducer from '../slices/settings/settings-slice';
import noticesSliceReducer from '../slices/view/notices-slice';
import campaignsViewReducer from '../slices/view/view-campaigns-list';

export const store = configureStore({
  reducer: {
    notices: noticesSliceReducer,
    campaignsView: campaignsViewReducer,
    campaign: campaignSliceReducer,
    panelConfig: panelSliceReducer,
    apiCampaign: apiSlice.reducer,
    apiProduct: apiProductSlice.reducer,
    apiSettings: apiSettingsSlice.reducer,
    yithApiProduct: yithApiProductSlice.reducer,
    apiLink: apiLinkSlice.reducer
  },
  devTools: process.env.NODE_ENV !== 'production',
  middleware: [thunk, apiSlice.middleware, apiProductSlice.middleware, yithApiProductSlice.middleware, apiSettingsSlice.middleware, apiLinkSlice.middleware]
});
