import React from 'react';

import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import PreviewImage from '../../elements/preview-image';
import WizardPreview from '../../elements/wizard-preview';

import CategoryDiscount from './index';
const CategoryDiscountPreview = ({ campaign, type, showPreview, step }) => {
  return (
      <React.Fragment>
        {showPreview && (<PreviewImage src={getStaticPreviewImageByType(type)}/>)}
        {!showPreview && <WizardPreview className="boxed" minHeight='550px' sx={{ backgroundColor: 'transparent', height: '550px' }}><CategoryDiscount campaign={campaign} step={step}/></WizardPreview>}
      </React.Fragment>
  );
};

export default CategoryDiscountPreview;
