import React from 'react';

import { Grid, GridItem } from '@lapilli-ui/components';

import CollapseCard from './collapse-card';
import { CAMPAIGN_TYPES } from '../../config';
import { getCampaignIconType } from '../../utils/admin-functions';
const NewCampaignMenu = ({ onClick }) => {
  const items = CAMPAIGN_TYPES.map( (type, index ) => {
    const item = {
      id: type.id,
      title: type.name,
      description: type.description,
      icon: getCampaignIconType(type.id),
      isLast: index === (CAMPAIGN_TYPES.length - 1)
    };
    return item;
  });

  const keys = Object.keys(items);

  return (
        <Grid gap={2.5} columns={12}>
            {keys.map(index => (
                <GridItem xs={2} sm={4} md={3} key={index} sx={{ position: 'relative' }}>
                    <CollapseCard item={items[index]} onClick={onClick}/>
                </GridItem>
            ))}
        </Grid>
  );
};

export default NewCampaignMenu;
