import React, { useEffect, useState } from 'react';

import { Box, Button, Input, Select, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __, _x, sprintf } from '@wordpress/i18n';

import ConditionList from './condition-list';
import SimpleModal from '../../../../blocks/elements/simple-modal';
import Icon from '../../../../common/icon';
import { isEmpty } from '../../../../common/utils/common';
import { wcSettings } from '../../../config';
import {
  getApplyCartDiscountOpts,
  getConditionSubTypeCartOpts,
  getConditionTypeCartOpts,
  getWhenApplyCartDiscountOpts
} from '../../../utils/admin-functions';
import { AjaxTerms } from '../index';

const ConditionRule = styled(Box)(({ theme }) => ({
  '&': {
    marginBottom: '20px'
  },

  '& .iconButton': {
    border: 0,
    backgroundColor: 'transparent'
  },
  '& .iconButton:hover': {
    cursor: 'pointer'
  }
}));
const GiftDiscountRules = ({ id, label, value, editButtonLabel, buttonLabel, addNewRuleButtonLabel, onSaveHandler, required, showError }) => {
  const [isEditMode, setEditMode] = useState(false);
  const [isModalVisible, setModalVisible] = useState(false);
  const [items, setItems] = useState(value.rules.length > 0 ? value.rules : []);
  const [handleCondition, setHandleCondition] = useState(value.handle);
  const [typeCondition, setTypeCondition] = useState(value.type);
  const [saved, setSaved] = useState(value.rules.length > 0);

  useEffect(() => {
    setEditMode(value.rules.length > 0);
    setTypeCondition(value.rules.length === 0 ? 'always' : 'conditions');
  }, [value]);

  const handleRuleOpts = getApplyCartDiscountOpts();
  const handleModalRuleOpts = getWhenApplyCartDiscountOpts();
  const conditionTypeOpts = getConditionTypeCartOpts('gift-on-cart');
  const labelEditOption = handleCondition === 'any' ? __('Trigger when any of these conditions are met:', 'wonder-cart') : __('Trigger when all of these conditions are met:', 'wonder-cart');

  const buttonLabelToShow = isEditMode ? editButtonLabel : buttonLabel;

  const placeholderProduct = __('Search for products', 'wonder-cart');
  const placeholderProductCat = __('Search for product categories', 'wonder-cart');

  const handleChangeNumber = (value, id, property) => {
    setTimeout(() => handleOnChange(value, id, property), 500);
  };

  const handleOnChange = (value, id, property) => {
    let updatedItems = [];
    if (items.length === 0) {
      updatedItems = [{ id: 0, type: 'cart_total', condition: 'at_least', value: 50 }];
    } else {
      updatedItems = [...items];
    }
    if (typeof value === 'object' && value?.target) {
      value = value.target.value;
    }

    updatedItems = updatedItems.map(item => {
      const newItem = { ...item };
      if (newItem.id === id) {
        newItem[property] = value;
        if (property === 'type') {
          if (value === 'categories' || value === 'products') {
            newItem.value = [];
          } else {
            newItem.value = 50;
          }
        }
      }
      return newItem;
    });

    setItems(updatedItems);
  };
  const handleTypeOnChange = (value) => {
    setTypeCondition(value);
    const e = {
      target: {
        id,
        value: {
          type: value,
          handle: 'any',
          rules: []
        }
      }
    };
    onSaveHandler(e);
  };
  const handleConditionOnChange = (value) => {
    setHandleCondition(value);
  };
  const handleOpenModal = () => {
    setModalVisible(true);
  };
  const handleCloseModal = () => {
    if (!saved) {
      setItems([]);
    }
    setModalVisible(false);
  };
  const handleAddRule = () => {
    let newList = [];
    if (items.length === 0) {
      newList = [{ id: 0, type: 'cart_total', condition: 'at_least', value: 50 }];
    } else {
      newList = [...items];
    }
    // const newList = items.map((item, index) => ({ ...item, id: index }));
    newList.push({ id: newList.length, type: 'cart_total', condition: 'at_least', value: 50 });
    setItems(newList);
  };

  const handleSave = () => {
    if (items.length === 0) {
      items.push({ id: 0, type: 'cart_total', condition: 'at_least', value: 50 });
    }
    const e = {
      target: {
        id,
        value: {
          type: 'conditions',
          handle: handleCondition,
          rules: items
        }
      }
    };

    setSaved(true);
    onSaveHandler(e);
    setModalVisible(false);
  };
  const handleRemoveItem = (index) => {
    const newList = items.filter((item) => (item.id !== index));

    if (newList.length === 0) {
      setEditMode(false);
      setSaved(false);
      setTypeCondition('always');
      const e = {
        target: {
          id,
          value: {
            type: 'always',
            handle: 'any',
            rules: []
          }
        }
      };

      onSaveHandler(e);
    } else {
      const e = {
        target: {
          id,
          value: {
            type: 'conditions',
            handle: handleCondition,
            rules: newList
          }
        }
      };
      onSaveHandler(e);
    }
    setItems(newList);
  };

  const SingleCondition = ({ condition }) => {
    const isTermCondition = condition.type === 'products' || condition.type === 'categories';
    const conditionsSubType = getConditionSubTypeCartOpts(condition.type);

    return (
            <ConditionRule sx={{ marginBottom: '30px' }}>
                <Typography sx={{
                  fontWeight: '500',
                  textTransform: 'uppercase',
                  marginBottom: '15px',
                  fontSize: '11px'
                }}>{sprintf(__('Condition #%s', 'wonder-cart'), parseInt(condition.id) + 1)}</Typography>

                <Stack direction='row' spacing={1.5} align='center'>
                    <Select options={conditionTypeOpts} value={condition.type} size='md'
                            onChange={(value) => handleOnChange(value, condition.id, 'type')}/>
                    <Select options={conditionsSubType} value={condition.condition}
                            size='md'
                            onChange={(value) => handleOnChange(value, condition.id, 'condition')} style={{ flex: 1 }}/>
                    {!isTermCondition && (
                        <>
                            <Input type='number' min={0} step={condition.type === 'num_order' ? 1 : 0.1}
                                   defaultValue={condition.value}
                                   isMini={true}
                                   onChange={(e) => handleChangeNumber(e, condition.id, 'value')} size='md'/>
                            {(condition.type === 'cart_total' || condition.type === 'past_expense') && (
                                <Typography>{wcSettings.currency.symbol}</Typography>)}
                        </>
                    )}
                    {items.length > 1 &&
                        <button className="iconButton" onClick={() => handleRemoveItem(condition.id)}><Icon
                            icon="XMarkIcon" size="15" className="remove-item"/></button>}

                </Stack>
                {isTermCondition && (
                    <Box sx={{ marginTop: '10px', marginRight: items.length > 1 ? '33px' : '0' }}>
                        <AjaxTerms terms={condition.value} taxonomy={condition.type} multiple={true}
                                   placeholder={condition.type === 'products' ? placeholderProduct : placeholderProductCat}
                                   isFullWidth={true} size='md'
                                   onChange={(values) => handleOnChange(values, condition.id, 'value')}/>
                    </Box>
                )}

            </ConditionRule>
    );
  };

  const error = value.type !== 'always' && showError && required && isEmpty(value.rules);
  const typeMessage = __('Set conditions before continuing', 'wonder-cart');
  const showNotice = error ? <Typography className="yith-sales-field-error">{typeMessage}</Typography> : '';

  return (
        <Stack direction='column' spacing={2}>
            {
                !isEditMode && (
                    <>
                        <label htmlFor="yith-sales-handle-condition-type" style={{ marginBottom: '0' }}>{label}</label>
                        <Select options={handleRuleOpts} value={value.type} size='xl' width='100%' onChange={handleTypeOnChange}/>
                    </>
                )
            }
            {
                isEditMode && (<Typography sx={{ fontWeight: '500' }}>{labelEditOption}</Typography>)
            }
            <ConditionList items={items} onHandleRemove={handleRemoveItem}/>
            {value.type !== 'always' &&
                <>
                  <Button size="lg" fullWidth={true} onClick={handleOpenModal} variant={isEditMode ? 'outlined' : 'contained'} >{buttonLabelToShow}</Button>
                  {showNotice}
                </>}
            <SimpleModal title={__('Gift product at cart conditions', 'wonder-cart')}
                         open={isModalVisible} onClose={handleCloseModal}>
                <Typography
                    sx={{ marginBottom: '30px' }}>{__('Products will be offered as gifts in cart only when the following conditions are met.', 'wonder-cart')}</Typography>
                <Stack direction='row' spacing={1} align='center' sx={{ marginBottom: '30px' }}>
                    <Typography sx={{
                      fontWeight: '700',
                      fontSize: '13px'
                    }}>{__('Offer gift product only when', 'wonder-cart')}</Typography>
                    <Select options={handleModalRuleOpts} value={handleCondition} size='lg' width='140px'
                            onChange={handleConditionOnChange}/>
                    <Typography
                        sx={{ fontSize: '13px' }}>{_x('of these conditions are met:', '[PART OF] Offer discount at cart only when all/any of these conditions are met:', 'wonder-cart')}</Typography>
                </Stack>
                {items.length > 0 && items.map((item, index) =>
                    <SingleCondition condition={item} id={`item-${item.id}`} key={`item-${index}`}/>
                )}
                {items.length === 0 &&
                    <SingleCondition
                        condition={{ id: items.length, type: 'cart_total', condition: 'at_least', value: 50 }}
                        id="item-0"/>}
                <Box sx={{ marginTop: '30px' }}>
                    <Button size="lg" startIcon={<Icon icon='PlusSmallIcon' sx={{ color: 'inherit' }}/>} fullWidth={true}
                            onClick={handleAddRule} variant="outlined"
                            style={{ marginBottom: '20px', maxHeight: '35px' }}>{addNewRuleButtonLabel}</Button>
                    <Button size="lg" fullWidth={true}
                            onClick={handleSave}>{__('Save', 'wonder-cart')}</Button>
                </Box>

            </SimpleModal>
        </Stack>
  );
};
export default GiftDiscountRules;
