import React, { useState } from 'react';

import { Box, Button, Card, CardContent, CardMedia, Collapse, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

const CollapseCardStyled = styled(Card)(({ theme, viewMode }) => ({
  '&': {
    boxShadow: 'unset',
    border: '1px solid ' + theme.palette.border.main,
    borderRadius: '4px'
  },
  '& .collapse-card--img': {
    margin: '15px auto',
    objectFit: 'contain',
    backgroundSize: '143px 60px',
    height: '60px',
    maxWidth: '80%'
  },
  '& .collapse-card--content': {
    padding: '0!important'
  },
  '& .collapse-card--title': {
    marginBottom: '15px',
    fontWeight: '500',
    textAlign: 'center'
  },
  '& .collapse-card--description': {
    fontSize: '10px',
    marginBottom: 15,
    padding: '0 16px',
    whiteSpace: 'normal'
  },
  '& .collapse-card-button': {
    borderRadius: '0'
  },
  '& .collapse-card--action-area': {
    height: '120px'
  },
  '& .collapse-card--action-area:hover': {
    cursor: 'pointer',
    backgroundColor: theme.palette.campaign.new.backgroundHover
  },
  '&.collapsed': {
    backgroundColor: theme.palette.campaign.new.backgroundHover,
    position: 'absolute',
    zIndex: 9999999,
    paddingBottom: '0!important'
  },
  '&.collapsed.last': {
    backgroundColor: theme.palette.campaign.new.backgroundHover,
    position: 'relative',
    paddingBottom: '0!important'
  },
  '&.collapsed .collapse-card--action-area': {
    height: 'auto'
  },
  '&.collapsed .collapse-card--title': {
    color: theme.palette.primary.main
  }
}));

const CollapseCard = ({ item, onClick }) => {
  const [collapsed, setCollapsed] = useState(false);
  const handleCollapseEnter = () => {
    setCollapsed(true);
  };
  const handleCollapseLeave = () => {
    setCollapsed(false);
  };

  const handleOnClick = (itemID) => {
    const event = {
      target: {
        id: itemID
      }
    };
    onClick(event);
  };

  let classCard = collapsed ? 'collapsed' : '';
  classCard += item.isLast ? ' last' : '';
  return (
        <CollapseCardStyled className={ classCard } sx={{ maxWidth: 250 }}>
            <Box className='collapse-card--action-area'
                 onClick={() => handleOnClick(item.id)}
                 onMouseEnter={handleCollapseEnter}
                 onMouseLeave={handleCollapseLeave}>
                <CardMedia
                    className='collapse-card--img'
                    src={item.icon}
                    component={'img'}
                    height={'60'}
                    alt={item.title}
                />
                <CardContent className='collapse-card--content'>
                    <Typography className='collapse-card--title' variant={'body2'}>{item.title}</Typography>
                    <Collapse open={collapsed}>
                        <Typography className='collapse-card--description'>{item.description}</Typography>
                        <Button id={item.id} fullWidth onClick={onClick}
                                className='collapse-card-button'>{__('Create now', 'wonder-cart')}</Button>
                    </Collapse>
                </CardContent>
            </Box>
        </CollapseCardStyled>
  );
};

export default CollapseCard;
