import React from 'react';

import { _n, sprintf } from '@wordpress/i18n';

import { VerticalScrollableContentStyled } from '../../../../blocks/elements/styles/VerticalScrollableContent.styled';
import Icon from '../../../../common/icon';
import { isEmpty } from '../../../../common/utils/common';
import { formatCurrency } from '../../../../common/utils/numbers';
import {
  getConditionSubTypeCartOpts,
  getConditionTypeCartOpts,
  getCountriesOpts,
  getStatesOpts
} from '../../../utils/admin-functions';
import {
  PreviewItemDelete,
  PreviewItemName,
  PreviewItemsBoxContainer,
  PreviewItemsBoxWrapper
} from '../../styles/PreviewItemsBox.styled';

const ConditionList = ({ items, onHandleRemove }) => {
  if (items.length === 0) return '';

  const conditionTypeOpts = getConditionTypeCartOpts('free-shipping');
  const conditionCompareOpts = getConditionSubTypeCartOpts();
  const states = getStatesOpts();
  const countries = getCountriesOpts();
  const cartOpt = ['cart_total', 'num_order', 'past_expense'];

  const getFormattedNameList = (type, values) => {
    const currentList = [];
    const termsToCheck = type === 'state' ? states : countries;
    for (const i in values) {
      const termLabel = termsToCheck.filter((term) => term.value === values[i]);
      if (!isEmpty(termLabel)) {
        currentList.push(termLabel[0].label);
      }
    }
    let finalNames = [];
    if (currentList.length > 5) {
      finalNames = currentList.slice(0, 5);
    } else {
      finalNames = currentList;
    }
    const remain = currentList.length - 5;
    const suffix = remain > 0 ? sprintf(_n('...other %s', '...others %s', remain, 'wonder-cart'), remain) : '';
    return finalNames.join(', ') + suffix;
  };
  const getFormattedName = (item) => {
    const typeLabel = conditionTypeOpts.filter((conditionType) => (conditionType.value === item.type));
    if (cartOpt.indexOf(item.type) >= 0) {
      const conditionLabel = conditionCompareOpts.filter((conditionType) => (conditionType.value === item.condition));
      const value = item.type === 'cart_total' ? formatCurrency(item.value) : item.value;
      return `${typeLabel[0].label}: ${conditionLabel[0].label}  ${value}`;
    } else {
      let value = '';
      switch (item.type) {
        case 'state_is':
        case 'state_not':
          value = getFormattedNameList('state', item.value);
          break;
        case 'country_is':
        case 'country_not':
          value = getFormattedNameList('country', item.value);
          break;
      }
      return `${typeLabel[0].label}: ${value}`;
    }
  };

  return (
        <PreviewItemsBoxContainer>
            <PreviewItemsBoxWrapper>
                <VerticalScrollableContentStyled maxHeight="98px">
                    <ul>
                        {items.map((item) => (
                            <li key={item.id}>
                                <PreviewItemName>
                                    {getFormattedName(item)}
                                </PreviewItemName>
                                <PreviewItemDelete onClick={() => onHandleRemove(item.id)}>
                                    <Icon icon="XMarkIcon" size="12"/>
                                </PreviewItemDelete>
                            </li>
                        ))}
                    </ul>
                </VerticalScrollableContentStyled>
            </PreviewItemsBoxWrapper>
        </PreviewItemsBoxContainer>
  );
};

export default ConditionList;
