import React, { useState } from 'react';

import {
  Box, Paper,
  Spinner,
  Stack,
  Typography
} from '@lapilli-ui/components';
import {
  Autocomplete,
  createFilterOptions,
  InputAdornment,
  Popper,
  TextField
} from '@mui/material';
import { __ } from '@wordpress/i18n';

import { useSearchLinkQuery } from '../../../../common/api/links';
import Icon from '../../../../common/icon';
import { htmlEntityDecode } from '../../../../common/utils/common';
import { getHighlightedText, isEmpty } from '../../../utils/admin-functions';

const filter = createFilterOptions();
const SearchLink = ({ id, label, value, description, onSaveHandler, showError, ...other }) => {
  const [search, setSearch] = useState('');
  const { data, isFetching } = useSearchLinkQuery({ search });

  const defaultData = isEmpty(value) ? [] : [value];
  const textClassName = showError && other.required && isEmpty(value.url) ? 'yith-sales-field-error' : '';
  const typeMessage = __('Please enter a link here ', 'wonder-cart');
  const showNotice = textClassName !== ''
    ? <Typography className="yith-sales-field-error" sx={{ padding: '10px 0' }}>{typeMessage}</Typography>
    : '';
  const isValidUrl = urlString => {
    const urlPattern = new RegExp('^(https?:\\/\\/)?' + // validate protocol
            '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // validate domain name
            '((\\d{1,3}\\.){3}\\d{1,3}))' + // validate OR ip (v4) address
            '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // validate port and path
            '(\\?[;&a-z\\d%_.~+=-]*)?' + // validate query string
            '(\\#[-a-z\\d_]*)?$', 'i'); // validate fragment locator
    return !!urlPattern.test(urlString);
  };
  const handleChange = (e, value) => {
    if (e && e.type === 'change') {
      const newSearch = e.target.value;
      if (!isFetching) {
        if (!isValidUrl(newSearch) && newSearch.length > 1) {
          setTimeout(() => setSearch(newSearch), 500);
        } else {
          setSearch('');
        }
      }
    }
  };
  const handleSaveSelected = (e, value) => {
    let el = {};
    if (e.type === 'keydown') {
      let element = {};
      if (value?.type) {
        element = value;
      } else {
        const url = formatHttpLink(value);
        element = {
          type: 'url',
          url,
          title: value
        };
      }
      el = {
        target: {
          id,
          value: element
        }
      };
    } else {
      el = {
        target: {
          id,
          value
        }
      };
    }
    onSaveHandler(el);
  };
  const formatHttpLink = (value) => {
    let url = '';
    if (value !== '') {
      url = value.toLowerCase().replace(' ', '');
      url = url.includes('http') ? url : 'http://' + url;
    }
    return url;
  };
  const renderSuggestion = (props, option) => {
    const optionType = option.subtype ?? option.type;
    return (
            <Stack direction='row' spacing={1} {...props} align='start'>
                {optionType === 'page' && <Icon icon='DocumentIcon' size={20}/>}
                {optionType === 'url' && <Icon icon='GlobeAltIcon' size={20}/>}
                <Box sx={{ fontSize: '13px', flex: 1 }}>
                    <Typography sx={{
                      fontWeight: 500,
                      marginBottom: '.2em',
                      textOverflow: 'ellipsis'
                    }}>{getHighlightedText(htmlEntityDecode(option.title), search)}</Typography>
                    <Typography sx={{
                      fontSize: '0.9em',
                      marginBottom: '.2em',
                      textOverflow: 'ellipsis',
                      wordBreak: 'break-all'
                    }}>{optionType === 'url' ? __('Press ENTER to add this link', 'wonder-cart') : option.url}</Typography>
                </Box>
                <Box sx={{
                  backgroundColor: '#f0f0f0',
                  borderRadius: '2px',
                  display: 'block',
                  fontSize: '.9em',
                  marginLeft: 'auto',
                  padding: '3px 6px',
                  whiteSpace: 'nowrap'
                }}>
                    {optionType === 'url' ? optionType.toUpperCase() : optionType}
                </Box>
            </Stack>
    );
  };

  return (
        <>
            {label && <label htmlFor={id}>{label}</label>}
            <Autocomplete
                freeSolo
                disablePortal
                value={value}
                PaperComponent={(props) => (
                    <Paper
                        sx={{
                          marginTop: '5px',
                          boxShadow: '0px 5px 5px -3px rgba(0,0,0,0.2), 0px 8px 10px 1px rgba(0,0,0,0.14), 0px 3px 14px 2px rgba(0,0,0,0.12)'
                        }}
                        {...props}
                    />
                )}
                PopperComponent={({ style, ...props }) => (
                    <Popper
                        {...props}
                        style={{ ...style, height: 0 }} // width is passed in 'style' prop
                    />
                )}
                filterOptions={(options, params) => {
                  const filtered = filter(options, params);
                  const { inputValue } = params;

                  if (inputValue !== '') {
                    const url = formatHttpLink(inputValue);
                    filtered.push({
                      type: 'url',
                      url,
                      title: inputValue
                    });
                  }
                  return filtered;
                }
                }
                options={data ?? defaultData}
                getOptionLabel={(option) => {
                  return option.url ?? formatHttpLink(option);
                }}
                loading={isFetching}
                loadingText={__('Searching for url..', 'wonder-cart')}
                sx={{ width: '300px' }}
                disableClearable
                onInputChange={handleChange}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        className={textClassName}
                        placeholder={__('Search or type url', 'wonder-cart')}
                        sx={{
                          '& input[type="text"]:focus, & input[type="text"]': {
                            borderColor: 'unset',
                            border: 'unset',
                            boxShadow: 'unset'
                          },
                          '&.yith-sales-field-error .MuiInputBase-root': {
                            border: '1px solid #D10000!important'
                          }
                        }}
                        InputProps={{
                          ...params.InputProps,
                          autoComplete: 'new-password', // disable autocomplete and autofill
                          endAdornment: <>
                                {isFetching && <Spinner/>}
                                <InputAdornment position="end">
                                    <Icon icon='ArrowUturnLeftIcon' size={20}
                                          sx={{ transform: 'rotateX(180deg)' }}/>
                                </InputAdornment>
                            </>
                        }}
                    />
                )}
                renderOption={(props, option) => renderSuggestion(props, option)}
                onChange={handleSaveSelected}
            />
            {showNotice}
        </>
  );
};
export default SearchLink;
