import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

import { yithSalesSearchLinkApiFetch } from '../basic-fetch';

export const apiLinkSlice = createApi({
  reducerPath: 'apiLink',
  baseQuery: fetchBaseQuery({
    baseUrl: '/wp/v2/',
    fetchFn: yithSalesSearchLinkApiFetch
  }),
  endpoints: (builder) => ({
    searchLink: builder.query({
      async queryFn (args, _queryApi, _extraOption, fetchWithBQ) {
        const { search = '' } = args;
        let data = [];
        if (search !== '') {
          const dataPosts = await fetchWithBQ(`search?search=${search}&per_page=20&type=post&_locale=user`);
          if (dataPosts.error) return { error: dataPosts.error };
          const dataTerms = await fetchWithBQ(`search?search=${search}&per_page=20&type=term&_locale=user`);
          if (dataTerms.error) return { error: dataTerms.error };

          data = [...dataPosts.data, ...dataTerms.data];
        }

        return { data };
      }
    })
  })
});

export const {
  useSearchLinkQuery
} = apiLinkSlice;
