import React from 'react';

import { Box, Spinner } from '@lapilli-ui/components';
import { parse } from '@wordpress/blocks';

import ProductList from '../../../../../blocks/elements/product-list';
import ProductSingleItem from '../../../../../blocks/elements/product-single-item';
import ProductSingleItemEmpty from '../../../../../blocks/elements/product-single-item-empty';
import BlockEditorPreview from '../../../block-editor-preview';

const Upsell = ({ campaign, options, products, showContent, inFetching, isSingle, numProductToShow = -1, step }) => {
  const {
    title,
    show_product_name: showProductName,
    show_product_price: showProductPrice,
    show_saving: showSaving,
    saving_color: savingColor,
    apply_discount: addDiscount,
    checkbox_color: checkboxColor,
    discount_to_apply: discountToApply,
    add_to_cart_button_label: addToCartLabel,
    products_to_show: productToShow
  } = options;

  const productListOPT = {
    showProductName,
    showProductPrice,
    showSaving,
    savingColor,
    addDiscount,
    checkboxColor,
    discountToApply,
    addToCartLabel,
    productType: productToShow.type,
    applyDiscount: addDiscount,
    discountValue: discountToApply.discount_value,
    discountType: discountToApply.discount_type,
    checkedByDefault: 'yes'
  };

  return (
      <Box className={'yith-sales-upsell-preview-wrapper'}>
        {!showContent && <ProductSingleItemEmpty width='490px'/>}
        {(campaign.status === 'publish' || step === 'customization') &&
          <BlockEditorPreview blocks={parse(title)}/>
        }
        {inFetching && (<Spinner sx={{ marginLeft: '50%', marginTop: '20px', marginBottom: '20px' }}/>)}
        {
            showContent && !inFetching && !isSingle && (
                <ProductList products={products} productOpts={productListOPT} showTotals={false} numProductToShow={numProductToShow} showNoThanks={true} context='preview'/>
            )
        }
        {
            showContent && !inFetching && isSingle && (
                <ProductSingleItem data={products} productOpts={productListOPT}/>
            )
        }
      </Box>
  );
};

export default Upsell;
