import React from 'react';

import { Box, Button } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';

import { Divider } from '../../../common/fields';
import { getImage } from '../../../common/utils/common';
import ProductImage from '../product-image';

const ProductSingleItemEmpty = ({ width = 'auto' }) => {
  const imgWidth = width !== 'auto' ? `calc( ${width} - 48px )` : width;
  const generalImage = getImage({}, true);
  return (
      <Box sx={{ marginTop: '20px' }}>
        <Divider sx={{ width: '100%', marginBottom: '8px', borderBottomWidth: 'thick' }}/>
        <Divider sx={{ width: '100%', marginBottom: '8px', borderBottomWidth: 'thick' }}/>
        <Divider sx={{ width: '100%', marginBottom: '30px', borderBottomWidth: 'thick' }}/>
        <ProductImage src={generalImage.src} alt={generalImage.alt} width={imgWidth} />
        <Divider sx={{ width: '100%', marginBottom: '8px', marginTop: '20px', borderBottomWidth: 'thick' }}/>
        <Divider sx={{ width: '100%', marginBottom: '30px', borderBottomWidth: 'thick' }}/>
        <Button sx={{
          display: 'block',
          width: '100%',
          textAlign: 'center',
          textDecoration: 'none',
          marginBottom: '15px',
          backgroundColor: 'var(--wp--preset--color--primary)',
          borderRadius: 'var(--wp--custom--button-border-radius)',
          color: 'var(--wp--preset--color--base)',
          fontSize: 'var(--wp--preset--font-size--small)',
          paddingTop: '0.5rem',
          paddingRight: '1rem',
          paddingBottom: '0.5rem',
          paddingLeft: '1rem',
          '&:hover': {
            textDecoration: 'none',
            backgroundColor: 'var(--wp--preset--color--primary)',
            color: 'var(--wp--preset--color--base)'
          }
        }} size='lg' fullWidth={true}>{__('Add to cart', 'wonder-cart')}</Button>
      </Box>
  );
};
export default ProductSingleItemEmpty;
