import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  notices: []
};

const noticesSlice = createSlice({
  name: 'notices',
  initialState,
  reducers: {
    addNotice: (state, notice) => {
      const newNotice = notice.payload;
      let update = true;
      state.notices.forEach(notice => {
        if (newNotice.message === notice.message) {
          update = false;
          return false;
        }
      });

      update && state.notices.push(notice.payload);
    },
    updateNotice: (state, notices) => {
      state.notices = notices.payload;
    }
  }
});

export const { addNotice, updateNotice } = noticesSlice.actions;
export default noticesSlice.reducer;
