import React from 'react';

import { Spinner } from '@lapilli-ui/components';

import { VerticalScrollableContentStyled } from '../../../../blocks/elements/styles/VerticalScrollableContent.styled';
import Icon from '../../../../common/icon';
import { getFormattedTermName, isEmpty } from '../../../../common/utils/common';
import { getTermsByType } from '../../../../common/utils/data';
import { PreviewItemDelete, PreviewItemName, PreviewItemsBoxContainer, PreviewItemsBoxWrapper } from '../../styles/PreviewItemsBox.styled';

const SelectedItemList = ({ termType, termIds, onRemoveItem }) => {
  if (!isEmpty(termIds)) {
    const { data, isLoading } = getTermsByType({ termType, termIds });

    return (
            <PreviewItemsBoxContainer>
                {isLoading && (<Spinner sx={{ marginLeft: '50%', marginTop: '10px' }}/>)}
                {!isLoading && data && (
                    <PreviewItemsBoxWrapper>
                      <VerticalScrollableContentStyled maxHeight="98px">
                        <ul>
                            {data.map((term) => (
                                <li key={term.id}>
                                    {term?.images && term.images.length > 0 && (<img src={term.images[0].src}/>)}
                                    <PreviewItemName>
                                        {getFormattedTermName(term)}
                                    </PreviewItemName>
                                    <PreviewItemDelete onClick={() => onRemoveItem(term.id)}>
                                      <Icon icon="XMarkIcon" size="12"/>
                                    </PreviewItemDelete>
                                </li>
                            ))}
                        </ul>
                      </VerticalScrollableContentStyled>
                    </PreviewItemsBoxWrapper>
                )}
            </PreviewItemsBoxContainer>
    );
  }

  return '';
};

export default SelectedItemList;
