import React, { useState } from 'react';

import { Button, Select, Stack } from '@lapilli-ui/components';
import { ToggleButton, ToggleButtonGroup } from '@mui/material';
import { __ } from '@wordpress/i18n';
import { useDispatch, useSelector } from 'react-redux';

import { useDeleteCampaignMutation, useSaveCampaignMutation } from '../../../common/api/campaign';
import Icon from '../../../common/icon';
import { useMediaQuery } from '../../../common/utils/common';
import { nowScheduleDateFrom } from '../../../common/utils/date';
import { BULK_ACTION, FILTER_STATUS, FILTER_TYPE } from '../../config';
import { resetCampaignView, setCampaignsView } from '../../slices/view/view-campaigns-list';
import { useViewMode } from '../../utils/view-mode-context';
import ConfirmDialog from '../confirm-dialog/confirmDialog';

const CampaignFilters = () => {
  const {
    campaigns,
    status: currentStatusFilter,
    type: currentTypeFilter
  } = useSelector((state) => state.campaignsView);
  const { viewMode, setViewMode } = useViewMode();

  const [bulkAction, setBulkAction] = useState('');
  const [modalVisible, setModalVisible] = useState(false);
  const matches = useMediaQuery('(max-width:1131px)');
  const matches1 = useMediaQuery('(max-width:500px)');
  const [saveCampaign] = useSaveCampaignMutation();
  const [deleteCampaign] = useDeleteCampaignMutation();
  const dispatch = useDispatch();

  let filterStackConfig = {
    direction: 'row',
    spacing: 1,
    align: 'end',
  };
  let minLabelWidth = 'none';
  if (matches) {
    filterStackConfig = {
      direction: 'column',
      spacing: 2,
      align: 'start'
    };
    minLabelWidth = '120px';
  }

  const handleTypeChange = (value) => {
    dispatch(setCampaignsView({
      type: value,
      campaigns: [],
      totalPages: 0,
      totalCount: 0,
      currentPage: 1,
      loadedPages: 0
    }));
  };
  const handleStatusChange = (value) => {
    dispatch(setCampaignsView({
      status: value,
      campaigns: [],
      totalPages: 0,
      totalCount: 0,
      currentPage: 1,
      loadedPages: 0
    }));
  };

  const resetTypeSelect = () => {
    dispatch(setCampaignsView({
      type: 'all',
      campaigns: [],
      totalPages: 0,
      totalCount: 0,
      currentPage: 1,
      loadedPages: 0
    }));
  };
  const resetStatusSelect = () => {
    dispatch(setCampaignsView({
      campaigns: [],
      status: '',
      totalPages: 0,
      totalCount: 0,
      currentPage: 1,
      loadedPages: 0
    }));
  };

  const resetBulkSelect = () => {
    setBulkAction('');
  };

  const handleViewModeChange = (e, newValue) => {
    e.stopPropagation();
    if (newValue && newValue !== viewMode) {
      setViewMode(newValue);
    }
  };

  const getConfirmTitle = action => {
    switch (action) {
      case 'activate':
        return __('Are you sure you want to activate all campaigns?', 'wonder-cart');
      case 'deactivate':
        return __('Are you sure you want to deactivate all campaigns?', 'wonder-cart');
      case 'delete':
        return __('Are you sure you want to delete all campaigns?', 'wonder-cart');
      default:
        return '';
    }
  };

  const onConfirmClick = (e, action) => {
    setModalVisible(false);

    const scheduleDateFrom = nowScheduleDateFrom();
    switch (action) {
      case 'activate':
        campaigns.forEach(campaign => {
          if (campaign.campaign_status === 'active') return;
          saveCampaign(
            {
              body: {
                campaign_status: 'active',
                schedule_date_from: scheduleDateFrom
              },
              id: campaign.id
            }).then((result) => {
          });
        });
        dispatch(setCampaignsView({
          campaigns: campaigns.map(campaign => (campaign.campaign_status !== 'active'
            ? {
                ...campaign,
                campaign_status: 'active',
                schedule_date_from: scheduleDateFrom
              }
            : campaign)),
          totalPages: 0,
          totalCount: 0,
          currentPage: 1,
          loadedPages: 0
        }));
        break;
      case 'deactivate':
        campaigns.forEach(campaign => {
          if (campaign.campaign_status === 'inactive') return;
          saveCampaign(
            {
              body: {
                campaign_status: 'inactive'
              },
              id: campaign.id
            }).then((result) => {
          });
        });
        dispatch(setCampaignsView({
          campaigns: campaigns.map(campaign => (campaign.campaign_status !== 'inactive'
            ? {
                ...campaign,
                campaign_status: 'inactive'
              }
            : campaign)),
          totalPages: 0,
          totalCount: 0,
          currentPage: 1,
          loadedPages: 0
        }));
        break;
      case 'delete':
        campaigns.forEach(async campaign => await deleteCampaign({ id: campaign.id }).then((result) => {
          dispatch(resetCampaignView({}));
        }));
        break;
    }
  };

  const confirmDialog = (action = 'activate') => {
    return <ConfirmDialog open={true} onCancel={() => setModalVisible(false)}
                              onConfirm={(e) => onConfirmClick(e, action)} onClose={() => setModalVisible(false)}
                              title={__('Confirm action', 'wonder-cart')}
                              confirmButton={__('Yes', 'wonder-cart')}>
            {getConfirmTitle(action) + '\n\n' + (action === 'delete' ? __('This action cannot be undone and you will not be able to recover this data.', 'wonder-cart') : '')}
        </ConfirmDialog>;
  };

  const handleApplyBulkAction = () => {
    setModalVisible(true);
  };

  return (
        <Stack direction={!matches1 ? 'row' : 'column'} spacing={2} align={!matches ? 'center' : 'baseline'}
               className='yith-sales-panel-filter-wrapper'>
            <Stack {...filterStackConfig} sx={{ flex:1 }} className='yith-sales-panel-filters'>
                <Stack direction='column' spacing={1} className='yith-sales-panel-filter-by-type'>
                    <label
                        htmlFor="yith-filter-by-type"
                        style={{ minWidth: minLabelWidth, color: '#1E293B', fontWeight: 500 }}
                    >
                        {__('Filter by type', 'wonder-cart')}
                    </label>
                    <Select
                        placeholder={__('All type', 'wonder-cart')}
                        id="yith-filter-by-type"
                        options={FILTER_TYPE}
                        onChange={handleTypeChange}
                        value={currentTypeFilter === 'all' ? '' : currentTypeFilter}
                        allowClear="true"
                        onClear={resetTypeSelect}
                    />
                </Stack>
                <Stack direction='column' spacing={1} className='yith-sales-panel-filter-by-status'>
                    <label
                        htmlFor="yith-filter-by-status"
                        style={{ minWidth: minLabelWidth, color: '#1E293B', fontWeight: 500 }}
                    >
                        {__('Filter by status', 'wonder-cart')}
                    </label>
                    <Select
                        placeholder={__('All status', 'wonder-cart')}
                        id="yith-filter-by-status"
                        options={FILTER_STATUS}
                        onChange={handleStatusChange}
                        value={currentStatusFilter}
                        allowClear="true"
                        onClear={resetStatusSelect}
                    />
                </Stack>
                <Stack direction='row' spacing={1} align='end' className='yith-sales-panel-bulk-actions-row'>
                    <Stack direction='column' spacing={1} className='yith-sales-panel-bulk-actions'>
                        <label htmlFor="yith-bulk-actions" style={{ minWidth: minLabelWidth, color: '#1E293B', fontWeight: 500 }}>
                            {__('Bulk actions', 'wonder-cart')}
                        </label>
                        <Select
                            placeholder={__('Select action', 'wonder-cart')}
                            id="yith-bulk-actions"
                            options={BULK_ACTION}
                            onChange={setBulkAction}
                            value={bulkAction}
                            allowClear="true"
                            onClear={resetBulkSelect}
                        />
                    </Stack>
                    <Button className="yith-bulk-apply" color="primary" size="md" variant="contained"
                            onClick={handleApplyBulkAction} sx={bulkAction === ''
                              ? {
                                  opacity: '0.5',
                                  pointerEvents: 'none'
                                }
                              : {}}> {__('Apply', 'wonder-cart')}</Button>
                    {modalVisible && confirmDialog(bulkAction)}
                </Stack>
            </Stack>
          <ToggleButtonGroup exclusive value={viewMode} size="small" onChange={handleViewModeChange}
                                              aria-label={__('List view', 'wonder-cart')} sx={{ height: '30px', marginTop: '20px!important' }}>
                <ToggleButton value="grid" sx={{ width: '48px', height: '36px', borderRadius: '6px', padding: '1px 12px', lineHeight: '1em' }}><Icon
                    icon="Squares2X2Icon" size={16} sx={{ color: '#2E3A59' }}/></ToggleButton>
                <ToggleButton value="list" sx={{ width: '48px', height: '36px', borderRadius: '6px', padding: '1px 12px', lineHeight: '1em' }}><Icon
                    icon="Bars3BottomLeftIcon" size={16} sx={{ color: '#2E3A59' }}/></ToggleButton>
            </ToggleButtonGroup>
        </Stack>
  );
};

export default CampaignFilters;
