import React from 'react';

import { useTheme } from '@lapilli-ui/styles';

import GiftInCartPreview from './gift-in-cart-preview';
import { getPopupOptions, isEmpty } from '../../../../../common/utils/common';
import { getRandomProductToShow } from '../../../../../common/utils/data';
import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import ModalPreview from '../../elements/modal-preview';
import PreviewImage from '../../elements/preview-image';

import GiftOnCart from './index';

const GiftOnCartPreview = ({ campaign, type, showPreview }) => {
  let options = {};
  let popupOptions = {};
  let inFetching = true;
  let isSingle = true;
  let maxWidth = '336px';
  let numProductToShow = -1;
  let maxProductToAdd = 1;
  let products = [];
  let showProductName = false;
  let hasTermsSet = false;
  let isModalPreview = false;
  if (!isEmpty(campaign)) {
    options = campaign.meta[campaign.type];
    numProductToShow = options.product_to_offer.type === 'categories' ? options.num_products_to_show : -1;
    maxProductToAdd = options.max_products_to_add;
    const { data, isLoading, isFetching } = getRandomProductToShow({ type: options.product_to_offer.type, ids: options.product_to_offer.ids }, numProductToShow);
    inFetching = isLoading || isFetching;
    isSingle = !data || data.length < 2;
    showProductName = (options.product_to_offer.ids.length === 1 && options.add_in_cart === 'no') || options.product_to_offer.ids.length > 1;
    products = data;
    hasTermsSet = options.product_to_offer?.ids && options.product_to_offer.ids.length > 0;
    isModalPreview = options.product_to_offer?.ids && ((options.product_to_offer.ids.length === 1 && options.add_in_cart === 'no') || options.product_to_offer.ids.length > 1 || options.product_to_offer.type === 'categories');

    if (!isSingle) {
      maxWidth = '490px';
    }
    popupOptions = getPopupOptions(options);
  }
  const theme = useTheme();
  const bgImageConfig = {
    backgroundImage: 'url(' + theme.gift.giftBg + ')',
    backgroundPosition: 'top',
    backgroundRepeat: 'no-repeat'
  };

  return (
      <React.Fragment>
        { showPreview || !hasTermsSet
          ? (
                <PreviewImage src={getStaticPreviewImageByType(type)} hasBgColor={false}/>
            )
          : (
              isModalPreview
                ? (
                    <ModalPreview {...popupOptions} type={campaign.type} maxWidth={maxWidth} bgImageConfig={bgImageConfig}>
                      <GiftOnCart options={options} products={products} isSingle={isSingle} inFetching={inFetching} numProductToShow={numProductToShow} maxProductToSelect={maxProductToAdd} showProductName={showProductName}/>
                    </ModalPreview>

                  )
                : (
                    <GiftInCartPreview campaign={campaign}/>
                  )
            )
        }
      </React.Fragment>
  );
};

export default GiftOnCartPreview;
