import React, { useEffect, useState } from 'react';

import { Box } from '@lapilli-ui/components';
import { useSelector } from 'react-redux';

import { useMediaQuery } from '../../../common/utils/common';
import { MAIN_MENU } from '../../config';
import { isEmpty } from '../../utils/admin-functions';
import CampaignFilters from '../campaign-filters';
import CampaignListView from '../campaign-list-view';
const CampaignList = () => {
  const { type: currentMenu, statistics } = useSelector((state) => state.campaignsView);
  const currentType = MAIN_MENU.find(item => item.id === currentMenu);
  const canShowToggle = useMediaQuery('(min-width:1045px)');
  const newButtonLabel = currentType?.cta_button;
  const emptyStateHeader = currentType?.empty_state_header;
  const emptyStateSubheader = currentType?.empty_state_subheader;
  const [showFilters, setShowFilters] = useState(false);

  useEffect(() => {
    if (!isEmpty(statistics)) {
      let totals = 0;
      const keys = Object.keys(statistics);
      keys.forEach(index => {
        totals += statistics[index].total;
      });
      if (totals > 0) {
        setShowFilters(true);
      } else {
        setShowFilters(false);
      }
    }
  }, [statistics]);
  return (
      <Box className="mainlist" >
        <Box sx={{ padding: '32px' }} >
            {showFilters && <CampaignFilters showToggle={canShowToggle}/>}
          <CampaignListView forceListMode={!canShowToggle} newCampaignLabel={newButtonLabel} emptyStateHeader={emptyStateHeader} emptyStateSubheader={emptyStateSubheader} />
        </Box>
      </Box>
  );
};

export default CampaignList;
