import React from 'react';

import { BlockEditorPreview as UiBlockEditorPreview } from '@lapilli-ui/block-editor';
import { styled } from '@lapilli-ui/styles';

import { yithParams } from '../../config';

const BlockEditorPreviewWrapper = styled('div')(({ ownerState }) => ({
  '.editor-styles-wrapper': {
    backgroundColor: 'transparent',
    color: 'inherit',
    fontSize: '1em',
    ...ownerState.editorStyles
  },
  '.editor-styles-wrapper figure img': {
    maxWidth: '100%!important'
  },
  '.editor-styles-wrapper figure .components-resizable-box__container': {
	  width: '100%!important'
  }
}
));

// TODO: maybe we could use an iframe to avoid style conflicts with the current page, when loading "editor styles".

const BlockEditorPreview = ({ blocks, editorStyles }) => {
  const ownerState = { editorStyles };
  return <BlockEditorPreviewWrapper ownerState={ownerState}>
		<UiBlockEditorPreview blocks={blocks} settings={yithParams.blockEditorSettings}/>
	</BlockEditorPreviewWrapper>;
};

export default BlockEditorPreview;
