import React, { useState } from 'react';

import { Input } from '@lapilli-ui/components';

import Icon from '../../../../common/icon';
import TermsSearchList from '../terms-search-list';

function TermsSearchForm ({ termsToSearch, placeholder, termsSelected, showCount, multiple, onSaveClick }) {
  const [termSearched, setTermSearched] = useState('');
  const handleChange = (event) => {
    const search = event.target.value;
    if (search.length > 2) {
      setTermSearched(search);
    } else {
      setTermSearched('');
    }
  };
  return (
        <div className="yith-sales-terms-search-form">
            <div className="yith-sales-term-search-form__input">
                <Input type="text" fullWidth={true} placeholder={placeholder} onChange={handleChange} endAdornment={<Icon icon="MagnifyingGlassIcon" size={16} sx={{ color: '#2E3A59' }}/>}/>
            </div>
            <TermsSearchList multiple={multiple} termSearched={termSearched} termsSelected={termsSelected} termsToSearch={termsToSearch} showCount={showCount} onSaveClick={onSaveClick}></TermsSearchList>
        </div>
  );
}
export default TermsSearchForm;
