import React from 'react';

import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import PreviewImage from '../../elements/preview-image';
import WizardPreview from '../../elements/wizard-preview';

import ShopDiscount from './index';

const ShopDiscountPreview = ({ campaign, type, showPreview, step }) => {
  return (
      <React.Fragment>
        {showPreview && (<PreviewImage src={getStaticPreviewImageByType(type)} width="800px"/>)}
        {!showPreview && <WizardPreview className="boxed" minHeight='500px' sx={{ backgroundColor: 'transparent', height: '550px' }}><ShopDiscount campaign={campaign} step={step}/></WizardPreview>}
      </React.Fragment>
  );
};

export default ShopDiscountPreview;
