import React, { useEffect, useState } from 'react';

import { Stack, Switch } from '@lapilli-ui/components';

const OnoffComponent = ({ id, label, value, onSaveHandler, ...other }) => {
  const [checked, setChecked] = useState(value === 'yes');

  useEffect(() => {
    setChecked(value === 'yes');
  }, [value]);

  const handleChange = (el, value) => {
    const e = {
      target: {
        id,
        value: value ? 'yes' : 'no'
      }
    };

    onSaveHandler(e);
    setChecked(value);
  };

  return (
        <Stack direction="row" className='yith-sales-field-onoff' align="center">
            {label && <label htmlFor={id} style={{ flex: 1 }}>{label}</label>}
            <Switch id={id} type="hidden" checked={checked} {...other} onChange={handleChange}/>
        </Stack>
  );
};

const Onoff = React.memo(OnoffComponent, (prevProps, nextProps) => {
  return prevProps.value === nextProps.value;
});
export default Onoff;
