import React, { useState } from 'react';

import { Button, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import Icon from '../../../common/icon';
import ConfirmDialog from '../confirm-dialog/confirmDialog';

const BackToDashboardStyled = styled(Button)(({ theme }) => ({
  '&': {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    background: '#E3EAF1!important',
    width: '45%',
    borderRadius: '0!important',
    zIndex: '1',
    padding: '8.5px 20px!important',
    color: theme.palette.primary.main
  },
  '& span': {
    color: theme.palette.primary.main,
  },
  '&:hover': {
    background: 'rgba(0, 119, 155, 0.04)!important'
  },
  '& .yith-sales-icon': {
    display: 'flex',
    alignItems: 'center'
  },
}));

const BackToDashboard = ({ onclick, updated }) => {
  const [showConfirm, setShowConfirm] = useState(false);

  const onConfirm = () => {
    setShowConfirm(false);
    onclick();
  };
  const backToDashboard = () => {
    if (updated) {
      setShowConfirm(true);
    } else {
      onConfirm();
    }
  };

  const closeConfirm = (e) => {
    e.stopPropagation();
    setShowConfirm(false);
  };

  const confirmDialog = () => {
    return <ConfirmDialog open={true} onCancel={(e) => closeConfirm(e)} onConfirm={onConfirm} onClose={(e) => closeConfirm(e)} title={__('Are you sure?', 'wonder-cart')}
                          confirmButton={__('Yes, proceed without saving', 'wonder-cart')}>
      {__('Do you want to come back to the dashboard? The changes made may not be saved.', 'wonder-cart') }
    </ConfirmDialog>;
  };

  return (

            <BackToDashboardStyled onClick={backToDashboard} variant="text">
                <Icon icon="ArrowLongLeftIcon" sx={{ color: '#475569' }}/><Typography variant={'body3'} >{__('Back to WP', 'wonder-cart')}</Typography>
              {showConfirm && confirmDialog()}
            </BackToDashboardStyled>

  );
};

export default BackToDashboard;
