import React, { useEffect, useState } from 'react';

import { Input } from '@lapilli-ui/components';

import { FlexFieldWithLabelStyled } from '../../styles/FlexFieldWithLabel.styled';

const NumberComponent = ({ id, label, value, onSaveHandler, ...other }) => {
  const [currentValue, setCurrentValue] = useState(value);
  const handleChange = (value) => {
    setCurrentValue(value);
    const e = {
      target: {
        id,
        value
      }
    };

    setTimeout(() => onSaveHandler(e), 500);
  };
  useEffect(() => {
    setCurrentValue(value);
  }, [value]);

  return (
      <FlexFieldWithLabelStyled className='yith-sales-field-number'>
          {label && (<label htmlFor={id}>{label}</label>)}
        <Input type="number" id={id} value={currentValue} onChange={(_, newValue) => handleChange(newValue)} {...other} isMini={true}/>
      </FlexFieldWithLabelStyled>

  );
};

const Number = React.memo(NumberComponent, (prevProps, nextProps) => {
  return prevProps.value === nextProps.value;
});
export default Number;
