const PanelSection = ({ id, section, onChange, values }) => {
  const options = section.options;
  const optionIDS = Object.keys(options);
  const handleOnChange = (option, element) => {
    let newOPT = {};
    if (option?.fields) {
      newOPT = { sectionID: id, mainOptionID: option.id, subOptionID: element.target.id, optionValue: element.target.value };
    } else {
      newOPT = { sectionID: id, mainOptionID: option.id, subOptionID: false, optionValue: element.target.value };
    }
    onChange(newOPT);
  };

  return (
        <Section id={id}>
            <SectionTitle>
                <Typography variant='h3'
                            sx={{ fontSize: '1rem!important', fontWeight: '600!important' }} color = 'text.primary'>{section.title}</Typography>
                {section?.description && (<Typography color = 'text.secondary' sx={{ fontSize: '.8125rem', fontWeight: 'normal' }}>{section.description}</Typography>)}
            </SectionTitle>
            <SectionContent>
                {
                    optionIDS.map((optionID) => {
                      const option = options[optionID];
                      const label = option.name;
                      const field = { ...option };
                      delete field.name;
                      field.value = values[id][option.id];
                      if (field.type === 'inline-fields') {
                        for (const index in field.fields) {
                          field.fields[index] = { ...field.fields[index], isVisible: componentIsVisible(field.fields[index], values, false) };
                        }
                      }
                      const isVisible = componentIsVisible(field, values, false);

                      if (isVisible) {
                        field.isVisible = true;
                        return (
                            <Option className={`yith-sales-panel-option--${option.type}`} key={optionID}>
                              <Box className='yith-sales-panel-option-label'>
                                <label htmlFor={option.id}>{label}</label>
                              </Box>
                              <Box className='yith-sales-panel-option-content'>
                                <Field field={field} onSave={(element) => handleOnChange(field, element)} {...field.style}/>
                              </Box>
                              {option?.description &&
                                  <Box className='yith-sales-panel-option-description'>{option.description}</Box>}
                            </Option>
                        );
                      }
                      return '';
                    })
                }
            </SectionContent>
        </Section>
  );
};

export default PanelSection;
