import React, { useState } from 'react';

import { Button, Select } from '@lapilli-ui/components';

import { SelectedItemList } from '../index';

const SelectCondition = ({ args, value, itemsSelected = [], onClickSelectButton, onSave, ...other }) => {
  const [selectBtnClicked, setSelectBtnClicked] = useState(false);
  const getCurrentButtonLabel = (currentOPT) => {
    let filtered = false;
    filtered = args.buttonLabels.find(label => {
      return label.value === currentOPT;
    });
    return filtered?.value ? filtered : false;
  };

  const getEditCurrentButtonLabel = (currentOPT) => {
    let filtered = false;

    if (itemsSelected.length > 0) {
      filtered = args.editButtonLabels.find(label => {
        return label.value === currentOPT;
      });
    }

    return filtered?.value ? filtered : false;
  };

  const [buttonLabel, setButtonLabel] = useState(false);
  const [editButtonLabel, setEditButtonLabel] = useState(getEditCurrentButtonLabel(value));

  const handleChange = (value) => {
    itemsSelected = [];
    const currentOPT = value;
    const currentLabel = getCurrentButtonLabel(currentOPT);

    const metaValue = { trigger_type: value, ids: [] };
    const e = {
      target: {
        id: args.name,
        value: metaValue
      }
    };
    onSave(e);
    setButtonLabel(currentLabel);
  };

  const handlerRemoveItem = (removeItemID) => {
    const newItems = itemsSelected.filter(termId => {
      return termId !== removeItemID;
    });
    const metaValue = { trigger_type: value, ids: newItems };
    const e = {
      target: {
        id: args.name,
        value: metaValue
      }
    };
    onSave(e);
  };

  return (
        <div className="yith-sales-field-select-condition__wrapper">
            {!selectBtnClicked && (
                <Select
                    options={args.options}
                    onChange={handleChange}
                    value={value}
                    {...other}
                />
            )}
            {
                itemsSelected.length > 0 && (
                    <SelectedItemList termType={value} termIds={itemsSelected} onRemoveItem={handlerRemoveItem}/>
                )
            }
            {
                !editButtonLabel && buttonLabel && (
                    <Button size="lg" fullWidth={true}
                            onClick={() => onClickSelectButton(buttonLabel)}>{buttonLabel.label}</Button>
                )
            }
            {
                editButtonLabel && (
                    <Button size="lg" fullWidth={true}
                            onClick={() => onClickSelectButton(editButtonLabel)}>{editButtonLabel.label}</Button>
                )
            }
        </div>
  );
};
export default SelectCondition;
