import React, {useEffect, useState} from 'react';

import {Stack, Typography} from '@lapilli-ui/components';
import {__} from '@wordpress/i18n';

import Icon from '../../../../../common/icon';
import {isEmpty} from '../../../../../common/utils/common';

const Countdown = ({start = true, countdownValue, countdownType, onEnd}) => {
  const addMinutes = (date, minutes) => {
    return new Date(date.getTime() + minutes * 60000);
  };

  const addSeconds = (date, seconds) => {
    return new Date(date.getTime() + seconds * 1000);
  };

  const [endDate, setEndDate] = useState((countdownType === 'minutes') ? addMinutes(new Date(), countdownValue) : addSeconds(new Date(), countdownValue));

  const formatTimeNumber = (number) => {
    return number > 9 ? number : `0${number}`;
  };

  const calculateTimeLeft = () => {
    const difference = endDate - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
      };
    }

    return timeLeft;
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

  useEffect(() => {
    setEndDate((countdownType === 'minutes') ? addMinutes(new Date(), countdownValue) : addSeconds(new Date(), countdownValue));
    setTimeLeft(calculateTimeLeft());
  }, [countdownType, countdownValue]);

  useEffect(() => {
    if (start) {
      let mounted = true;
      if (isEmpty(timeLeft)) {
        clearTimeout(1000);
        if (typeof onEnd === 'function') {
          onEnd();
        }
      } else {
        setTimeout(() => {
          if (mounted) {
            setTimeLeft(calculateTimeLeft());
          }
        }, 1000);
      }
      return () => {
        mounted = false;
      };
    }
  }, [timeLeft]);

  const timerComponents = (timeLeft.hours > 0 ? formatTimeNumber(timeLeft.hours) + ':' : '') + formatTimeNumber(timeLeft.minutes) + ':' + formatTimeNumber(timeLeft.seconds);

  return (
      <Stack direction="row" sx={{margin: '10px auto', gap: '8px', textAlign: 'center'}}>
        <Icon sx={{color: '#d60000'}} icon="ClockIcon"/>
        <Typography sx={{
          color: '#d60000',
          fontSize: '16px',
        }}>{!isEmpty(timeLeft) ? timerComponents : __('Time is up!', 'wonder-cart')}</Typography>
      </Stack>
  );
};

export default Countdown;
