import React, { useState } from 'react';

import { Box, Button, DatePicker, Select, Stack } from '@lapilli-ui/components';
import { format as yithFormatDate } from '@lapilli-ui/date';
import { styled } from '@lapilli-ui/styles';
import Popover from '@mui/material/Popover';
import { __ } from '@wordpress/i18n';

import Icon from '../../../../common/icon';

const DateTimePickerToggle = styled('div')(({ theme }) => ({
  '&': {
    cursor: 'pointer',
    userSelect: 'none',
    outline: 'none',
    borderRadius: '7px',
    padding: '8.5px 15px',
    fontSize: '14px',
    lineHeight: '1.5',
    background: '#ffffff',
    color: '#333333',
    borderWidth: '1px',
    borderStyle: 'solid',
    borderColor: '#d8d8d8',
    display: 'inline-flex',
    alignItems: 'center'
  },
  '& .yith-sales-datepicker-toggle-label': {
    flex: 1,
    minWidth: '120px',
    color: 'rgba(30, 30, 30, 0.62)'
  },
  '& .yith-sales-datepicker-toggle-icon': {
    pointerEvents: 'none',
    display: 'flex',
    color: '#d8d8d8',
    fontSize: '1.25em',
    marginLeft: '6px'
  },
  '& .yith-sales-icon': {
    display: 'inline-flex',
    fontSize: 'inherit'
  }
}));
const DateTimePickerContent = styled('div')(({ theme }) => ({
  '& div[class*="DatePickerStatic"]': {
    borderRadius: 0
  },
  '& .yith-sales-datepicker-time-wrapper': {
    marginTop: '10px',
    marginBottom: '20px'
  },
  '& .yith-sales-datepicker-time-wrapper label': {
    display: 'block',
    textAlign: 'center',
    marginBottom: '5px',
    fontSize: '13px',
    fontWeight: '600'
  },
  '& .yith-sales-datepicker-time-wrapper button': {
    marginTop: '10px'
  }
}));
const DateTimePickerComponent = ({
  className,
  placeholder,
  name,
  id,
  onChange,
  value,
  inputFormat = 'Y-m-d',
  displayFormat = 'M j, Y',
  size = 'md',
  allowClear = false,
  innerRef,
  ...otherProps
}) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [date, setDate] = useState(value || new Date());
  const [formattedDate, setFormattedDate] = useState(value ? yithFormatDate(displayFormat, value) : '');
  const [hours, setHours] = useState(value ? yithFormatDate('g', value) : '0');
  const [minutes, setMinutes] = useState(value ? yithFormatDate('i', value) : '00');
  const [amPM, setAmPm] = useState(value ? yithFormatDate('a', value) : 'am');

  const handleButtonClick = (event) => {
    setAnchorEl(event.target);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const hoursOpts = [];
  const minutesOpts = [];
  const AmPmOpts = [
    { value: 'am', label: 'AM' },
    { value: 'pm', label: 'PM' }
  ];
  for (let i = 0; i < 13; i++) {
    const opt = i < 10 ? '0' + i : '' + i;
    hoursOpts.push({ value: '' + i, label: opt });
  }

  for (let i = 0; i < 60; i++) {
    const opt = i < 10 ? '0' + i : '' + i;
    minutesOpts.push({ value: opt, label: opt });
  }

  const convertHoursFrom12To24 = (hours, amPM) => {
    let newHours = parseInt(hours);
    if (amPM === 'pm' && newHours < 12) {
      newHours += 12;
    } else if (amPM === 'am' && newHours === 12) {
      newHours -= 12;
    }
    return newHours < 10 ? '0' + newHours : newHours;
  };

  const formatDate = (date, hours, minutes, amPM) => {
    const newDate = yithFormatDate('Y-m-d', date);
    const newHours = convertHoursFrom12To24(hours, amPM);
    const newValue = new Date(`${newDate}T${newHours}:${minutes}`);

    return newValue;
  };
  const setDateTime = () => {
    const newValue = formatDate(date, hours, minutes, amPM);
    setFormattedDate(yithFormatDate(displayFormat, newValue));
    onChange(newValue);
  };
  const handleChange = (value) => {
    setDate(value);
  };

  const handleHoursChange = (newHours) => {
    setHours(newHours);
  };
  const handleMinutesChange = (newMinutes) => {
    setMinutes(newMinutes);
  };
  const handleAmPmChange = (newAmPm) => {
    setAmPm(newAmPm);
  };

  const handleClickSetDateButton = () => {
    setDateTime();
    handleClose();
  };
  return (

        <Box className={`yith-sales-datetimepicker-root ${className}`} sx={{ display: 'inline-flex' }}>
            <DateTimePickerToggle className='yith-sales-datetimepicker-toggle' onClick={handleButtonClick}>
                <div
                    className='yith-sales-datepicker-toggle-label'>{formattedDate !== '' ? formattedDate : placeholder}</div>
                <div className='yith-sales-datepicker-toggle-icon'>
                    <Icon icon='CalendarDaysIcon' size={20}/>
                </div>
            </DateTimePickerToggle>
            <Popover open={!!anchorEl} onClose={handleClose} anchorEl={anchorEl}
                     anchorOrigin={{
                       vertical: 'bottom',
                       horizontal: 'left'
                     }}
                     sx={{ zIndex: 999999 }}
            >
                <DateTimePickerContent>
                    <Stack direction='column' align='center'>
                        <DatePicker id={id} name={name} inputFormat={inputFormat} displayFormat={displayFormat}
                                    size={size}
                                    allowClear={allowClear} onChange={handleChange}
                                    value={date !== null ? date : undefined} {...otherProps} isStatic={true}/>
                        <Box className='yith-sales-datepicker-time-wrapper'>
                            <label>{__('Time', 'wonder-cart')}</label>
                            <Stack direction='row' align='center' spacing={0.5}>
                                <Select options={hoursOpts} value={hours} onChange={handleHoursChange}
                                        className='yith-sales-time-hour' width='80px'/>
                                <span>:</span>
                                <Select options={minutesOpts} onChange={handleMinutesChange} value={minutes}
                                        className='yith-sales-time-minute' width='80px'/>
                                <Select options={AmPmOpts} value={amPM} onChange={handleAmPmChange}
                                        className='yith-sales-time-am-pm' width='80px'/>
                            </Stack>
                            <Button fullWidth={true} onClick={handleClickSetDateButton}>Set Date</Button>
                        </Box>
                    </Stack>
                </DateTimePickerContent>
            </Popover>
        </Box>

  );
};

const DateTimePicker = React.memo(DateTimePickerComponent, (prevProps, nextProps) => {
  return prevProps.value === nextProps.value;
});
export default DateTimePicker;
