import React from 'react';

import { Box, Container, Spinner, Stack, Typography } from '@lapilli-ui/components';
import { parse } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';

import Price from '../../../../../blocks/elements/price';
import { Divider } from '../../../../../common/fields';
import Icon from '../../../../../common/icon';
import { getFormattedTermName, getImage } from '../../../../../common/utils/common';
import { getRandomProductToShow } from '../../../../../common/utils/data';
import BlockEditorPreview from '../../../block-editor-preview';

const FreeProductInCart = ({ productToShow, showPrice, title }) => {
  const { data, isLoading, isFetching } = getRandomProductToShow(productToShow, 1);

  const inLoading = isLoading || isFetching;
  const productImage = getImage(data, true);
  return (
        <Container gutters className="yith-sales-cart-discount-preview-wrapper"
                   sx={{ backgroundColor: '#fff', maxWidth: '660px!important', padding: '45px', margin: '0 auto' }} maxWidth={false}>
            <Typography sx={{
              textTransform: 'uppercase',
              fontSize: '40px',
              color: '#E4E4E4',
              textAlign: 'center',
              fontWeight: '500',
              letterSpacing: '3.25px'
            }}>Cart</Typography>
            <Stack direction='column' spacing={2}>
                <Stack direction='row' spacing={2} align='center' justify='center'>
                    <Icon icon="PhotoIcon" size='100'
                          sx={{ color: '#E4E4E4', padding: '12px', backgroundColor: '#FBFBFB' }}/>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                </Stack>
                <Stack direction='row' spacing={2} align='center' justify='center'>
                    <Icon icon="PhotoIcon" size='100'
                          sx={{ color: '#E4E4E4', padding: '12px', backgroundColor: '#FBFBFB' }}/>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                </Stack>
                <Stack direction='row' spacing={2} align='center' justify='center'>
                    {inLoading && (
                        <Spinner sx={{ marginLeft: '50%', marginTop: '20px', marginBottom: '20px' }}/>
                    )}
                    {!inLoading && (
                        <>
                            <Box>
                                <img src={productImage.src} alt={productImage.alt} width='100px' height='auto'
                                     style={{ border: '1px solid #E5E5E5', height: 'auto' }}/>
                            </Box>
                            <Box sx={{ width: '300px' }}>
                                { title && <BlockEditorPreview blocks={parse(title)}/>}
                                <Typography sx={{ fontWeight: '500' }}>{getFormattedTermName(data)}</Typography>
                            </Box>
                            <Box sx={{ width: '70px' }}>
                                <Typography component='span' sx={{
                                  fontSize: '13px',
                                  lineHeight: '22px',
                                  fontWeight: '500',
                                  color: '#B6A000',
                                  textTransform: 'uppercase',
                                  marginRight: '5px'
                                }}>{__('Free!', 'wonder-cart')}</Typography>
                                { showPrice && (<Price price={data.price} showDel={true}/>) }
                            </Box>

                        </>

                    )}
                </Stack>
            </Stack>
            <Divider sx={{ width: '90%', margin: '40px auto', borderColor: '#E0E0E0' }}/>
            <Stack direction='column' spacing={2} align='end' justify='end' sx={{ marginRight: '40px' }}>
                <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
            </Stack>
            <Divider sx={{ width: '90%', margin: '40px auto', borderColor: '#E0E0E0' }}/>
            <Stack direction='column' spacing={1} align='end' sx={{ marginRight: '40px', marginTop: '26px' }}>
                <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
            </Stack>
        </Container>
  );
};

export default FreeProductInCart;
