import React from 'react';

import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import PreviewImage from '../../elements/preview-image';
import WizardPreview from '../../elements/wizard-preview';

import Bogo from './index';

const BogoPreview = ({ campaign, type, showPreview, step }) => {
  return (
      <React.Fragment>
        {showPreview && (<PreviewImage src={getStaticPreviewImageByType(type)}/>)}
        {!showPreview && <WizardPreview className="boxed" sx={{ backgroundColor: 'transparent', height: '600px' }}><Bogo campaign={campaign} step={step}/></WizardPreview>}
      </React.Fragment>
  );
};

export default BogoPreview;
