import React from 'react';

import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import PreviewImage from '../../elements/preview-image';
import WizardPreview from '../../elements/wizard-preview';

import CartDiscount from './index';

const CartDiscountPreview = ({ campaign, type, step, showPreview }) => {
  return (
      <React.Fragment>
        {showPreview && <PreviewImage src={getStaticPreviewImageByType(type)} hasBgColor={false}/>}
        {!showPreview && <WizardPreview className="boxed" minHeight='600px' maxWidth='600px' sx={{ backgroundColor: 'transparent', height: '600px' }}><CartDiscount campaign={campaign} step={step}/></WizardPreview>}
      </React.Fragment>
  );
};

export default CartDiscountPreview;
