import { createSlice } from '@reduxjs/toolkit';

import { buildInitialSettingState } from '../../../common/utils/common';

const resetPanelState = (settings) => {
  const sections = {};
  for (const sectionID in settings) {
    const options = settings[sectionID];
    sections[sectionID] = {};
    for (const optionKey in options) {
      const option = options[optionKey];
      sections[sectionID][option.id] = option.value;
    }
  }
  return sections;
};

const initSetting = buildInitialSettingState();
const initialState = {
  saved: initSetting,
  current: initSetting,
  updates: []
};

const panelSlice = createSlice({
  name: 'panelConfig',
  initialState,
  reducers: {
    updateOption: (state, action) => {
      const { panelID, sectionID, mainOptionID, subOptionID = false, optionValue } = action.payload;
      let oldValue = '';

      if (subOptionID) {
        oldValue = state.current[panelID][sectionID][mainOptionID][subOptionID];
        if (oldValue !== optionValue) {
          state.current[panelID][sectionID][mainOptionID][subOptionID] = optionValue;
        }
      } else {
        oldValue = state.current[panelID][mainOptionID];
        if (oldValue !== optionValue) {
          state.current[panelID][sectionID][mainOptionID] = optionValue;
        }
      }
    },
    saveSettings: (state, action) => {
      state.saved = action.payload;
    },
    resetPanel: (state, action) => {
      const { panelID, config } = action.payload;

      state.current[panelID] = resetPanelState(config);

      state.saved = state.current;
      state.updates = [];
    },
    addUpdatedOptions: {
      reducer: (state, action) => {
        const { panelID, mainOptionID, optionValue } = action.payload;
        const newUpdates = state.updates.filter(option => option.id !== mainOptionID);
        state.updates = [...newUpdates];
        state.updates.push({ id: mainOptionID, group_id: panelID, value: optionValue });
      },
      prepare: (value) => {
        return { payload: { ...value } };
      }

    },
    clearUpdatedOption: {
      reducer: (state) => {
        state.updates = [];
      },
      prepare: (value) => {
        return { payload: {} };
      }
    }
  }
});

export const {
  updateOption,
  saveSettings,
  resetPanel,
  addUpdatedOptions,
  clearUpdatedOption
} = panelSlice.actions;
export default panelSlice.reducer;
