import React from 'react';

import { IconButton, Spinner } from '@lapilli-ui/components';
import Link from '@mui/material/Link';
import { store as core } from '@wordpress/core-data';
import { useSelect } from '@wordpress/data';
import { addFilter } from '@wordpress/hooks';
import { MediaUpload } from '@wordpress/media-utils';

import Icon from '../../../common/icon';

export function MediaUploadCheck (props) {
  const {
    fallback = null,
    children
  } = props;

  const {
    checkingPermissions,
    hasUploadPermissions
  } = useSelect((select) => {
    return {
      hasUploadPermissions: select(core).canUser('read', 'media'),
      checkingPermissions: !select(core).hasFinishedResolution('canUser', ['read', 'media'])
    };
  }, []);

  return <>
        {checkingPermissions && <Spinner/>}
        {(!checkingPermissions && hasUploadPermissions) ? children : fallback}
    </>;
}

const replaceMediaUpload = () => MediaUpload;

addFilter(
  'editor.MediaUpload',
  'core/edit-post/components/media-upload/replace-media-upload',
  replaceMediaUpload
);

const MyMediaUploader = ({
  onSelect,
  allowedMediaTypes = ['image'],
  editorState = null,
  mediaHandlerComponentType = 'button',
  label,
  icon = 'PhotoIcon',
  size = 20,
  buttonClassName = ''
}) => {
  return (
        <MediaUploadCheck>
            <MediaUpload
                onSelect={(media) => onSelect(media, editorState)}
                allowedTypes={allowedMediaTypes}
                value=''
                render={({ open }) => (
                    <>
                        {mediaHandlerComponentType === 'button' &&
                            <IconButton title={label} onClick={open} className={buttonClassName}><Icon icon={icon}
                                                                                                       size={size}/></IconButton>}
                        {mediaHandlerComponentType === 'link' && <Link href='#' onClick={(e) => {
                          e.preventDefault();
                          open();
                        }}>{label}</Link>}

                    </>
                )}
            />
        </MediaUploadCheck>

  );
};

export default MyMediaUploader;
