import React from 'react';

import { Stack } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';

const PreviewContainer = styled(Stack, { shouldForwardProp: (prop) => prop !== 'hasBgColor' })(({ theme, hasBgColor }) => ({
  height: 'calc(100vh - 200px)',
  width: '100%',
  position: 'relative',
  backgroundColor: hasBgColor ? '#fff' : 'transparent'

}));
const PreviewImageStyled = styled(Stack, { shouldForwardProp: (prop) => prop !== 'width' && prop !== 'height' })(({ theme, width, height }) => ({
  maxWidth: width !== 'auto' ? '800px' : 'none',
  padding: '30px',
  '& img': {
    width: '100%'
  }
}));
const PreviewImage = ({ src, alt, width = 'auto', height = 'auto', fullsize = false, hasBgColor = true }) => {
  const containerClass = fullsize ? 'fullsize' : '';
  return (
        <PreviewContainer className={containerClass} direction='row' align='center' hasBgColor={hasBgColor} justify='center'>
            <PreviewImageStyled width={width} height={height}>
              <img src={src} alt={alt}/>
            </PreviewImageStyled>
        </PreviewContainer>
  );
};

export default PreviewImage;
