import React, { useState } from 'react';

import { Box, Input } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { ChromePicker } from 'react-color';

const ColorPickerWrapper = styled('div')(({ ownerState, theme }) => ({
  border: '1px solid #D8D8D8',
  borderRadius: '3px',
  minWidth: '134px',
  maxWidth: '200px',
  position: 'relative',
  '& .yith-sales-color-picker-info': {
    display: 'flex',
    alignItems: 'center'
  },
  '& .yith-sales-color-picker-info .yith-sales-color-picker-color-code': {
    flex: 1
  },
  '& .yith-sales-color-picker-info .yith-sales-color-picker-color-code input': {
    padding: '10px 5px!important'
  },
  '& .yith-sales-color-picker-info .yith-sales-color-picker-color': {
    width: '28px',
    height: '28px',
    border: '1px solid #D8D8D8',
    marginLeft: '5px'
  },
  '& .yith-sales-color-picker-info .yith-sales-color-picker-color:hover': {
    cursor: 'pointer'
  },
  '& .yith-sales-color-picker-info .yith-sales-color-picker-color-code > div': {
    maxWidth: '110px',
    fontSize: '10.5px',
    margin: 0,
    padding: 0
  },
  ...(ownerState.hasFocus && {
    border: `1px solid ${theme.fields.focusedBorderColor}`
  })
}));

const ColorPickerPopup = styled('div')(({ theme }) => ({
  position: 'absolute',
  zIndex: 99,
  '&.align-right': {
    right: 0,
    left: 'auto'
  },
  '& .yith-sales-colorpicker-bg': {
    position: 'fixed',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0
  },
  '& .chrome-picker': {
    margin: '10px 0 5px 0',
    fontFamily: 'Inter!important'
  }
}));
const ColorPickerComponent = ({ id, label, value, align, onSaveHandler, ...other }) => {
  const [showColorPicker, setShowColorPicker] = useState(false);
  const [hasFocus, setHasFocus] = useState(false);
  const handleChangeColor = (color, event) => {
    const rgb = color.rgb;
    const e = {
      target: {
        id,
        value: `rgba(${rgb.r},${rgb.g},${rgb.b},${rgb.a})`
      }
    };

    onSaveHandler(e);
  };

  const ownerState = { hasFocus };
  const handleInputChange = (event) => {
    const color = event.target.value;
    const e = {
      target: {
        id,
        value: color
      }
    };
    onSaveHandler(e);
  };

  const toggleColorPicker = (isOpen) => {
    setShowColorPicker(isOpen);
    setHasFocus(isOpen);
  };
  const setFocus = (hasFocus) => {
    if (!showColorPicker) {
      setHasFocus(hasFocus);
    }
  };

  return (
      <Box>
        <label style={{ textTransform: 'uppercase', letterSpacing: '0.5', fontSize: '10px', marginBottom: '5px' }}>{label}</label>
        <ColorPickerWrapper className="yith-sales-colorpicker-wrapper" ownerState={ownerState}>
            <div className="yith-sales-color-picker-info">
                <div className="yith-sales-color-picker-color" style={{ backgroundColor: value, borderRadius: '3px' }} onClick={() => toggleColorPicker(!showColorPicker)}></div>
                <div className="yith-sales-color-picker-color-code"><Input type="text" value={value} onChange={handleInputChange} onFocus={() => setFocus(true)} onBlur={() => setFocus(false)} variant="ghost" size="md"/> </div>
            </div>
            {showColorPicker && (
                <ColorPickerPopup className={`yith-sales-color-picker-popup-field align-${align}`}>
                <div className="yith-sales-colorpicker-bg" onClick={() => toggleColorPicker(false) }/>
                        <ChromePicker color={value} onChangeComplete={handleChangeColor} />
                </ColorPickerPopup>
            )
            }
        </ColorPickerWrapper>
      </Box>
  );
};
const ColorPicker = React.memo(ColorPickerComponent, (prevProps, nextProps) => {
  return prevProps.value === nextProps.value;
});
export default ColorPicker;
