import React, { useEffect, useRef, useState } from 'react';

import { Button, Stack, Switch, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';
import { useDispatch } from 'react-redux';

import Icon from '../../../common/icon';
import { DATE_FORMAT, TIME_FORMAT } from '../../config';
import { updateCampaign } from '../../slices/editor/campaign-slice';
import { getStatusCampaignLabel, getStatusToShow } from '../../utils/admin-functions';
import { DateTimePicker } from '../fields';
import StatusBadge from '../status-badge';

const SettingsSidebarStyled = styled('div')(({ theme }) => ({
  background: '#fafafa',
  boxShadow: '0 4px 20px 5px #DDEEF9',
  padding: '40px',
  borderBottomRightRadius: '20px',
  zIndex: 2,
  width: '305px'
}));

const SettingSidebarFieldWrapper = styled('div')(({ theme }) => ({
  marginBottom: '30px',
  '& label': {
    fontWeight: 450,
    fontSize: '14px',
    lineHeight: '23px',
    marginBottom: '15px',
    display: 'block'
  },
  '& .date-picker': {

    width: '100%!important'
  },
  '& .yith-sales-datepicker-toggle-label': {
    fontSize: '11px'
  },
  '& .date-picker >div': {
    width: '100%!important'
  }
}));

const SettingSidebarButtonWrapper = styled('div')(({ theme }) => ({
  '& button': {
    marginTop: '15px'
  },
  '& button[disabled]': {
    opacity: '0.5',
    pointerEvents: 'none'
  }
}));

const SettingsSidebar = ({ currentCampaign, onSave, backToDashboard, updated = false }) => {
  const { campaign } = currentCampaign;
  const statusToShow = getStatusToShow(campaign);
  const dispatch = useDispatch();
  const [showEndDate, setShowEndDate] = useState(campaign.schedule_date_to > 0);
  const [showNotice, setShowNotice] = useState(updated);
  const displayFormat = DATE_FORMAT + ' - ' + TIME_FORMAT;

  useEffect(() => {
    setShowEndDate(campaign.schedule_date_to > 0);
  }, [campaign.schedule_date_to]);

  useEffect(() => {
    setShowNotice(updated);
  }, [updated]);
  const startDate = useRef();

  const handleActivateCampaign = (e) => {
    const newStatus = e.target.checked ? 'active' : 'inactive';
    const scheduleDateFrom = newStatus === 'active' ? parseInt(Date.now() / 1000) : campaign.schedule_date_from;
    dispatch(updateCampaign({
      campaign: {
        ...campaign, campaign_status: newStatus, schedule_date_from: scheduleDateFrom
      },
      updated: true
    }));
    setShowNotice(false);
  };

  const handleChangeShowEndDateCampaign = () => {
    setShowEndDate(!showEndDate);
    setShowNotice(false);
    dispatch(updateCampaign({
      campaign: {
        ...campaign, campaign_status: campaign.campaign_status, schedule_date_to: 0
      },
      updated: true
    }));
  };

  const updateStartDate = (value) => {
    const timestamp = Date.parse(value) / 1000;
    dispatch(updateCampaign({
      campaign: { ...campaign, schedule_date_from: timestamp },
      updated: true
    }));
    setShowNotice(false);
  };

  const updateEndDate = (value) => {
    const timestamp = Date.parse(value) / 1000;
    dispatch(updateCampaign({
      campaign: { ...campaign, schedule_date_to: timestamp },
      updated: true
    }));
    setShowNotice(false);
  };

  const startDateValue = parseInt(campaign.schedule_date_from) > 0 ? new Date(campaign.schedule_date_from * 1000) : null;
  const endDateValue = parseInt(campaign.schedule_date_to) > 0 ? new Date(campaign.schedule_date_to * 1000) : undefined;
  const saveButtonLabel = __('Save Changes', 'wonder-cart');

  return (<SettingsSidebarStyled>
        <Stack justify="space-between" sx={{ height: '100%' }}>
            <div className="yith-settings-sidebar__settings">
                <SettingSidebarFieldWrapper>
                    <label
                        htmlFor="yith-sales-campaign-active">{__('Campaign status', 'wonder-cart')}</label>
                    <Stack direction="row" justify="space-between" className="show-inline">
                        <Stack direction="row" align="center" sx={{ fontWeight: 700, gap: '10px' }}
                               className="switch-status-wrapper">
                            <Switch id="yith-sales-campaign-active" type="hidden"
                                    checked={campaign.campaign_status === 'active'}
                                    onChange={handleActivateCampaign}/>
                          <Typography variant='body2'>{getStatusCampaignLabel(campaign.campaign_status)}</Typography>
                        </Stack>
                        <StatusBadge label={statusToShow}/>
                    </Stack>
                </SettingSidebarFieldWrapper>
                {campaign.campaign_status === 'active' && <>
                    <SettingSidebarFieldWrapper>
                        <label
                            htmlFor="yith-sales-campaign-active">{__('Start date:', 'wonder-cart')}</label>
                        <DateTimePicker id="yith-sales-campaign-schedule_date_from" className="date-picker"
                                        innerRef={startDate} value={startDateValue}
                                        onChange={updateStartDate}
                                        minDate={new Date()}
                                        maxDate={endDateValue}
                                        displayFormat={displayFormat}
                        />
                    </SettingSidebarFieldWrapper>

                    <SettingSidebarFieldWrapper>
                        <Stack direction="row" justify="space-between" className="show-inline">
                            <label
                                htmlFor="yith-sales-campaign-show-end-date">{__('Set the end date:', 'wonder-cart')}</label>
                            <Switch id="yith-sales-campaign-show-end-date" type="hidden"
                                    checked={showEndDate}
                                    onChange={handleChangeShowEndDateCampaign}/>

                        </Stack>
                        {showEndDate &&
                            <DateTimePicker id="yith-sales-campaign-schedule_date_to" className="date-picker"
                                            minDate={startDateValue}
                                            value={endDateValue}
                                            placeholder={__('Set the end date', 'wonder-cart')}
                                            onChange={updateEndDate} displayFormat={displayFormat}/>}
                    </SettingSidebarFieldWrapper>
                </>}
            </div>
            <SettingSidebarButtonWrapper>
                {showNotice
                  ? (
                      <Stack direction='column' sx={{ background: 'rgba(243, 253, 246)', border: '1px solid #83d88c', padding: '10px' }} spacing={1}>
                        <Stack direction='row' spacing={1} align='center'>
                          <Icon icon="CheckCircleIconSolid" sx={{ color: '#83d88c' }}/>
                          <Typography sx={{ fontWeight: 600, flex: 1 }}>{__('Campaign updated!', 'wonder-cart')}</Typography>
                        </Stack>
                        <Typography> {__('Continue to edit the campaign or exit the edit mode to go back to the dashboard', 'wonder-cart')}</Typography>
                      </Stack>
                    )
                  : (
                        <Button className="button-save" size="md" variant="contained"
                                fullWidth={true} onClick={onSave}
                                disabled={currentCampaign.disableNext}>{saveButtonLabel}</Button>
                    )
                }
                <Button className="button-save" size="md" variant="outlined"
                        fullWidth={true}
                        onClick={backToDashboard}>{__('Exit edit mode', 'wonder-cart')}</Button>
            </SettingSidebarButtonWrapper>
        </Stack>
    </SettingsSidebarStyled>);
};

export default SettingsSidebar;
