import React from 'react';

import { Box, Stack } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';

const DynamicPreviewContainer = styled(Box)(({ theme, modal, minheight: minHeight, backgroundColor, maxwidth: maxWidth }) => ({
  '&': {
    width: '100%',
    marginLeft: 'auto',
    boxSizing: 'border-box',
    marginRight: 'auto',
    display: 'block',
    backgroundColor,
    height: 'calc(100vh - 150px)',
    position: 'relative',
    pointerEvents: 'none',
    minHeight
  },
  '&.boxed': {
    marginTop: '100px',
    borderRadius: '7px',
    position: 'relative',
    pointerEvents: 'none',
    maxWidth,
    height: 'auto'
  }
}));
const WizardPreview = ({ className, minHeight = '400px', maxWidth = '800px', modal = false, backgroundColor = '#fff', children, sx = {} }) => {
  const extraStyle = { height: 'calc(100vh - 150px)', backgroundColor: '#fff', ...sx };
  return (
      <DynamicPreviewContainer className={className} minheight={minHeight} maxwidth={maxWidth}>
      <Stack direction='row' align='center' justify='center' sx={extraStyle }>
        {children}
      </Stack>
      </DynamicPreviewContainer>
  );
};

export default WizardPreview;
