import React, { useEffect, useState } from 'react';

import { Box, Button, Input, Select, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __, _n, _x, sprintf } from '@wordpress/i18n';
import { useSelector } from 'react-redux';

import SimpleModal from '../../../../blocks/elements/simple-modal';
import { VerticalScrollableContentStyled } from '../../../../blocks/elements/styles/VerticalScrollableContent.styled';
import { useGetTermsQuery } from '../../../../common/api/products';
import { useGetProductsQuery } from '../../../../common/api/yith-products';
import { getFormattedTermName, isEmpty } from '../../../../common/utils/common';
import { getTermsByType } from '../../../../common/utils/data';
import { wcSettings } from '../../../config';
import { PreviewItemName, PreviewItemsBoxContainer, PreviewItemsBoxWrapper } from '../../styles/PreviewItemsBox.styled';

const PromoRule = styled(Box)(({ theme }) => ({
  '&': {
    lineHeight: '65px',
    marginBottom: '20px'
  },
  '& .promo-rule-qty': {
    padding: '0 10px'
  }
}));
const XYPromoRule = ({ id, label, value, buttonLabel, onSaveHandler, required, showError }) => {
  const currentCampaign = useSelector((state) => state.campaign);
  const [isModalVisible, setModalVisible] = useState(false);
  const meta = currentCampaign.campaign.meta[currentCampaign.campaign.type];
  const [dataX, setDataX] = useState([]);
  const [dataY, setDataY] = useState([]);
  const trigger = meta.trigger_product;
  const productToShow = meta.products_to_offer.type === 'same' ? trigger : meta.products_to_offer;

  const { data: dataCatX, isFetching: isFetchingCatX, isLoading: isLoadingCatX } = useGetTermsQuery({ termType: trigger.type, termIds: trigger.ids, fetchWithEmptyTerms: false }, { skip: trigger.type !== 'categories' });
  const { data: dataProdX, isFetching: isFetchingProdX, isLoading: isLoadingProdX } = useGetProductsQuery({ termType: trigger.type, termIds: trigger.ids, fetchWithEmptyTerms: false }, { skip: trigger.type !== 'products' });

  const { data: dataCatY, isFetching: isFetchingCatY, isLoading: isLoadingCatY } = useGetTermsQuery({ termType: productToShow.type, termIds: productToShow.ids, fetchWithEmptyTerms: false }, { skip: productToShow.type !== 'categories' });
  const { data: dataProdY, isFetching: isFetchingProdY, isLoading: isLoadingProdY } = useGetProductsQuery({ termType: productToShow.type, termIds: productToShow.ids, fetchWithEmptyTerms: false }, { skip: productToShow.type !== 'products' });

  const inFetchingX = isFetchingCatX || isLoadingCatX || isFetchingProdX || isLoadingProdX;
  const inFetchingY = isFetchingProdY || isFetchingCatY || isLoadingCatY || isLoadingProdY;

  const [rule, setRule] = useState(!isEmpty(value) ? value : { itemsToBuy: 1, itemsToGet: 1, discount_value: 10, discount_type: 'percentage' });
  const [saved, setSaved] = useState(!isEmpty(value) > 0);
  const [itemX, setItemX] = useState('');
  const [itemY, setItemY] = useState('');

  const options = [
    { value: 'percentage', label: __('% - percentage discount', 'wonder-cart') },
    { value: 'fixed', label: sprintf(__('%s - fixed discount', 'wonder-cart'), wcSettings.currency.symbol) }
  ];

  useEffect(() => {
    if (trigger.type === 'categories' && dataCatX && dataCatX.length) {
      setDataX(dataCatX);
    } else {
      setDataX(dataProdX);
    }
  }, [dataCatX, dataProdX]);
  useEffect(() => {
    if (productToShow.type === 'categories' && dataCatY && dataCatY.length) {
      setDataY(dataCatY);
    } else {
      setDataY(dataProdY);
    }
  }, [dataCatY, dataProdY]);

  const handleOpenModal = () => {
    setModalVisible(true);
  };

  const handleCloseModal = () => {
    if (!saved) {
      setRule({});
    }
    setModalVisible(false);
  };
  const getSelectedXItems = () => {
    let itemXSelected = '';

    if (dataX && dataX.length) {
      itemXSelected = getFormattedTermName(dataX[0]);
      if (trigger.type === 'categories') {
        itemXSelected = sprintf(_x('%s category', '[Part OF]: Shop category', 'wonder-cart'), itemXSelected);
      }
    }

    return itemXSelected;
  };

  const getSelectedYItems = () => {
    let itemYSelected = '';

    if (productToShow.type === 'same') {
      return itemX;
    }
    if (dataY && dataY.length) {
      itemYSelected = getFormattedTermName(dataY[0]);
      if (productToShow.type === 'categories') {
        itemYSelected = sprintf(_x('%s category', '[Part OF]: Shop category', 'wonder-cart'), itemYSelected);
      }
    }

    return itemYSelected;
  };

  useEffect(() => {
    setItemX(getSelectedXItems());
  }, [dataX]);
  useEffect(() => {
    setItemY(getSelectedYItems());
  }, [dataX, dataY]);

  const handleOnChange = (value, id, property) => {
    const newRule = { ...rule };
    newRule[property] = value;
    setRule(newRule);
  };
  const handleSave = () => {
    const e = {
      target: {
        id,
        value: rule
      }
    };
    setSaved(true);
    onSaveHandler(e);
    setModalVisible(false);
  };

  const handleChangeNumber = (e, id, property) => {
    setTimeout(() => handleOnChange(parseInt(e.target.value), id, property), 500);
  };

  const error = showError && required && isEmpty(value);
  const typeMessage = __('Set discount rules before continuing', 'wonder-cart');
  const showNotice = error ? <Typography className="yith-sales-field-error" sx={{ padding: '10px 0' }}>{typeMessage}</Typography> : '';

  const PreviewPromoRule = () => {
    const discountType = rule.discount_type === 'percentage' ? '%' : wcSettings.currency.symbol;
    return (<PreviewItemsBoxContainer>
      <PreviewItemsBoxWrapper>
        <VerticalScrollableContentStyled maxHeight="98px">
          <ul>
            <li>
              <PreviewItemName>
                {sprintf(__('%d %s = %d %s with %s discount', 'wonder-cart'), rule.itemsToBuy, itemX, rule.itemsToGet, itemY, rule.discount_value + discountType)}
              </PreviewItemName>
            </li>
          </ul>
        </VerticalScrollableContentStyled>
      </PreviewItemsBoxWrapper>
    </PreviewItemsBoxContainer>);
  };
  return (
      <>
        <label>{label}</label>
        {!inFetchingX && !inFetchingY && !isEmpty(value) && <PreviewPromoRule/>}
        <Button size="lg" fullWidth={true} onClick={handleOpenModal} variant="outlined" color="secondary">{buttonLabel}</Button>
        {showNotice}
        <SimpleModal title={__('Promo rule', 'wonder-cart')} open={isModalVisible} onClose={handleCloseModal}>
          <p style={{ marginBottom: '30px' }}>{__('Set the discount to apply to products of selected categories.', 'wonder-cart')}</p>
          {!inFetchingX && !inFetchingY && <PromoRule>
            <Typography variant='body3'>{__('If users purchases', 'wonder-cart')}</Typography> <Typography variant='body3' className="promo-rule-qty"><Input size="lg" type="number" defaultValue={rule.itemsToBuy} onChange={(e) => handleChangeNumber(e, rule.itemsToBuy, 'itemsToBuy')} isMini={true}/></Typography>
            <Typography variant='body3'>{_n('item of', 'items of', rule.itemsToBuy, 'wonder-cart')} {itemX} {__('gets', 'wonder-cart')}</Typography>
            <Typography variant='body3' className="promo-rule-qty"><Input size="lg" type="number" defaultValue={rule.itemsToGet} onChange={(e) => handleChangeNumber(e, rule.itemsToGet, 'itemsToGet')} isMini={true}/></Typography> <Typography variant='body3'>{__('of ', 'wonder-cart')}</Typography>

            <Typography variant='body3'>{itemY} {__('with a ', 'wonder-cart')}</Typography>
            <Select size="lg" classes={{ root: 'discount-type' }} options={options} onChange={(value) => handleOnChange(value, rule.id, 'discount_type')} value={rule.discount_type}/>
            <Typography variant='body3' className="promo-rule-qty"><Input size="lg" type="number" defaultValue={rule.discount_value} onChange={(e) => handleChangeNumber(e, rule.id, 'discount_value')} isMini={true}/></Typography>
          </PromoRule>}
          <Button size="lg" fullWidth={true} onClick={handleSave}>{__('Save', 'wonder-cart')}</Button>
        </SimpleModal>
      </>
  );
};

export default XYPromoRule;
