import React, { useState } from 'react';

import { Box, HStack, IconButton, Input, Typography, VStack } from '@lapilli-ui/components';
import { keyframes, styled } from '@lapilli-ui/styles';
import { __, _x, sprintf } from '@wordpress/i18n';

import Icon from '../../../../../src/common/icon';
import { isEmpty } from '../../../utils/admin-functions';
import MediaGallery from '../../media-gallery';
import Notice from '../../notices/notice';

const { wp } = window;
const myKeyframe = keyframes`
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
`;
const MediaWrap = styled(Box)(({ theme }) => ({
  '&': {
    maxWidth: '400px'
  },
  '&.yith-plugin-fw-media--has-tabs': {
    border: '1px solid #cbd5e1',
    borderRadius: '6px',
    boxShadow: '0 1px 2px 0 rgba(0, 0, 0, 0.05)'
  },
  '& .yith-sales-media-tab': {
    flex: '1',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    padding: '12px 24px',
    borderRadius: '6px',
    border: '1px solid #cbd5e1',
    background: 'rgba(203, 213, 225, .25)',
    margin: '-1px',
    cursor: 'pointer'
  },
  '& .yith-sales-media-tab:hover': {
    color: '#0079b0'
  },
  '& .yith-sales-media-tab.yith-sales-media-tab--active': {
    color: '#0079b0',
    fontWeight: '500',
    border: 'none',
    background: 'transparent',
    cursor: 'default'
  },
  '&.yith-sales-media--has-tabs .yith-sales-media__content': {
    padding: '24px'
  },
  '& .yith-sales-media__preview': {
    position: 'relative',
    border: '1px dashed #cbd5e1',
    background: 'rgba(203, 213, 225, .25)'
  },
  '& .yith-sales-media__preview.yith-sales-media--is-dragging:before': {
    content: "''",
    position: 'absolute',
    top: '0',
    left: '0',
    width: '100%',
    height: '100%',
    background: 'rgba(0, 121, 176, .4)',
    zIndex: '998',
    backdropFilter: 'blur(3px)',
    WebkitBackdropFilter: 'blur(3px)',
    animation: `${myKeyframe} .3s forwards`
  },

  '& .yith-sales-media__preview__file': {
    backgroundColor: '#fff',
    padding: '16px',
    width: '100%'
  },
  '& .yith-sales-media__actions': {
    position: 'absolute',
    top: '7px',
    right: '7px'
  },
  '& .yith-sales-media__edit, & .yith-sales-media__delete,& .yith-sales-media__edit:hover, & .yith-sales-media__delete:hover': {
    padding: 0,
    margin: 0,
    border: '0',
    borderRadius: 'unset',
    background: 'none',
    boxShadow: 'unset'
  },
  '& .yith-sales-media__delete': {
    color: '#c41d04',
    marginLeft: '8px!important'
  }
}));
const Media = ({ id, label, value, onSaveHandler, ...other }) => {
  const [currentTab, setCurrentTab] = useState('upload');
  const [dragActive, setDragActive] = useState(false);
  const [error, setError] = useState('');
  const {
    showTabs = 'yes',
    allowedTypes = [],
    size = 'sm'
  } = other;

  let height = 55;
  switch (size) {
    case 'md':
      height *= 2;
      break;
    case 'lg':
      height *= 3;
      break;
    case 'xl':
      height *= 4;
      break;
  }

  const isImage = (url) => {
    const fileType = url.split('.').pop().toLowerCase();

    const isImage = /(jpg|jpeg|png|gif|ico|svg|jpe|webp)$/.test(fileType);

    return isImage;
  };
  const handleSelectMedia = (media) => {
    const newMedia = {
      id: media.id,
      type: media.type,
      url: media.url,
      fileName: media.filename
    };

    const e = {
      target: {
        id,
        value: newMedia
      }
    };
    onSaveHandler(e);
  };

  const handleRemoveMedia = () => {
    const e = {
      target: {
        id,
        value: {}
      }
    };

    onSaveHandler(e);
  };
  const handleChangeMediaUrl = (e, url) => {
    const mediaType = isImage(url) ? 'image' : 'file';

    const newMedia = {
      id: '',
      url,
      type: mediaType,
      fileName: url.split('/').pop()
    };
    const ev = {
      target: {
        id,
        value: newMedia
      }
    };
    onSaveHandler(ev);
  };

  const handleDrag = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === 'dragenter' || e.type === 'dragover') {
      if (!e.dataTransfer) {
        e.dataTransfer = e.originalEvent.dataTransfer;
      }

      e.dataTransfer.dropEffect = 'copy';
      setDragActive(true);
    } else if (e.type === 'dragleave') {
      setDragActive(false);
    }
  };

  const handleDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    if (!e.dataTransfer) {
      e.dataTransfer = e.originalEvent.dataTransfer;
    }

    let files = e.dataTransfer.files;

    if (files.length > 1) {
      files = files[0];
    }

    const mineType = files[0].type.toLowerCase();
    const generalType = mineType.split('/')[0];

    const rightFileType = allowedTypes.length === 0 || allowedTypes.indexOf(mineType) !== -1 || allowedTypes.indexOf(generalType) !== -1;
    if (!files.length) {
      setError(__('No file provided.', 'wonder-cart'));
    } else if (!rightFileType) {
      setError(sprintf(__('Sorry, you are not allowed to upload %s files.', 'wonder-cart'), mineType));
    } else if ('mediaUtils' in wp && 'uploadMedia' in wp.mediaUtils) {
      wp.mediaUtils.uploadMedia(
        {
          filesList: files,
          onFileChange: function (data) {
            // Check for the ID, since uploadMedia triggers onFileChange at the start with a temporary blob URL to allow previewing it before uploading.
            if (data && data.length && data[0] && 'id' in data[0]) {
              const newMedia = {
                id: data[0].id,
                type: data[0].media_type,
                fileName: data[0].title,
                url: data[0].url
              };
              const e = {
                target: {
                  id,
                  value: newMedia
                }
              };
              onSaveHandler(e);
            }
          },
          onError: function (data) {
            console.error(data);

            setError(data.message);
          }
        }
      );
    } else {
      setError(__('You cannot drop files here.', 'wonder-cart'));
    }
  };

  return (
        <MediaWrap className={`yith-sales-media ${showTabs === 'yes' ? 'yith-sales-media--has-tabs' : ''}`}>
            {label && <label htmlFor={id}>{label}</label>}
            {showTabs === 'yes' && (
                <HStack className='yith-sales-media__tabs'>
                    <HStack
                           className={`yith-sales-media-tab ${currentTab === 'upload' ? 'yith-sales-media-tab--active' : ''}`}
                           spacing={1} onClick={() => setCurrentTab('upload')}>
                        <Icon icon='ArrowDownTrayIcon' size='20'/>
                        <Typography>{__('Upload file', 'wonder-cart')}</Typography>
                    </HStack>
                    <HStack direction='row'
                           className={`yith-sales-media-tab ${currentTab === 'url' ? 'yith-sales-media-tab--active' : ''}`}
                           spacing={1} onClick={() => setCurrentTab('url')}>
                        <Icon icon='LinkIcon' size='20'/>
                        <Typography>{__('Enter url', 'wonder-cart')}</Typography>
                    </HStack>

                </HStack>
            )}
            <Box className='yith-sales-media__content'>
                {
                    currentTab === 'upload'
                      ? (
                            <Box className='yith-sales-media__preview__dropzone' onDragEnter={handleDrag}
                                 onDragOver={handleDrag} onDragLeave={handleDrag} onDrop={handleDrop}>
                                <VStack
                                    className={`yith-sales-media__preview ${dragActive ? 'yith-sales-media--is-dragging' : ''}`}
                                    align='center' justify='center' sx={{ padding: size === 'sm' ? '30px 12px 10px 12px' : '30px' }}>
                                    {!value?.url || isEmpty(value.url)
                                      ? (
                                            <Box className='yith-sales-media__preview__upload'>
                                                <Typography>{_x('Drag or ', '[PART OF] : Drag or Upload a file', 'wonder-cart')}
                                                    <MediaGallery
                                                        label={_x('Upload file', '[PART OF] : Drag or Upload a file', 'wonder-cart')}
                                                        mediaHandlerComponentType='link' onSelect={handleSelectMedia}
                                                        allowedMediaTypes={allowedTypes}

                                                    />
                                                </Typography>
                                            </Box>
                                        )
                                      : (
                                            <>
                                                {isImage(value.url)
                                                  ? (
                                                      <VStack className='yith-sales-media__preview_container' sx={{ width: '100%', height: height + 'px', backgroundColor: size === 'sm' ? '#fff' : 'inherit' }} align='center' justify='center'>
                                                        <Box className='yith-sales-media__preview_image'
                                                             sx={{
                                                               backgroundImage: 'url(' + value.url + ')',
                                                               backgroundRepeat: 'no-repeat',
                                                               backgroundPosition: 'center',
                                                               backgroundSize: size === 'sm' ? 'contain' : 'cover',
                                                               width: '100%',
                                                               height: size === 'sm' ? '80%' : '100%'
                                                             }}>

                                                        </Box>
                                                      </VStack>

                                                    )
                                                  : (
                                                        <HStack spacing={1}
                                                               className='yith-sales-media__preview__file'
                                                               align='center' justify='center'>
                                                            <Icon icon='DocumentIcon' size={24}/>
                                                            <Typography component='span'>{value.fileName}</Typography>
                                                        </HStack>
                                                    )
                                                }
                                                <HStack spacing={1} align='center'
                                                       className='yith-sales-media__actions'>
                                                    <MediaGallery mediaHandlerComponentType='button'
                                                                  allowedMediaTypes={allowedTypes}
                                                                  onSelect={handleSelectMedia} size={15}
                                                                  label={__('Edit', 'wonder-cart')}
                                                                  buttonClassName='yith-sales-media__edit'
                                                                  icon='PencilIcon'/>
                                                    <IconButton className='yith-sales-media__delete'
                                                                onClick={handleRemoveMedia}
                                                                title={__('Delete', 'wonder-cart')}>
                                                        <Icon icon='TrashIcon' size={15}/>
                                                    </IconButton>
                                                </HStack>
                                            </>
                                        )
                                    }

                                </VStack>
                            </Box>
                        )
                      : (
                            <div>
                                <Input type='text' fullWidth={true} value={value?.url ? value.url : ''}
                                       onChange={handleChangeMediaUrl}/>
                            </div>
                        )
                }
            </Box>
            {error !== '' &&
                <Box sx={{ margin: '0 24px 24px' }}>
                    <Notice severity='error' message={error} onEnd={() => setError('')}/>
                </Box>
            }
        </MediaWrap>
  );
};

export default Media;
