import React from 'react';

import { GridItem } from '@lapilli-ui/components';
import Fade from '@mui/material/Fade';

import { CAMPAIGNS_PER_PAGE } from '../../config';
import CampaignItem from '../campaign-item';

const CampaignItems = ({ campaigns, currentPage, scrollToRef, duplicateItem, handleUpdateCampaign, handleDeleteCampaign, viewMode, notices }) => {
  const getCampaignsGrouped = () => {
    const campaignsGrouped = [];
    for (let i = 0; i < currentPage; i++) {
      campaignsGrouped.push(campaigns.slice(i * CAMPAIGNS_PER_PAGE, (1 + i) * CAMPAIGNS_PER_PAGE));
    }
    return campaignsGrouped;
  };

  const groupCampaigns = getCampaignsGrouped();
  const viewProps = viewMode === 'grid' ? { ...{ xs: 12, sm: 6, md: 4, lg: 4 } } : {};
  return (
    groupCampaigns.length > 0 && groupCampaigns.map((group, indexG) => {
      return (
        group.map((item, index) => {
          const isLast = (groupCampaigns.length === indexG + 2 && group.length === index + 1);
          const marked = notices.filter(notice => notice.campaign === item.id);
          return (
                      <Fade timeout={1000} in={true} mountOnEnter unmountOnExit key={`fade-${item.id}`}>
                        <GridItem ref={isLast ? scrollToRef : false} {...viewProps}>
                          <CampaignItem campaign={item} viewMode={viewMode} onDuplicate={duplicateItem} updateCampaign={handleUpdateCampaign} deleteCampaign={handleDeleteCampaign} marked={marked.length ? marked[0] : false}/>
                        </GridItem>
                      </Fade>);
        })
      );
    })
  );
};

export default CampaignItems;
