import React, { useState } from 'react';

import { BlockEditor } from '@lapilli-ui/block-editor';
import { Button, Modal, ModalActions, ModalContent, ModalTitle, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { parse, serialize } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';

import { yithParams } from '../../../config';
import ActionButton from '../../action-button';
import BlockEditorPreview from '../../block-editor-preview';

const CompactBlockEditorWrapper = styled('div')(() => ({}));
const CompactBlockEditorPreview = styled('div')(() => ({
  position: 'relative',
  border: '1px solid #cbd5e1',
  borderRadius: '7px',
  cursor: 'pointer'
}));

const CompactBlockEditorPreviewContent = styled('div')(() => ({
  '.editor-styles-wrapper': {
    maxHeight: 500,
    minHeight: 52,
    padding: '8px',
    overflowY: 'auto',
    borderRadius: '7px'
  }
}));

const CompactBlockEditorPreviewEdit = styled('div')(() => ({
  position: 'absolute',
  top: 8,
  right: 8,
  zIndex: 999
}));

const CompactBlockEditorModalWrapper = styled(Modal)(({ ownerState }) => ({
  '.yithUI-Modal__paper': {
    overflowX: 'visible'
  },
  '.editor-styles-wrapper': {
    backgroundColor: 'transparent',
    color: 'inherit',
    fontSize: '1em',
    ...ownerState.editorStyles
  },
  '.block-list-appender': {
    display: 'none'
  }
}));

function Preview ({ blocks, onEdit, editorStyles, ...others }) {
  return <CompactBlockEditorPreview onClick={onEdit} {...others}>
		<CompactBlockEditorPreviewEdit>
			<ActionButton title={__('Edit', 'wonder-cart')} type="PencilIcon"/>
		</CompactBlockEditorPreviewEdit>
		<CompactBlockEditorPreviewContent>
			<BlockEditorPreview blocks={blocks} editorStyles={editorStyles}/>
		</CompactBlockEditorPreviewContent>
	</CompactBlockEditorPreview>;
}

const CompactBlockEditor = function (
  { id, value, onSaveHandler, label, description, editorStyles = {} }
) {
  const [blocks, setBlocks] = useState(parse(value));
  const [isEditing, setIsEditing] = useState(false);

  const ownerState = { editorStyles };

  const handleSave = () => {
    onSaveHandler({ target: { id, value: serialize(blocks) } });
    setIsEditing(false);
  };

  return (
		<CompactBlockEditorWrapper id={id} className="yith-sales-compact-block-editor">
			<label htmlFor={id}>{label}</label>
			<Preview onEdit={() => setIsEditing(true)} blocks={blocks} editorStyles={editorStyles}/>
			<CompactBlockEditorModalWrapper ownerState={ownerState} onClose={() => setIsEditing(false)} open={isEditing} maxWidth="lg" fullWidth>
				<ModalTitle>{label}</ModalTitle>
				<ModalContent>
					<BlockEditor
						blocks={blocks}
						onChange={setBlocks}
						settings={yithParams.blockEditorSettings}
					/>
				</ModalContent>
				<ModalActions>
					<Button onClick={handleSave}>Save</Button>
				</ModalActions>
			</CompactBlockEditorModalWrapper>
			{description && <Typography className="field-description">{description}</Typography>}
		</CompactBlockEditorWrapper>
  );
};

export default CompactBlockEditor;
