import React, { useCallback, useRef, useState } from 'react';

import { Box, Card, CardContent, CardMedia, HStack, Stack, Switch, Typography } from '@lapilli-ui/components';
import { __, sprintf } from '@wordpress/i18n';
import { useDispatch, useSelector } from 'react-redux';

import CampaignItemActions from './campaign-item-actions';
import { useSaveCampaignMutation } from '../../../common/api/campaign';
import { Divider } from '../../../common/fields';
import { formatTimestamp, nowScheduleDateFrom } from '../../../common/utils/date';
import { setCampaignId, setCampaignObject, setCampaignType } from '../../slices/editor/campaign-slice';
import { setEndpoint, setStatistics } from '../../slices/view/view-campaigns-list';
import { theme } from '../../theme';
import {
  getStaticPreviewImageByType,
  getStatusCampaignLabel,
  getStatusToShow,
  getTypeCampaignLabel
} from '../../utils/admin-functions';
import ConfirmDialog from '../confirm-dialog/confirmDialog';
import StatusBadge from '../status-badge';
import { CampaignListItemStyled, CampaignMediaWrapperStyled } from '../styles/CampaignCard.styled';

const CampaignItemComponent = ({ campaign, viewMode, onDuplicate, deleteCampaign, updateCampaign, marked }) => {
  const [saveCampaign] = useSaveCampaignMutation();
  const [modalVisible, setModalVisible] = useState(false);
  const [isItemVisible, setItemVisible] = useState(true);

  const statusToShow = getStatusToShow(campaign);
  const ref = useRef();
  const { statistics } = useSelector((state) => state.campaignsView);
  const dispatch = useDispatch();
  const handleEditClick = (e) => {
    e.preventDefault();
    dispatch(setCampaignObject({ ...campaign }));
    dispatch(setCampaignId(campaign.id));
    dispatch(setCampaignType(campaign.type));
    dispatch(setEndpoint('edit'));
  };

  const handleDuplicateClick = async (e) => {
    e.preventDefault();
    onDuplicate(campaign.id);
  };

  const handleTrashClick = (e) => {
    e.preventDefault();
    setModalVisible(true);
  };

  const onConfirmClick = () => {
    setModalVisible(false);
    setItemVisible(false);
    const oldStatus = campaign.campaign_status;
    const campaignType = campaign.type;

    const keys = Object.keys(statistics);
    const newStatistic = {};

    keys.forEach(index => {
      const newStat = { ...statistics[index] };
      if (index === campaignType) {
        newStat[oldStatus] -= 1;
        newStat.total -= 1;
      }

      newStatistic[index] = newStat;
    });

    dispatch(setStatistics(newStatistic));
    deleteCampaign(campaign.id);
  };

  const onSwitch = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    if (ref.current.id == campaign.id) {
      const newStatus = e.target.checked ? 'active' : 'inactive';
      const scheduleDateFrom = newStatus === 'active' ? nowScheduleDateFrom() : campaign.schedule_date_from;
      const oldStatus = campaign.campaign_status;
      const campaignType = campaign.type;

      const keys = Object.keys(statistics);
      const newStatistic = {};

      keys.forEach(index => {
        const newStat = { ...statistics[index] };
        if (index === campaignType) {
          newStat[oldStatus] -= 1;
          newStat[newStatus] += 1;
        }

        newStatistic[index] = newStat;
      });

      await saveCampaign(
        {
          body: {
            campaign_status: newStatus,
            schedule_date_from: scheduleDateFrom
          },
          id: campaign.id,
          cache: true
        }).then((result) => {
        updateCampaign({ ...campaign, campaign_status: newStatus, schedule_date_from: scheduleDateFrom });
        dispatch(setStatistics(newStatistic));
      });
    }
  };

  const startOn = campaign.schedule_date_from > 0
    ? sprintf(__('Scheduled (%s)', 'wonder-cart'), formatTimestamp(campaign.schedule_date_from))
    : __('No start date',
      'wonder-cart');

  const getStatus = () => {
    switch (statusToShow) {
      case 'ended' :
      case 'inactive' :
        return <span style={{
          marginLeft: '5px',
          color: theme.palette.grey[300]
        }}>{getStatusCampaignLabel(statusToShow)}</span>;
      default :
        if (campaign.schedule_date_from < (new Date()).getTime() / 1000) {
          return <span style={{
            marginLeft: '5px',
            color: theme.palette.success.main
          }}>{getStatusCampaignLabel(statusToShow)}</span>;
        } else {
          return <span style={{ marginLeft: '5px', color: theme.palette.primary.orange }}>{startOn}</span>;
        }
    }
  };
  const typoStyle = { fontSize: theme.typography.body3.fontSize, lineHeight: theme.typography.body3.lineHeight };
  const campaignDetails = () => {
    return (
        <>

          <Typography sx={typoStyle}>
            <span>{__('Type:', 'wonder-cart')}</span> <span style={{ marginLeft: '5px' }}>{getTypeCampaignLabel(campaign.type)}</span>
          </Typography>

          <Typography sx={typoStyle}>
            <span>{__('Status:', 'wonder-cart')}</span>
            {getStatus()}
          </Typography>

          <Typography sx={typoStyle}>
            <span>{__('End on:', 'wonder-cart')}</span>
            {
              campaign.schedule_date_to <= 0
                ? <span style={{
                  marginLeft: '5px',
                  color: theme.palette.grey[300],
                  fontWeight: 500
                }}>{__('No end date', 'wonder-cart')}</span>
                : <span style={{ marginLeft: '5px' }}>{formatTimestamp(campaign.schedule_date_to)}</span>
            }
          </Typography>
        </>
    );
  };

  const confirmDialog = useCallback(() => {
    return <ConfirmDialog
            open={true}
            onCancel={() => setModalVisible(false)}
            onConfirm={onConfirmClick}
            onClose={() => setModalVisible(false)}
            title={__('Confirm delete', 'wonder-cart')}
            confirmButtonType="delete"
            confirmButton={__('Yes, delete', 'wonder-cart')}
            isDeleteButton={true}>
          {__('Are you sure you want to delete this campaign?\n' +
              '\n' +
              'This action cannot be undone and you will not be able to recover this data.', 'wonder-cart')}
        </ConfirmDialog>;
  },
  []
  );

  if (!isItemVisible) return null;

  if (viewMode === 'list') {
    return (
        <CampaignListItemStyled id={campaign.id} ref={ref}
                                className={`campaign-list-item ${marked ? 'show-transition ' + marked.severity : 'no-transition'}`}>
          <Box className="yith-card-bg-image"><img src={getStaticPreviewImageByType(campaign.type)}
                                                   alt={campaign.name}/></Box>
          <Stack sx={{ flex: 1 }}>
            <Stack align="top" sx={{ flex: 1 }} direction="row">
              <Typography className="yith-card-title" variant={'body2'}
                          sx={{ fontWeight: '700' }}>{campaign.name}</Typography>
              <CampaignItemActions sx={{ position: 'relative', top: 'auto', left: 'auto' }}
                                   onEditClick={handleEditClick} onDuplicateClick={handleDuplicateClick}
                                   onTrashClick={handleTrashClick}/>
              <StatusBadge sx={{ margin: '0 10px' }} label={statusToShow}/>
              <Switch onChange={onSwitch}
                      checked={statusToShow !== 'ended' && campaign.campaign_status === 'active'}
                      disabled={statusToShow === 'ended'}/>
            </Stack>
            <Stack className="yith-campaign-details" direction="row" spacing={1}>{campaignDetails()}</Stack>
          </Stack>
          {modalVisible && confirmDialog()}
        </CampaignListItemStyled>
    );
  }

  const opacity = statusToShow === 'ended' ? 0.6 : 1;
  const animation = marked ? `${theme.palette.animations[marked.severity]} 3s linear` : 'none';

  return (
      <Card id={campaign.id} ref={ref} className="yith-campaign-item-wrapper" variant="outlined"
            borderColor={'border.secondary'} sx={{ height: '100%', borderRadius: '8px', animation }}>
        <Stack sx={{ height: 'inherit', flex: '1' }}>
          <CampaignMediaWrapperStyled>
            <CardMedia component="img"
                       height="170"
                       src={getStaticPreviewImageByType(campaign.type)}
                       alt={campaign.name}
                       sx={{ backgroundColor: theme.palette.campaign.backgroundMedia, padding: '20px', opacity, objectFit: 'contain' }}/>
            <CampaignItemActions onEditClick={handleEditClick} onDuplicateClick={handleDuplicateClick}
                                 onTrashClick={handleTrashClick}/>
            <StatusBadge sx={{ position: 'absolute', right: '5px', bottom: '5px', zIndex: '100' }}
                         label={statusToShow}/>
          </CampaignMediaWrapperStyled>

          <CardContent sx={{ display: 'flex', flexDirection: 'column', gridAutoRows: '1fr', flex: 1, opacity }}>
            <Stack sx={{ flex: 1 }}>
              <Typography pb="10px" sx={{ minHeight: '29px', fontWeight: '700', fontSize: '14px' }}>
                {campaign.name}
              </Typography>
              {campaignDetails()}
            </Stack>
            <Divider sx={{ margin: '10px 0' }} component='div'/>
            <HStack mt="10px" justify="space-between">
              <Box sx={{ flex: 1 }}>
                <Typography sx={{ fontSize: theme.typography.body3.fontSize }}>
                  {__('Enable campaign', 'wonder-cart')}
                </Typography>
              </Box>
              <Box>
                <Switch onChange={onSwitch}
                        checked={statusToShow !== 'ended' && campaign.campaign_status === 'active'}
                        disabled={statusToShow === 'ended'}/>
              </Box>
            </HStack>
            {modalVisible && confirmDialog()}
          </CardContent>
        </Stack>
      </Card>
  );
};

const CampaignItem = React.memo(CampaignItemComponent, (prevProps, nextProps) => {
  const titleIsEqual = prevProps.campaign.name === nextProps.campaign.name;
  const statusIsEqual = prevProps.campaign.campaign_status === nextProps.campaign.campaign_status;
  const dateFromIsEqual = prevProps.campaign.schedule_date_from === nextProps.campaign.schedule_date_from;
  const dateToIsEqual = prevProps.campaign.schedule_date_to === nextProps.campaign.schedule_date_to;
  return titleIsEqual && statusIsEqual && dateFromIsEqual && dateToIsEqual;
});
export default CampaignItem;
