import React from 'react';

import { __ } from '@wordpress/i18n';

import { VerticalScrollableContentStyled } from '../../../../blocks/elements/styles/VerticalScrollableContent.styled';
import { wcSettings } from '../../../config';
import { PreviewItemName, PreviewItemsBoxContainer, PreviewItemsBoxWrapper } from '../../styles/PreviewItemsBox.styled';

const RuleList = ({ items, className }) => {
  if (items.length === 0) {
    return '';
  }

  const getFormattedName = (item) => {
    const unit = (item.unit === 0 || item.unit > 1) ? __('units', 'wonder-cart') : __('unit', 'wonder-cart');
    const discountType = item.discountType === 'percentage' ? '%' : wcSettings.currency.symbol;
    return `${item.unit}  ${unit} = ${item.discount}${discountType}  ${__('discount', 'wonder-cart')}`;
  };

  return (
      <PreviewItemsBoxContainer className={className}>
        <PreviewItemsBoxWrapper>
          <VerticalScrollableContentStyled maxHeight="98px">
            <ul>
              {items.map((item) => (
                  <li key={item.id}>
                    <PreviewItemName>
                      {getFormattedName(item)}
                    </PreviewItemName>
                  </li>
              ))}
            </ul>
          </VerticalScrollableContentStyled>
        </PreviewItemsBoxWrapper>
      </PreviewItemsBoxContainer>
  );
};

export default RuleList;
