import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  campaigns: [],
  endpoint: 'campaigns',
  type: 'all',
  status: '',
  totalPages: 0,
  totalCount: 0,
  currentPage: 1,
  loadedPages: 0,
  statistics: {}
};

const campaignsViewSlice = createSlice({
  name: 'campaignsView',
  initialState,
  reducers: {
    setCampaigns: (state, action) => {
      state.campaigns = action.payload;
    },
    setCampaign: (state, action) => {
      const campaignToUpdate = action.payload;

      const campaigns = state.campaigns.map(campaign => {
        let newCampaign = [];
        if (campaign.id === campaignToUpdate.id) {
          newCampaign = campaignToUpdate;
        } else {
          newCampaign = campaign;
        }
        return newCampaign;
      });
      state.campaigns = campaigns;
    },
    setStatus: (state, action) => {
      state.status = action.payload;
    },
    setType: (state, action) => {
      state.type = action.payload === null ? 'all' : action.payload;
    },
    setEndpoint: (state, action) => {
      state.endpoint = action.payload;
    },
    resetCampaignView: (state, action) => {
      state.campaigns = [];
      state.totalPages = 0;
      state.totalCount = 0;
      state.currentPage = 1;
      state.loadedPages = 0;
      state.endpoint = 'campaigns';
      state.type = 'all';
      state.status = '';
    },
    setCampaignsView: (state, action) => {
      const keysToUpdate = Object.keys(action.payload);

      keysToUpdate.forEach(key => {
        if (state[key] !== action.payload[key]) {
          if (key === 'type') {
            state.type = action.payload[key] === null ? 'all' : action.payload[key];
          } else {
            state[key] = action.payload[key];
          }
        }
      });
    },
    setStatistics: (state, action) => {
      state.statistics = action.payload;
    },
    setCampaignStatistics: (state, action) => {
      const keys = Object.keys(state.statistics);
      const { type, status } = action.payload;
      const newStatistic = {};
      keys.forEach(index => {
        const newStat = { ...state.statistics[index] };
        if (index === type) {
          newStat[status] += 1;
          newStat.total += 1;
        }

        newStatistic[index] = newStat;
      });
      state.statistics = newStatistic;
    }
  }
});

export const {
  setCampaigns,
  setStatus,
  setType,
  setEndpoint,
  setCampaignsView,
  resetCampaignView,
  setCampaign,
  setStatistics,
  setCampaignStatistics
} = campaignsViewSlice.actions;
export default campaignsViewSlice.reducer;
