import { alpha, createTheme, defaultTheme, keyframes } from '@lapilli-ui/styles';
import _ from 'lodash';

import { yithParams } from '../config';

const uiLibrary = yithParams.uiLibraryColors;
export const mainTheme = {
  fields: {
    focusedBorderColor: '#196BDE'
  },
  palette: {
    primary: {
      main: '#1A4884',
      light: '#196BDE',
      contrastText: '#FFFFFF',
      orange: '#D07900'
    },
    secondary: {
      main: '#196BDE',
      light: '#CCDCF4',
      contrastText: '#000'
    },
    badge: {
      background: '#000000',
      color: '#FFFFFF'
    },
    campaign: {
      backgroundMedia: '#F3F3F3',
      new: {
        background: '#F4F8FD',
        backgroundHover: '#e2eef9',
        border: '1px dashed #CCDCF4'
      }
    },
    border: {
      main: '#DDDDDD',
      light: '#EDEDED',
      tertiary: '#e5e7eb'
    },
    success: {
      main: '#348528',
      light: '#EDEDED'
    },
    tabs: {
      active: '#196bde',
      hover: '#cbd5e1',
      borderColor: '#e2e8f0'
    },
    tooltip: {
      color: '#fff',
      backgroundColor: '#333',
      margin: '30px',
      borderWidth: '6px',
      boxShadow: '0px 2px 16px 5px rgba(199, 215, 225, 0.5)',
      boxShadowActive: '10px 2px 16px 5px rgba(187, 219, 239, 0.5)'
    },
    horizontalStepper: {
      default: {
        borderBottom: '4px solid',
        borderColor: '#fff',
        backgroundColor: '#fff'
      },
      hover: {
        borderColor: '#cbd5e1'
      },
      current: {
        borderColor: '#1976d2'
      }
    },
    animations: {
      success: keyframes({
        '0%': { backgroundColor: '#348528', borderColor: 'rgba(187, 222, 195,1)' },
        '100%': { backgroundColor: '#fff', borderColor: 'rgba(0, 0, 0, 0.12)' }
      })
    },
    text: {
      primary: '#0f172a',
      secondary: 'rgb(74, 85, 103)',
      label: 'rgb(30,41,59)'
    }
  },
  typography: {
    body: {
      fontSize: '0.95rem'
    },
    body3: {
      fontSize: '0.75rem',
      lineHeight: '1.8'
    }
  },
  wonderCart: {
    down: (key) => {
      return `@media (max-width:${defaultTheme.breakpoints.values[key]}${defaultTheme.breakpoints.unit})`;
    }
  }

};
const components = {
  Typography: {
    styles: {
      Root: ({ ownerState, theme }) => {
        return {
          '&.yith-sales-field-error': {
            color: theme.palette.error.main,
            fontSize: theme.typography.body3.fontSize,
            padding: '10px 0'
          }
        };
      }
    }
  },
  Button: {
    styles: {
      Root: ({ ownerState, theme }) => {
        const color = ownerState?.color ?? 'primary';
        return {
          fontWeight: '500!important',

          ...(ownerState?.variant === 'outlined' && {
            color: '#000',
            '&:hover': {
              backgroundColor: alpha(theme.palette[color].light, 0.1) + '!important'
            }
          }),
          ...(ownerState?.variant === 'contained' && {
            color: `${theme.palette[color].contrastText}!important`,
            border: `1px solid ${theme.palette[color].light}`,
            backgroundColor: `${theme.palette[color].light}!important`,
            '&:hover': {
              backgroundColor: `${theme.palette[color].main}!important`
            }
          })
        };
      }
    }
  },
  Switch: {
    styles: {
      Root: ({ ownerState, theme }) => {
        return {
          ...(ownerState?.color === 'primary' && ownerState.checked && {
            color: theme.palette.primary.light
          })
        };
      },
      Track: ({ ownerState, theme }) => {
        return {
          boxShadow: `0 0 0 0 ${theme.palette.primary.light}`,
          ...(ownerState?.color === 'primary' && {
            ...(ownerState.checked && {
              background: theme.palette.primary.light
            }),
            ...(ownerState.isFocused && {
              boxShadow: `0 0 0px 2px ${theme.palette.background.default}, 0 0 0px 4px ${theme.palette[ownerState.color].light}`
            })

          })
        };
      }
    }
  }
};
mainTheme.components = { ...components };
export const theme = createTheme(_.merge(mainTheme, uiLibrary));
export const getGlobalTheme = (settings) => {
  const sets = settings['general-settings'];

  const global = {
    forms: {
      backgroundColor: sets.form.yith_sales_form_colors.background_color,
      borderColor: sets.form.yith_sales_form_colors.border_color,
      text: sets.form.yith_sales_form_colors.text_color,
      checkColor: sets.form.yith_sales_form_colors.check_color,
      borderRadius: sets.form.yith_sales_form_border.border_radius
    },
    badge: {
      backgroundColor: sets.badges.yith_sales_badge_colors.background_color,
      text: sets.badges.yith_sales_badge_colors.text_color
    },
    modal: {
      overlayColor: sets.modal_window.yith_sales_modal_colors.overlay_color,
      backgroundColor: sets.modal_window.yith_sales_modal_colors.background_color,
      closeIcon: sets.modal_window.yith_sales_modal_colors.close_icon_color,
      borderRadius: sets.modal_window.yith_sales_modal_border.border_radius
    },
    extra: {
      pricingColor: sets.extra.yith_sales_price_colors.regular_color,
      saleColor: sets.extra.yith_sales_price_colors.sale_color,
      savingColor: sets.extra.yith_sales_price_colors.saving_color,
      freeColor: sets.extra.yith_sales_price_colors.free_color
    },
    gift: {
      giftIcon: sets.gift_product.yith_sales_gift_settings.gift_icon.url,
      giftBg: sets.gift_product.yith_sales_gift_settings.gift_bg.url
    },
    freeShipping: {
      freeShippingIcon: sets.free_shipping.yith_sales_free_shipping_icon.url
    }
  };
  return global;
};
