import React from 'react';

import { Button } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import Icon from '../../../common/icon';

const BottomNavStyled = styled('div')(({ theme }) => ({
  '&': {
    display: 'flex',
    justifyContent: 'space-between',
    backgroundColor: '#f9f9f9',
    padding: '25px 40px 25px',
    position: 'sticky',
    bottom: 0,
    zIndex: '10'
  },
  'button[disabled]': {
    opacity: '0.5',
    pointerEvents: 'none'
  },
  'button.last-step': {
    background: theme.palette.primary.main
  }
}));
const BottomNav = ({ onClickNext, onClickBack, onClickFinish, onClickSave, buttonView, nextDisabled = false }) => {
  return (
      <BottomNavStyled>
        {buttonView.prev !== false && <Button className="button-back" size="sm" variant="text" onClick={onClickBack} startIcon={<Icon icon="ArrowLongLeftIcon" />}>{__('Back', 'wonder-cart')}</Button>}
        {buttonView.next !== false && <Button size="lg" onClick={onClickNext} disabled={nextDisabled}>{__('Next', 'wonder-cart')}</Button>}
        {buttonView.next === false && buttonView.finish && <Button size="lg" color="info" onClick={onClickFinish} className='last-step'>{__('Finish', 'wonder-cart')}</Button>}
        {buttonView.next === false && !buttonView.finish && <Button size="lg" color="info" onClick={onClickSave} className='last-step'>{__('Save Changes', 'wonder-cart')}</Button>}
      </BottomNavStyled>
  );
};
export default BottomNav;
