import React, { useState } from 'react';

import { styled } from '@lapilli-ui/styles';
import { Accordion, AccordionDetails, AccordionSummary } from '@mui/material';

import Icon from '../../../common/icon';
import { isEmpty } from '../../../common/utils/common';
import Field from '../field';

const ToggleWrapper = styled('div')(({ theme }) => ({

  '& .MuiAccordion-root': {
    boxShadow: 'unset'

  },
  '& .MuiAccordionSummary-content': {
    fontSize: '12px',
    fontWeight: 500,
    textTransform: 'uppercase'
  },
  '& .MuiAccordionSummary-root': {
    padding: '0 21px 0 0'
  },
  '& .MuiAccordionSummary-root.Mui-expanded': {
    minHeight: 'unset',
    marginBottom: '23px'
  },
  '& .MuiAccordionSummary-content.Mui-expanded': {
    margin: '9px 0'
  },
  '& .MuiAccordionSummary-content:after': {
    display: 'block',
    width: '306px',
    position: 'absolute',
    bottom: 0,
    content: '""',
    borderBottom: '1px solid rgba(151, 151, 151, 0.25)'
  },
  '& .MuiAccordionDetails-root': {
    padding: 0
  }
}));
const ToggleContainer = ({ id, label, isOpened = true, toggleData, value, onSaveHandler, showError = false }) => {
  const [expanded, setExpanded] = useState(isOpened ? id : false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  if (isEmpty(toggleData)) {
    return '';
  }

  return (
        <ToggleWrapper className='yith-sales-toggle-field'>
            <Accordion expanded={expanded === id} onChange={handleChange(id)} square>
                <AccordionSummary aria-controls={id} id={id} expandIcon={expanded ? <Icon icon="MinusSmallIcon" size="20" sx={{ color: 'rgba(46, 58, 89, 1)' }}/> : <Icon icon="PlusSmallIcon" size="20" sx={{ color: 'rgba(46, 58, 89, 1)' }}/> } >
                      {label}
                </AccordionSummary>
                <AccordionDetails>
                    {
                        toggleData.map(field => {
                          if (value[field.id] !== 'undefined') {
                            field.value = value[field.id];
                          } else {
                            field.value = field.default;
                          }

                          if (!field?.style) {
                            field.style = {};
                          }

                          return (
                                    <div className="yith-sales-toggle_sub-field" key={`toggle-${field.id}`}>
                                        {<Field field={field} showError ={showError} onSave={onSaveHandler} {...field.style}/>}
                                    </div>
                          );
                        }
                        )
                    }
                </AccordionDetails>
            </Accordion>
        </ToggleWrapper>
  );
};

export default ToggleContainer;
