import React from 'react';

import { Box, Input, Spinner, Typography } from '@lapilli-ui/components';
import { parse } from '@wordpress/blocks';

import AddToCartButton from '../../../../../blocks/elements/add-to-cart-button';
import ProductImage from '../../../../../blocks/elements/product-image';
import { Divider } from '../../../../../common/fields';
import { getImage } from '../../../../../common/utils/common';
import { getRandomProductToShow } from '../../../../../common/utils/data';
import BlockEditorPreview from '../../../block-editor-preview';
const ThreeForTwo = ({ campaign, step }) => {
  const options = campaign.meta[campaign.type];
  const {
    title,
    default_qty: setDefaultQty,
    show_badge: showBadge,
    badge_text: badgeText,
    badge_background_color: badgeBackgroundColor,
    badge_text_color: badgeColor,
    trigger_product: triggerProduct
  } = options;

  const { data, isLoading, isFetching } = getRandomProductToShow(triggerProduct);
  const inFetching = isLoading || isFetching;
  const productImage = getImage(data, true);
  const showContent = campaign.status === 'publish' || step !== 'trigger';
  return (
        <Box sx={{ display: 'flex', columnGap: '50px', backgroundColor: '#fff', padding: '60px' }} className="yith-sales-three-for-two-preview-wrapper">
            {inFetching && (<Spinner sx={{ marginLeft: '50%', marginTop: '20px', marginBottom: '20px' }}/>)}
            {!inFetching && (
                <>
                  <ProductImage src={productImage.src} alt={productImage.alt} width='280px' badge={showContent && showBadge === 'yes' ? <Typography sx={{ fontSize: '15px' }}>{badgeText}</Typography> : false}
                                  badgeBackgroundColor={badgeBackgroundColor} badgeColor={badgeColor}/>
                    <Box sx={{ flex: 1, marginTop: '20px' }}>
                      <Divider sx={{ width: '386px', marginBottom: '18px', borderBottomWidth: 'thick' }}/>
                      <Divider sx={{ width: '386px', marginBottom: '18px', borderBottomWidth: 'thick' }}/>
                      <Divider sx={{ width: '386px', borderBottomWidth: 'thick' }}/>
                      <Box sx={{ marginTop: '40px' }}>
                          { showContent && (
                              <>
                                <BlockEditorPreview blocks={parse(title)}/>
                            <Input type="number" value={setDefaultQty === 'yes' ? 3 : 1} size='md' isMini={true}/>
                                </>
                          )
                          }
                          {
                            !showContent && (
                                <>
                                  <Divider sx={{ width: '386px', marginBottom: '18px', borderBottomWidth: 'thick' }}/>
                                  <Divider sx={{ width: '386px', borderBottomWidth: 'thick' }}/>
                                </>
                            )
                          }
                        <Box sx={{ marginTop: '30px' }}>
                          <AddToCartButton/>
                        </Box>
                        </Box>
                    </Box>
                </>
            )
            }
        </Box>
  );
};

export default ThreeForTwo;
