import React from 'react';

import { IconButton } from '@lapilli-ui/components';

import Icon from '../../../common/icon';
import Tooltip from '../tooltip/tooltip';
const ActionButton = ({ title, type, color = '#000', onCustomClick }) => {
  return (
        <Tooltip content={title} direction="bottom">
          <IconButton onClick={onCustomClick} size='sm' variant="shadowed" style={{ backgroundColor: '#fff', color: `${color}` }} sx={{
            '& div': { color: `${color}` }
          }}>
            <Icon icon={type} size={16}/>
          </IconButton>
        </Tooltip>

  );
};
export default ActionButton;
