import React, { useEffect, useState } from 'react';

import { Box } from '@lapilli-ui/components';
import { createTheme, ThemeProvider } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';
import { Autosave } from 'react-autosave';
import ReactDOM from 'react-dom';
import { useDispatch, useSelector } from 'react-redux';

import BackToDashboard from './back-to-dashboard';
import SimpleModal from '../../../blocks/elements/simple-modal';
import {
  useAddCampaignMutation,
  useDeleteCampaignMutation,
  useGetCampaignQuery,
  useSaveCampaignMutation,
  useRegisterEventMutation
} from '../../../common/api/campaign';
import { isEmpty, isFieldValid } from '../../../common/utils/common';
import { AUTOSAVE_INTERVAL } from '../../config';
import { resetEditor, setCampaignStep, setClickedNext, updateCampaign } from '../../slices/editor/campaign-slice';
import { addNotice, updateNotice } from '../../slices/view/notices-slice';
import { resetCampaignView, setCampaignStatistics, setStatistics } from '../../slices/view/view-campaigns-list';
import { getGlobalTheme, mainTheme } from '../../theme';
import { getNext, getPrev, getToolbarOptions } from '../../toolbars';
import BottomNav from '../bottom-nav';
import FinalStep from '../final-step';
import Preview from '../preview';
import SettingsSidebar from '../settings-sidebar';
import Toolbar from '../toolbar';
import TopNav from '../top-nav';

const CampaignEditor = () => {
  const dispatch = useDispatch();
  const currentCampaign = useSelector((state) => state.campaign);
  const { statistics } = useSelector((state) => state.campaignsView);
  const { campaign } = currentCampaign;
  const isNew = currentCampaign.id === 'new';

  const settings = useSelector((state) => state.panelConfig.saved);
  const globalThemeSettings = getGlobalTheme(settings);
  const globalTheme = createTheme({ ...mainTheme, ...globalThemeSettings });

  const [addCampaign] = useAddCampaignMutation();
  const [saveCampaign] = useSaveCampaignMutation();
  const [deleteCampaign] = useDeleteCampaignMutation();
  const [registerEvent] = useRegisterEventMutation();
  const [isPossibleAutoSave, setIsPossibleAutoSave] = useState(true);
  const [campaignUpdated, setCampaignUpdated] = useState(false);
  const { data, isFetching, isSuccess } = useGetCampaignQuery(
    currentCampaign.id, { skip: (isNew || !isPossibleAutoSave) });

  const currentToolbar = getToolbarOptions(currentCampaign.type);
  const [showModal, setShowModal] = useState(false);
  const showSettingsSidebar = campaign.status !== 'draft' && !isNew;
  const clickedNext = currentCampaign.clickedNext;
  const showError = clickedNext && !isNew;

  const bottomNavBar = {
    next: getNext(currentCampaign.step, currentToolbar),
    prev: getPrev(currentCampaign.step, currentToolbar),
    finish: currentCampaign.campaign.status === 'draft'
  };

  const currentFields = currentToolbar[currentCampaign.step]?.fields;

  const checkError = () => {
    const invalidList = [];
    currentFields.forEach(currentField => {
      if (currentField.type !== 'toggle' && currentField.required && !isFieldValid(currentField, currentFields)) {
        invalidList.push(currentField);
      }
      if (currentField.type === 'toggle') {
        currentField?.fields.forEach(subfield => {
          if (subfield.required && !isFieldValid(subfield, currentFields)) {
            invalidList.push(subfield);
          }
        });
      }
    });

    return invalidList.length > 0;
  };

  const disableButton = () => {
    dispatch(updateCampaign({ updated: true, disableNext: checkError() }));
  };

  useEffect(() => {
    if (!isEmpty(data)) {
      if (isEmpty(campaign) || data.id !== campaign?.id) {
        dispatch(updateCampaign({ id: data.id, campaign: data, type: data.type, updated: false }));
      }
    }
  }, [data]);
  useEffect(() => {
    showError && disableButton();
  }, [showError, campaign]);

  useEffect(() => {
    isNew && createAutoDraft(currentCampaign.type);
  }, []);

  useEffect(() => {
    document.body.classList.add('is-fullscreen-mode');
    dispatch(updateNotice([]));
  }, []);

  useEffect(() => {
    return () => {
      dispatch(resetEditor(true));
      dispatch(resetCampaignView({}));
    };
  }, []);

  useEffect(() => {
    if (currentCampaign.updated) {
      setCampaignUpdated(false);
    }
  }, [currentCampaign]);
  const createAutoDraft = async (type) => {
    const payload = { type };

    await addCampaign(payload).then((result) => {
      if (result.data) {
        dispatch(updateCampaign({
          id: result.data.id,
          campaign: result.data,
          autoSave: true
        }));
      } else {
        console.log(result);
      }
    });
  };
  const handleNextClick = () => {
    if (checkError()) {
      dispatch(setClickedNext(true));
    } else if (bottomNavBar.next) {
      dispatch(setCampaignStep(bottomNavBar.next));
    }
  };

  const goBack = async () => {
    if (!showSettingsSidebar && !isEmpty(campaign)) {
      setIsPossibleAutoSave(false);

      const args = {...campaign, meta:[] };
      // :{ campaignID: campaign.id, campaignType: campaign.type, campaign_status: campaign.campaign_status, status: campaign.status,  }
      registerEvent({ body: { eventName : 'campaign_abandoned', args}})
      await deleteCampaign(campaign);
    }
    dispatch(resetCampaignView({}));
    dispatch(resetEditor(true));
  };

  const handleBackClick = async () => {
    if (bottomNavBar.prev === '') {
      goBack();
    } else {
      dispatch(setCampaignStep(bottomNavBar.prev));
    }
  };

  const handleBackToDashboardClick = () => {
    goBack();
  };

  const handleFinishClick = () => {
    if (checkError()) {
      dispatch(setClickedNext(true));
    } else {
      setShowModal(true);
    }
  };

  const publishCampaign = async (dateFrom, dateTo) => {
    setIsPossibleAutoSave(false);
    setShowModal(false);
    await saveCampaign(
      {
        body: {
          name: campaign.meta[campaign.type].campaign_name,
          status: 'publish',
          meta: campaign.meta,
          campaign_status: 'active',
          schedule_date_from: dateFrom,
          schedule_date_to: dateTo
        },
        id: campaign.id,
        cache: false
      }).then((result) => {
      dispatch(addNotice({ severity: 'success', message: __('Campaign created. Enjoy!', 'wonder-cart'), campaign: campaign.id }));
      dispatch(setCampaignStep('trigger'));
      dispatch(updateCampaign({
        id: result.data.id,
        campaign: result.data,
        autoSave: false,
        updated: false
      }));
      const keys = Object.keys(statistics);
      const newStatistic = {};

      keys.forEach(index => {
        const newStat = { ...statistics[index] };
        if (index === result.data.type) {
          newStat[result.data.campaign_status] += 1;
          newStat.total += 1;
        }

        newStatistic[index] = newStat;
      });

      dispatch(setStatistics(newStatistic));
      dispatch(resetEditor(true));
      dispatch(resetCampaignView({}));
    });
  };

  const handleChangeStep = (step) => {
    if (checkError()) {
      dispatch(setClickedNext(true));
    } else {
      dispatch(setCampaignStep(step));
    }
  };

  const handleAutoSaveCampaign = async () => {
    if (isPossibleAutoSave) {
      await saveCampaign(
        {
          body: {
            name: campaign.meta[campaign.type].campaign_name,
            status: campaign.status,
            meta: campaign.meta
          },
          id: campaign.id,
          cache: true
        }).then((result) => {
      });
    }
  };

  const backToDashboard = () => {
    dispatch(resetEditor(true));
    dispatch(resetCampaignView({}));
  };

  const handleSaveSettings = async () => {
    if (checkError()) {
      dispatch(setClickedNext(true));
    } else {
      await saveCampaign({
        body: {
          ...campaign,
          name: campaign.meta[campaign.type].campaign_name,
          campaign_status: campaign.campaign_status,
          schedule_date_from: campaign.schedule_date_from,
          schedule_date_to: campaign.schedule_date_to > 0 ? campaign.schedule_date_to : 0
        },
        id: campaign.id
      }).then((result) => {
        setCampaignUpdated(true);
        dispatch(updateCampaign({
          campaign: result.data,
          updated: false
        }));
      });
    }
  };

  let contentSidebar;
  let contentMain;
  if (isFetching) {
    contentSidebar = '';
    contentMain = <Preview isLoading={true}/>;
  } else if (isSuccess && !isEmpty(campaign)) {
    contentSidebar = <Toolbar showError={showError}/>;
    contentMain = <Preview campaign={campaign} step={currentCampaign.step} type={currentCampaign.type}/>;
  }

  return ReactDOM.createPortal(
      <Box sx={{ position: 'fixed', width: '100%', height: '100%', top: 0, left: 0, zIndex: 9999 }}>
      <ThemeProvider theme={globalTheme}>
        <Box className="yith-sales-editor-wrapper" sx={{
          display: 'flex',
          backgroundColor: 'rgb(241, 245, 249)',
          height: '100vh',
          '.lapilliUI-Switch__field': {
            position: 'absolute!important'
          }
        }}>
          <Box className="yith-sales-editor--sidebar" sx={{ width: '392px', backgroundColor: '#fff', boxShadow: '0px 2px 22px rgba(126, 190, 228, 0.246568)', flexDirection: 'column', display: 'flex', position: 'relative', justifyContent: 'space-between' }}>
            <BackToDashboard onclick={handleBackToDashboardClick} updated={currentCampaign.updated}/>
            {contentSidebar}
            <BottomNav onClickNext={handleNextClick} onClickBack={handleBackClick} onClickFinish={handleFinishClick} onClickSave={handleSaveSettings} nextDisabled={currentCampaign.disableNext} buttonView={bottomNavBar}/>
          </Box>
          <Box className="yith-sales-editor-preview-wrapper" sx={{ height: '100%', backgroundColor: 'rgb(241, 245, 249)', flexGrow: 1, position: 'relative' }}>
            {!isNew && <TopNav
                onChangeStep={handleChangeStep}
                steps={currentToolbar}
                step={currentCampaign.step}
                disableOtherSteps={currentCampaign.disableNext}
                editing={showSettingsSidebar}
            />}
            <Box sx={{ display: 'flex', margin: showSettingsSidebar ? '80px 0 0 60px' : '80px 60px 0 60px', height: 'calc(100vh - 80px)', backgroundColor: '#fff' }} className="yith-sales-editor-content-wrapper">
              <Box sx={{ flex: '1', position: 'relative' }}>{contentMain}</Box>
              {showSettingsSidebar && <SettingsSidebar currentCampaign={currentCampaign} updated={campaignUpdated} onSave={handleSaveSettings} backToDashboard={backToDashboard}/>}
            </Box>
          </Box>

          <SimpleModal title="" open={showModal} onClose={() => setShowModal(false)} maxWidth="450px">
            <FinalStep onButtonClick={publishCampaign}/>
          </SimpleModal>
          {!showSettingsSidebar && !isEmpty(currentCampaign.campaign) && <Autosave data={currentCampaign.campaign} interval={AUTOSAVE_INTERVAL * 1000} onSave={handleAutoSaveCampaign}/>}
        </Box>
      </ThemeProvider>
        </Box>,
      document.body

  );
};

export default CampaignEditor;
