import React from 'react';

import { parse } from '@wordpress/blocks';

import ThankYouPage from '../../../../../blocks/thank-you-page';
import { getCampaignTitle, isEmpty } from '../../../../../common/utils/common';
import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import BlockEditorPreview from '../../../block-editor-preview';
import PreviewImage from '../../elements/preview-image';
import WizardPreview from '../../elements/wizard-preview';
const ThankYouPagePreview = ({ campaign, type, showPreview, step }) => {
  const campaignTitle = !isEmpty(campaign) ? getCampaignTitle(campaign) : '';
  const campaignTitleComponent = campaignTitle ? <BlockEditorPreview blocks={parse(campaignTitle)}/> : '';
  return (
      <React.Fragment>
        {showPreview && (<PreviewImage src={getStaticPreviewImageByType(type)} width="800px"/>)}
        {!showPreview && <WizardPreview className="boxed" minHeight='700px' sx={{ backgroundColor: 'transparent', height: '550px' }}><ThankYouPage campaign={campaign} campaignTitle={campaignTitleComponent} step={step} preview={true} context='admin'/></WizardPreview>}
      </React.Fragment>
  );
};

export default ThankYouPagePreview;
