import React from 'react';

import { Select, Stack } from '@lapilli-ui/components';

const MultiSelect = ({ id, label, options, value, placeholder, noResultsText, onSaveHandler, ...other }) => {
  const handleOnChange = (values) => {
    const e = {
      target: {
        id,
        value: values
      }
    };
    onSaveHandler(e);
  };
  return (
        <Stack direction="column" className='yith-sales-field-multiselect' align="center">
            {label && <label htmlFor={id}>{label}</label>}
            <Select id={id} options={options} value={value} allowSearch={true} hideSelectedOptions={true}
                    filterSearch={() => true} showTags={true} limitTags={5} multiple={true} onChange={handleOnChange} placeholder={placeholder} noResultsText={noResultsText} {...other}/>
        </Stack>
  );
};

export default MultiSelect;
