import React from 'react';

import { Box, Spinner, Stack, Typography } from '@lapilli-ui/components';
import { parse } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';

import Price from '../../../../../blocks/elements/price';
import ProductImage from '../../../../../blocks/elements/product-image';
import SaveDiscount from '../../../../../blocks/elements/save-discount';
import { Divider } from '../../../../../common/fields';
import { getFormattedTermName, getImage, isEmpty } from '../../../../../common/utils/common';
import { getRandomProductToShow } from '../../../../../common/utils/data';
import { formatCurrency, getDiscountedPrice } from '../../../../../common/utils/numbers';
import BlockEditorPreview from '../../../block-editor-preview';

const CategoryDiscount = ({ campaign, step }) => {
  const options = campaign.meta[campaign.type];
  const isPublic = campaign.status === 'publish';
  const showContent = isPublic || step !== 'trigger';
  let {
    title,
    category_discount: rules,
    show_saving: showSaving,
    trigger_product: triggerProduct,
    show_badge: showBadge,
    badge_text: badgeText,
    badge_background_color: badgeBackgroundColor,
    badge_text_color: badgeColor
  } = options;
  const categoryPreview = !isEmpty(triggerProduct.ids) ? triggerProduct.ids[0] : false;
  const trigger = !isEmpty(triggerProduct.ids) ? { ...triggerProduct, ids: [categoryPreview] } : triggerProduct;

  const { data, isFetching } = getRandomProductToShow(trigger, 3);

  const getCategoryPreviewDiscount = () => {
    let discountValue = '';
    const categoryDiscounts = campaign.meta['category-discount'].category_discount;
    const catDiscount = categoryDiscounts.find(item => item.category === categoryPreview);
    if (!isEmpty(catDiscount)) {
      discountValue = catDiscount.discount_type === 'fixed' ? formatCurrency(catDiscount.discount_value, false, false) : catDiscount.discount_value + '%';
    }
    return discountValue;
  };

  const categoryTitle = categoryPreview ? title.replace('%discount%', getCategoryPreviewDiscount()) : title;
  const getDiscountRule = (product) => {
    const defaultRule = { id: product.id, discount_value: 10, discount_type: 'percentage' };
    const rule = product.categories.length > 0 ? rules.find(rule => rule.category === product.categories[0].id) : defaultRule;
    return rule ?? defaultRule;
  };

  const getDiscountByProduct = (product) => {
    const currentRule = getDiscountRule(product);
    return currentRule ? getDiscountedPrice(product.price, currentRule.discount_value, currentRule.discount_type) : product.price;
  };

  const getEmptyObject = (key) => {
    const generalImage = getImage({}, true);
    return (<Stack key={key}>
      <ProductImage src={generalImage.src} alt={generalImage.alt} width="200px"/>
      <Divider sx={{ width: '100%', marginBottom: '15px', marginTop: '20px', borderBottomWidth: 'thick' }}/>
      <Divider sx={{ width: '100%', marginBottom: '15px', borderBottomWidth: 'thick' }}/>
      <Divider sx={{ width: '100%', borderBottomWidth: 'thick' }}/>
    </Stack>);
  };

  if (step !== 'customization') {
    showBadge = 'no';
  }

  return (
      <Box className="yith-sales-category-discount-preview-wrapper"
           sx={{ backgroundColor: '#fff', padding: '40px 60px' }}>
        {!showContent && <Stack direction="row" spacing={2.5}>{[...Array(3)].map((x, i) => getEmptyObject(i))}</Stack>}
        {showContent && <>
        <Typography sx={{
          marginTop: '20px',
          marginBottom: '50px',
          color: '#E4E4E4',
          fontSize: '40px',
          textTransform: 'uppercase',
          lineHeight: '17px',
          textAlign: 'center',
          fontWeight: '700',
          letterSpacing: '3px'
        }}> {__('Category Page', 'wonder-cart')}</Typography>
        {showContent && <BlockEditorPreview blocks={parse(categoryTitle)}/>}
        {(isFetching || !data) && <Box sx={{ textAlign: 'center' }}><Spinner/></Box>}
        {!isFetching && data && data.length > 0 &&
          <Stack direction="row" spacing={2.5} >
        {data.map(product => {
          const productImage = getImage(product, true);
          const discountedPrice = getDiscountByProduct(product);
          const rule = getDiscountRule(product);
          const badgeDiscount = rule.discount_type === 'fixed' ? formatCurrency(rule.discount_value, false, false) : rule.discount_value + '%';
          const currentBadgeText = badgeText.replace('%discount%', badgeDiscount);

          return (
          <Stack key={product.id}>
        <ProductImage src={productImage.src} alt={productImage.alt} width="200px"
                      badge={showBadge === 'yes' ? currentBadgeText : false}
                      badgeBackgroundColor={badgeBackgroundColor} badgeColor={badgeColor} badgeBorderRadius="3px"/>
        <Typography sx={{
          marginTop: '20px',
          marginBottom: '15px',
          fontSize: '14px',
          lineHeight: '17px',
          textAlign: 'center',
          fontWeight: '700'
        }}> {getFormattedTermName(product)}</Typography>
        <Stack spacing={1} justify="center" direction="row">
          <Price price={discountedPrice}/>
          <Price price={product.price} showDel={discountedPrice !== product.price}/>
          {showSaving === 'yes' && <SaveDiscount saved={rule.discount_value} discountType={rule.discount_type}/>}
        </Stack>
      </Stack>);
        })}
              </Stack>
          }
        </>
        }
      </Box>
  );
};

export default CategoryDiscount;
