import React, { useEffect, useRef } from 'react';

import { Box, Button, Card, CardHeader, Stack, Typography } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';
import { useDispatch, useSelector } from 'react-redux';

import { setCampaignId, setCampaignType } from '../../slices/editor/campaign-slice';
import { setEndpoint } from '../../slices/view/view-campaigns-list';
import { theme } from '../../theme';
import CampaignEditor from '../campaign-editor';
import CampaignList from '../campaign-list';
import ModalMenu from '../new-campaign-menu';
import ReportPage from '../report-page';
import SettingsPage from '../settings-page';

const Dashboard = ({ page }) => {
  const mainlist = useRef();
  const endpoint = useSelector((state) => state.campaignsView.endpoint);
  const dispatch = useDispatch();

  useEffect(() => {
    mainlist.current.focus();
    if (endpoint !== 'edit') {
      document.body.classList.remove('is-fullscreen-mode');
      document.body.style = null;
    }
  }, [endpoint]);

  const handleClick = (event) => {
    dispatch(setEndpoint(event.target.id));
  };
  const onClickNewCampaign = (event) => {
    dispatch(setEndpoint('edit'));
    dispatch(setCampaignId('new'));
    dispatch(setCampaignType(event.target.id));
  };

  const closeModal = () => {
    dispatch(setEndpoint('campaigns'));
  };
  const subtitle = <Typography component='p' sx={{ marginTop: '1rem!important', fontSize: 'inherit!important' }} color = 'text.secondary' >{__('Create and manage X-sell, Upsell, sales and promotions through campaigns to boost your sales.', 'wonder-cart')}</Typography>;

  return (

        <Box component="div" ref={mainlist}>
            <Card className='wonder-cart-dashboard' variant='outlined' borderColor='border.tertiary' sx={{ borderWidth: 0 }}>
                <Stack direction='column'>
                    <CardHeader
                        title={__('Sales & Promotions', 'wonder-cart')}
                        titleTypographyProps={{
                          variant: 'h2',
                          sx: {
                            fontSize: '1.5rem!important'
                          }
                        }}
                        subtitle={subtitle}
                        sx={{ padding: '32px' }}
                    />

                    <Box className='wonder-cart-buttons-header' sx={
                        {
                          borderBottom: `1px solid ${theme.palette.tabs.borderColor}`,
                          paddingLeft: '32px',
                          '& button': {
                            padding: '12px 16px!important',
                            fontWeight: 500
                          }
                        }
                    }>
                        <Box sx={{
                          marginLeft: '-20px',
                          '& .lapilliUI-Button__root': {
                            paddingLeft: '0!important',
                            paddingRight: '0!important',
                            fontSize: '16px',
                            marginLeft: '20px',
                            marginRight: '20px',
                            color: '#000!important'
                          },
                          '& .lapilliUI-Button__root:hover': {
                            borderBottom: '2px solid ' + theme.palette.tabs.hover,
                            borderRadius: '0!important',
                            backgroundColor: '#fff'
                          },
                          '& .lapilliUI-Button__root.active': {
                            borderBottom: '2px solid ' + theme.palette.tabs.active,
                            borderRadius: '0!important',
                            backgroundColor: '#fff'
                          },
                          '& .lapilliUI-Button__root:focus': {
                            backgroundColor: '#fff',
                            boxShadow: 'unset'
                          }
                        }}
                        >
                            <Button variant='text' id='campaigns' className={endpoint === 'campaigns' ? 'active' : ''}
                                    onClick={handleClick}>{__('All Campaigns', 'wonder-cart')}</Button>
                            <Button variant='text' id='settings' className={endpoint === 'settings' ? 'active' : ''}
                                    onClick={handleClick}>{__('Settings', 'wonder-cart')}</Button>
                            <Button variant='text' id='reports' className={endpoint === 'reports' ? 'active' : ''}
                                    onClick={handleClick}>{__('Reports', 'wonder-cart')}</Button>
                        </Box>
                    </Box>
                </Stack>
                {(endpoint === 'campaigns' || endpoint === 'new') && <CampaignList/>}
                {endpoint === 'settings' && <SettingsPage/>}
                {endpoint === 'reports' && <ReportPage/>}
                {endpoint === 'edit' && <CampaignEditor/>}
                <ModalMenu onClose={closeModal} open={endpoint === 'new'} onClickNew={onClickNewCampaign}/>
            </Card>
        </Box>
  );
};

export default Dashboard;
