import React, { useEffect, useState } from 'react';

import { Button, Select, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import SimpleModal from '../../../../blocks/elements/simple-modal';
import { isEmpty } from '../../../../common/utils/common';
import { SelectedItemList, TermsSearchForm } from '../index';

const SelectTermsWrapper = styled('div')(({ theme }) => ({
  '& button': {
    marginTop: '11px',
    marginBottom: '30px'
  },
  '& .lapilliUI-Select__root': {
    width: '100%'
  }
}));
const SelectTerms = ({ id, label, value, options, buttonLabels, editButtonLabels, searchModalTerms, onSaveHandler, required = false, multiple = true, showError = false, ...other }) => {
  const [isModalVisible, setModalVisible] = useState(false);
  const [isEditMode, setEditMode] = useState(false);
  const [modalTitle, setModalTitle] = useState('');
  const [termToSearch, setTermToSearch] = useState(false);
  const [placeholderSearch, setPlaceholderSearch] = useState(false);
  const [buttonLabel, setButtonLabel] = useState('');
  const [editButtonLabel, setEditButtonLabel] = useState('');

  const getCurrentButtonLabel = (currentOPT) => {
    const filtered = buttonLabels.find(label => {
      return label.value === currentOPT;
    });
    return filtered?.value ? filtered : '';
  };
  const getEditCurrentButtonLabel = (currentOPT) => {
    let filtered = false;

    if (!isEmpty(value.ids)) {
      filtered = editButtonLabels.find(label => {
        return label.value === currentOPT;
      });
    }
    return filtered?.value ? filtered : '';
  };
  const handleChangeSelect = (newValue) => {
    if (isEditMode) {
      setEditMode(false);
    }

    setButtonLabel(getCurrentButtonLabel(newValue));
    const metaValue = { type: newValue, ids: [] };
    const e = {
      target: {
        id,
        value: metaValue,
        error: required && (metaValue.type === 'products' || metaValue.type === 'categories') && isEmpty(metaValue.ids)
      }
    };
    onSaveHandler(e);
  };

  const handleOpenModal = (value) => {
    const filtered = searchModalTerms.find(label => {
      return label.value === value.value;
    });

    if (filtered?.value) {
      const modalTitle = getCurrentButtonLabel(value.value);
      setTermToSearch(value.value);
      setPlaceholderSearch(filtered.placeholder);
      setModalTitle(modalTitle.label);
      setModalVisible(true);
    }
  };
  const handleCloseModal = (termsToSave) => {
    if (termsToSave.length >= 0) {
      const metaValue = { type: value.type, ids: termsToSave };

      const e = {
        target: {
          id,
          value: metaValue,
          error: required && (metaValue.type === 'products' || metaValue.type === 'categories') && isEmpty(metaValue.ids)
        }
      };
      setModalVisible(false);
      setEditMode(true);
      onSaveHandler(e);
    }
  };
  const handleRemoveItem = (termRemoved) => {
    const newIds = value.ids.filter(id => {
      return id !== termRemoved;
    });
    const metaValue = { type: value.type, ids: newIds };

    const e = {
      target: {
        id,
        value: metaValue,
        error: required && (metaValue.type === 'products' || metaValue.type === 'categories') && isEmpty(metaValue.ids)
      }
    };
    onSaveHandler(e);
  };

  useEffect(() => {
    setEditMode(value?.ids && value.ids.length > 0);
  }, [value]);

  useEffect(() => {
    // eslint-disable-next-line no-unused-expressions
    setButtonLabel(getCurrentButtonLabel(value.type));
    setEditButtonLabel(getEditCurrentButtonLabel(value.type));
  }, [value]);

  const error = showError && required && (value.type === 'products' || value.type === 'categories') && isEmpty(value.ids);
  const typeMessage = value.type === 'products' ? __('Please, select at least one product ', 'wonder-cart') : __('Please, select at least one category', 'wonder-cart');
  const showNotice = error ? <Typography className="yith-sales-field-error" sx={{ padding: '10px 0', marginTop: '-30px' }}>{typeMessage}</Typography> : '';
  return (
      <>
        <SelectTermsWrapper className={error ? 'yith-sales-field-error' : ''}>
          {label && <label htmlFor={id}>{label}</label>}
          {options.length > 1 && <Select
              id={id}
              options={options}
              value={value.type}
              onChange={handleChangeSelect}
              {...other}
          />}
          {
              !isEditMode && buttonLabel && (
                  <Button size="lg" fullWidth={true} onClick={() => handleOpenModal(buttonLabel)}>{buttonLabel.label}</Button>
              )
          }
          {
              isEditMode && (
                  <>
                    <SelectedItemList termType={value.type} termIds={value.ids} onRemoveItem={handleRemoveItem}/>
                    <Button size="lg" fullWidth={true} onClick={() => handleOpenModal(editButtonLabel)}>{editButtonLabel.label}</Button>
                  </>
              )
          }
          {showNotice}
        </SelectTermsWrapper>
        <SimpleModal title={modalTitle} open={isModalVisible} onClose={() => setModalVisible(false)}>
          {
              termToSearch && placeholderSearch && (
                  <TermsSearchForm termsToSearch={termToSearch} multiple={multiple} termsSelected={value.ids} placeholder={placeholderSearch} showCount="true" onSaveClick={handleCloseModal}/>
              )
          }
        </SimpleModal>

      </>
  );
};

export default SelectTerms;
