import React, { useState } from 'react';

import { generateComponentClasses, styled } from '@lapilli-ui/styles';
import classNames from 'classnames';

import { theme } from '../../theme';

const useComponentClasses = (ownerState) => {
  const { direction } = ownerState;
  return generateComponentClasses(
    'WonderCarToolTip',
    {
      root: [
        'root'
      ],
      wrapper: [
        'wrapper',
        direction
      ]
    }
  );
};

const TooltipRoot = styled('div', { name: 'WonderCarToolTip', slot: 'Root' })(({ theme }) => ({
  display: 'inline-block',
  position: 'relative'
})
);

const TooltipWrapper = styled('div', { name: 'WonderCarToolTip', slot: 'Wrapper' })(({ theme, ownerState }) => ({
  position: 'absolute',
  left: '50%',
  transform: 'translateX(-50%)',
  lineHeight: '1!important',
  zIndex: 100,
  whiteSpace: 'nowrap',
  fontSize: '0.8em!important',
  maxWidth: '150px',
  textAlign: 'center',
  borderRadius: '3px',
  padding: '0.618em 1em',
  borderStyle: 'unset',
  borderWidth: theme.palette.tooltip.borderWidth,
  background: theme.palette.tooltip.backgroundColor,
  color: theme.palette.tooltip.color + '!important',
  '&:before': {
    content: '""',
    left: '50%',
    border: 'solid transparent',
    height: 0,
    width: 0,
    position: 'absolute',
    pointerEvents: 'none',
    marginLeft: `calc(${theme.palette.tooltip.borderWidth} * -1)`,
    borderWidth: theme.palette.tooltip.borderWidth
  },
  ...(ownerState.direction === 'top' && {
    top: `calc(${theme.palette.tooltip.margin} * -1)`,
    '::before': {
      top: '100%',
      borderTopColor: theme.palette.tooltip.backgroundColor
    }
  }),
  ...(ownerState.direction === 'right' && {
    top: '50%',
    left: `calc(100% + ${theme.palette.tooltip.margin})`,
    transform: 'translateX(0) translateY(-50%)',
    '::before': {
      left: `calc(${theme.palette.tooltip.borderWidth} * -1)`,
      top: '50%',
      transform: 'translateX(0) translateY(-50%)',
      borderRightColor: theme.palette.tooltip.backgroundColor
    }
  }
  ),
  ...(ownerState.direction === 'bottom' && {
    bottom: `calc(${theme.palette.tooltip.margin} * -1)`,
    '::before': {
      bottom: '100%',
      borderBottomColor: theme.palette.tooltip.backgroundColor
    }
  }),
  ...(ownerState.direction === 'left' && {
    top: '50%',
    left: 'auto',
    right: `calc(100% + ${theme.palette.tooltip.margin})`,
    transform: 'translateX(0) translateY(-50%)',
    '::before': {
      left: 'auto',
      right: `calc(${theme.palette.tooltip.borderWidth} * -2)`,
      top: '50%',
      transform: 'translateX(0) translateY(-50%)',
      borderLeftColor: theme.palette.tooltip.backgroundColor
    }
  })

})
);

const Tooltip = (props) => {
  const [active, setActive] = useState(false);
  const ownerState = { direction: props.direction };
  const classes = useComponentClasses(ownerState);
  const showTip = () => {
    setActive(true);
  };

  const hideTip = () => {
    setActive(false);
  };

  return (
      <TooltipRoot
          onMouseEnter={showTip}
          onMouseLeave={hideTip}
          className={ classNames(classes.root, props.className) }
      >
        {props.children}
        {active && (
            <TooltipWrapper className={classes.wrapper} ownerState={ownerState}>
              {props.content}
            </TooltipWrapper>
        )}
      </TooltipRoot>
  );
};

export default Tooltip;
