import React from 'react';

import { __ } from '@wordpress/i18n';

import { VerticalScrollableContentStyled } from '../../../../blocks/elements/styles/VerticalScrollableContent.styled';
import { wcSettings } from '../../../config';
import { PreviewItemName, PreviewItemsBoxContainer, PreviewItemsBoxWrapper } from '../../styles/PreviewItemsBox.styled';

const CategoryRuleList = ({ items, categories }) => {
  if (items.length === 0 || categories.length === 0) {
    return '';
  }

  const getFormattedName = (item) => {
    const category = categories.find(category => category.id === item.category);
    const discountType = item.discount_type === 'percentage' ? '%' : wcSettings.currency.symbol;
    return `${item.discount_value}${discountType}  ${__('on', 'wonder-cart')} ${category?.name}`;
  };

  return (
      <PreviewItemsBoxContainer>
        <PreviewItemsBoxWrapper>
          <VerticalScrollableContentStyled maxHeight="98px">
            <ul>
              {items.map((item) => (
                  <li key={item.id}>
                    <PreviewItemName>
                      {getFormattedName(item)}
                    </PreviewItemName>
                  </li>
              ))}
            </ul>
          </VerticalScrollableContentStyled>
        </PreviewItemsBoxWrapper>
      </PreviewItemsBoxContainer>
  );
};

export default CategoryRuleList;
