import React from 'react';

import { Modal, ModalContent, ModalTitle } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { useEffect } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

import NewCampaignMenu from './new-campaign-menu';
import { useRegisterEventMutation } from '../../../common/api/campaign';

const ModalMenuStyled = styled(Modal)(({ theme, viewMode }) => ({
  '& .yithUI-ModalContent__root': {
    height: '660px'
  }
}));
const ModalMenu = ({ open, onClose, onClickNew }) => {
  const [registerEvent] = useRegisterEventMutation();

  useEffect(() => {
    if (open) {
      registerEvent({ body: { eventName: 'modal_opened' } });
    }
  }, [open]);

  const handleNewCampaignClick = ( event ) =>{
    registerEvent({ body: { eventName: 'campaign_selected', args:{ type: event.target.id } } });
    onClickNew(event);
  };

  return (
      <ModalMenuStyled open={open} onClose={onClose} maxWidth={1000}>
        <ModalTitle>{__('Create a new campaign', 'wonder-cart')}</ModalTitle>
        <ModalContent><NewCampaignMenu onClick={handleNewCampaignClick}/></ModalContent>
      </ModalMenuStyled>
  );
};

export default ModalMenu;
