/**
 * Change the path if the page
 *
 * @param query_args
 * @param add_query
 */
export const addNewPathInHistory = (query_args, add_query) => {
  let path = '';
  if (add_query) {
    path = getNewPath(
      (query_args),
      '/'
    );
  } else {
    path = getNewPath(
      (query_args),
      '/',
      getPersistedQuery(),
      panelPage
    );
  }

  getHistory().push(path);
};

