/**
 * Return a highlight text
 *
 * @return string
 */
export const getHighlightedText = (text, highlight) => {
  // Split on highlight term and include term into parts, ignore case
  const parts = text.split(new RegExp(`(${highlight})`, 'gi'));
  return <span> {parts.map((part, i) =>
        <span key={i} style={part.toLowerCase() === highlight.toLowerCase() ? { fontWeight: 'bold' } : {}}>
            {part}
        </span>)
    } </span>;
};

export const componentIsVisible = (field, values, checkCampaignMeta = true) => {
  let show = true;
  if (!field?.deps) return show;

  let deps = field.deps;

  const relation = deps?.relation ? deps.relation.toLowerCase() : 'and';
  deps = deps?.conditions ? deps.conditions : deps;
  if (!Array.isArray(deps)) {
    deps = [deps];
  }

  if (deps) {
    show = checkConditions(deps, relation, values, checkCampaignMeta);
  }

  return show;
};

export const checkConditions = (conditions, relation, values, checkCampaignMeta) => {
  const conditionResults = [];

  for (const i in conditions) {
    const condition = conditions[i];
    const isValid = isConditionValid(condition, values, checkCampaignMeta);
    conditionResults.push(isValid);
  }

  let result = conditionResults.length ? conditionResults[0] : true;
  for (const i in conditionResults) {
    const singleResult = conditionResults[i];
    if (relation === 'or') {
      result = result || singleResult;
    } else {
      result = result && singleResult;
      if (!result) return false;
    }
  }

  return result;
};

export const isConditionValid = (condition, values, checkCampaignMeta) => {
  if (Array.isArray(condition)) {
    for (const i in condition) {
      const dep = condition[i];
      const res = checkCampaignMeta ? isDepValid(dep, values) : isDepOptionValid(dep, values);

      if (!res) return false;
    }
  } else {
    return checkCampaignMeta ? isDepValid(condition, values) : isDepOptionValid(condition, values);
  }
  return true;
};

export const isDepValid = (dep, campaignMeta) => {
  let fieldValue, currentValue;
  const show = true;
  if (typeof dep.value === 'object') {
    const fieldId = dep.value.index;
    fieldValue = dep.value.value;
    currentValue = campaignMeta[dep.id][fieldId];
  } else {
    fieldValue = dep.value;
    currentValue = campaignMeta[dep.id];
  }

  return evalueCondition(currentValue, fieldValue, dep);
};

export const evalueCondition = (currentValue, fieldValue, dep) => {
  let show = true;
  if (dep?.condition) {
    const value = typeof currentValue === 'object' ? currentValue.length : currentValue;

    switch (dep.condition) {
      case '>':
        show = value > fieldValue;
        break;
      case '>=':
        show = value >= fieldValue;
        break;
      case '<':
        show = value < fieldValue;
        break;
      case '<=':
        show = value <= fieldValue;
        break;
      case '==':
      case '===':
        show = value == fieldValue;
        break;
      case '!=':
      case '!==':
        show = value != fieldValue;
        break;
    }
  } else {
    if (dep.type === 'show') {
      if (currentValue !== fieldValue) {
        show = false;
      }
    } else {
      if (currentValue === fieldValue) {
        show = false;
      }
    }
  }

  return show;
};

export const isDepOptionValid = (dep, options) => {
  let fieldId = '';
  let fieldValue = '';
  let currentValue = '';
  const isObject = typeof dep.value === 'object';

  if (isObject) {
    fieldId = dep.value.index;
    fieldValue = dep.value.value;
  } else {
    fieldId = dep.id;
    fieldValue = dep.value;
  }
  for (const index in options) {
    if (options[index][fieldId]) {
      if (isObject) {
        currentValue = options[index][dep.id][fieldId];
      } else {
        currentValue = options[index][dep.id];
      }
      break;
    }
  }

  return evalueCondition(currentValue, fieldValue, dep);
};
