import React from 'react';

import { Button, Modal, ModalActions, ModalContent, ModalTitle } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';

import { theme } from '../../theme';
const ConfirmDialog = ({ open, onClose, children, onCancel, onConfirm, confirmButton, cancelButton, title, isDeleteButton = false }) => {
  return (
      <Modal
          onClose={onClose}
          open={open}
      >
        <ModalTitle sx={{fontSize:theme.typography.body.fontSize}}>
          {title}
        </ModalTitle>
        <ModalContent sx={{fontSize:theme.typography.body2.fontSize}}>
          {children}
        </ModalContent>
        <ModalActions>
          <Button
              onClick={onCancel}
              variant="text"
          >
            {cancelButton ?? __('Cancel', 'wonder-cart')}
          </Button>
          <Button onClick={onConfirm} color={isDeleteButton ? 'error' : 'primary'}>
            {confirmButton}
          </Button>
        </ModalActions>
      </Modal>);
};

export default ConfirmDialog;
