import { Box, Select } from '@lapilli-ui/components';

const SimpleSelect = ({ id, label, value, options, onSaveHandler, ...other }) => {
  const handleChange = (value) => {
    const e = {
      target: {
        id,
        value
      }
    };
    onSaveHandler(e);
  };

  return (
        <Box>
            <label htmlFor={id}>{label}</label>
        <Select id={id} options={options} onChange={handleChange} value={value} {...other}/>
        </Box>
  );
};

export default SimpleSelect;
