import React from 'react';

import Chip from '@mui/material/Chip';

import { theme } from '../../theme';
import { getStatusCampaignLabel } from '../../utils/admin-functions';

const StatusBadge = ({ label, sx }) => {
  if (label === 'active' || label === 'inactive') {
    return '';
  }

  let col = theme.palette.badge.background;
  if (label === 'scheduled') {
    col = theme.palette.primary.orange;
  }
  return (
      <Chip size="small" label={getStatusCampaignLabel(label)} sx={{ backgroundColor: col, color: theme.palette.badge.color, ...sx, fontSize: '10px' }}/>
  );
};

export default StatusBadge;
