import React, { useReducer } from 'react';

import { Button, Spinner } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __, _n, sprintf } from '@wordpress/i18n';

import BoxWithInfo from '../../../../blocks/elements/box-with-info';
import { VerticalScrollableContentStyled } from '../../../../blocks/elements/styles/VerticalScrollableContent.styled';
import { Checkbox } from '../../../../common/fields';
import { getFormattedTermName, getImage, isEmpty } from '../../../../common/utils/common';
import { searchTermsByType } from '../../../../common/utils/data';
import { getHighlightedText } from '../../../utils/admin-functions';

const TermListContainer = styled('div')(({ theme }) => ({
  '& button': {
    float: 'right',
    marginTop: '15px'
  }
}));
const TermListWrapper = styled('div')(({ theme }) => ({
  border: '1px solid #D8D8D8',
  borderRadius: '3px',
  marginTop: '20px',
  padding: '18px 0 18px 0',
  '& > div': {
    marginRight: '14px'
  },
  '& ul': {
    listStyle: 'none',
    margin: 0,
    paddingLeft: '15px'
  },
  '& ul li': {
    marginBottom: '18px',
    display: 'flex',
    alignItems: 'center',
    fontSize: '14px',
    lineHeight: '16px'
  },
  '& ul li > *': {
    marginRight: '10px'
  },
  '& ul li:last-child': {
    marginBottom: 0
  },
  '& ul li img': {
    width: '40px',
    height: '40px',
    border: '1px solid #D8D8D8'
  }
}));

const initState = (initialState) => {
  return { selectedItems: initialState.selectedItems };
};

const scanReducer = (state, action) => {
  switch (action.type) {
    case 'setItems':
      return { ...state, selectedItems: action.payload };
  }
};
const TermCount = styled('span')(({ theme }) => ({
  marginLeft: '5px',
  color: '#959595',
  fontSize: '11px',
  lineHeight: '13px'
}));

const TermsSearchList = ({
  termsToSearch,
  termSearched,
  termsSelected,
  multiple,
  showCount,
  onSaveClick
}) => {
  const _initializeSelectedItems = () => {
    return { selectedItems: [...termsSelected] };
  };
  const [state, dispatch] = useReducer(scanReducer, _initializeSelectedItems(), initState);

  const { selectedItems } = state;
  const { data, isLoading, isFetching } = searchTermsByType({
    search: termSearched,
    type: termsToSearch
  });

  const ifItemExist = (newItem) => {
    return !isEmpty(selectedItems) && selectedItems.indexOf(newItem) !== -1;
  };

  const deleteItem = (itemToDelete) => {
    const newItems = selectedItems.filter(item => item !== itemToDelete);
    dispatch({ type: 'setItems', payload: newItems });
  };
  const addItem = (itemToAdd) => {
    if (multiple) {
      selectedItems.push(itemToAdd);
      dispatch({ type: 'setItems', payload: selectedItems });
    } else {
      dispatch({ type: 'setItems', payload: [itemToAdd] });
    }
  };
  const handleCheck = (e, term) => {
    if (ifItemExist(term)) {
      deleteItem(term);
    } else {
      addItem(term);
    }
  };

  const handleClick = () => {
    onSaveClick(selectedItems);
  };

  if (!isLoading && (!data || data.length === 0)) {
    let noResultLabel = __('No results', 'wonder-cart');
    if (termSearched !== '') {
      noResultLabel = sprintf(__('No results found for %s', 'wonder-cart'), termSearched);
    }
    return <BoxWithInfo info={noResultLabel} justify='center' iconSize={20} icon='InformationCircleIcon'
                            bgColor='#fff' color='#000' borderColor="#cacaca"/>;
  }
  if (isLoading || isFetching) {
    return <Spinner sx={{ marginLeft: '50%', marginTop: '40px' }}/>;
  }

  return (
        <TermListContainer>
            <TermListWrapper>
                <VerticalScrollableContentStyled maxHeight='230px'>
                    <ul>
                        {data && data.map((term) => {
                          const image = termsToSearch === 'products' ? getImage(term) : false;
                          return (
                                <li key={term.id} id={term.id}>
                                    <Checkbox checked={ifItemExist(term.id)}
                                              onChange={(e) => handleCheck(e, term.id)}/>
                                    {image && (<img src={image.src} alt={image.alt}/>)}
                                    <span className="yith-sales-terms__term_name">
                                {getHighlightedText(getFormattedTermName(term), termSearched)}
                                        {
                                            showCount && term.count && (
                                                <TermCount>{sprintf(_n('(%s product)', '(%s products)', term.count), term.count)}</TermCount>
                                            )
                                        }
                            </span>
                                </li>
                          );
                        })}
                    </ul>
                </VerticalScrollableContentStyled>
            </TermListWrapper>
            {
                selectedItems.length > 0 && (
                    <Button size="lg"
                            onClick={handleClick}>{__('Save', 'wonder-cart')}</Button>
                )
            }
        </TermListContainer>
  );
};

export default TermsSearchList;
