import React from 'react';

import { Box, Spinner, Stack, Typography } from '@lapilli-ui/components';
import { parse } from '@wordpress/blocks';

import Price from '../../../../../blocks/elements/price';
import ProductImage from '../../../../../blocks/elements/product-image';
import SaveDiscount from '../../../../../blocks/elements/save-discount';
import { Divider } from '../../../../../common/fields';
import { getFormattedTermName, getImage } from '../../../../../common/utils/common';
import { getRandomProductToShow } from '../../../../../common/utils/data';
import { formatCurrency, getDiscountedPrice } from '../../../../../common/utils/numbers';
import BlockEditorPreview from '../../../block-editor-preview';

const ShopDiscount = ({ campaign, step }) => {
  const options = campaign.meta[campaign.type];
  let {
    title,
    exclude_products: excludeProducts,
    exclude_products_list: excludeProductsList,
    discount_to_apply: discountToApply,
    show_saving: showSaving,
    show_badge: showBadge,
    badge_text: badgeText,
    badge_background_color: badgeBackgroundColor,
    badge_text_color: badgeColor
  } = options;

  const { data, isFetching } = getRandomProductToShow({ type: 'all', ids: [] }, 3, excludeProducts === 'yes' ? excludeProductsList : {}, 'edit');
  const { discount_value: discountValue, discount_type: discountType } = discountToApply;

  const showContent = campaign.status === 'publish' || step !== 'trigger';

  if (step !== 'customization') {
    showBadge = 'no';
  }
  const getEmptyObject = (key) => {
    const generalImage = getImage({}, true);
    return (<Stack key={key}>
     <ProductImage src={generalImage.src} alt={generalImage.alt} width='200px'/>
     <Divider sx={{ width: '100%', marginBottom: '15px', marginTop: '20px', borderBottomWidth: 'thick' }}/>
     <Divider sx={{ width: '100%', marginBottom: '15px', borderBottomWidth: 'thick' }}/>
     <Divider sx={{ width: '100%', borderBottomWidth: 'thick' }}/>
   </Stack>);
  };
  const discountText = discountType === 'fixed' ? formatCurrency(discountValue, false, false) : discountValue + '%';

  return (
      <Box className="yith-sales-shop-discount-preview-wrapper" sx={{ backgroundColor: '#fff', padding: '40px 60px' }}>
        { showContent && <BlockEditorPreview blocks={parse(title.replace('%discount%', discountText))}/>}

        <Stack direction="row" spacing={2.5}>
        {!showContent && [...Array(3)].map((x, i) => getEmptyObject(i)) }
          {showContent && <>
             {(isFetching || !data) && <Box sx={{ textAlign: 'center' }}><Spinner/></Box>}
            {!isFetching && data && data.length > 0 &&
                <Stack direction="row" spacing={2.5}>
                  { data.map(product => {
                    const productImage = getImage(product, true);
                    const discountedPrice = getDiscountedPrice(product.price, discountValue, discountType);
                    const currentBadgeText = badgeText.replace('%discount%', discountText);
                    return (
                        <Stack key={product.id}>
                          <ProductImage src={productImage.src} alt={productImage.alt} width='200px' badge={ showBadge === 'yes' ? currentBadgeText : false}
                                        badgeBackgroundColor={badgeBackgroundColor} badgeColor={badgeColor} badgeBorderRadius="3px"/>
                          <Typography component="div" sx={{ marginTop: '20px', marginBottom: '15px', fontSize: '14px', lineHeight: '17px', textAlign: 'center', fontWeight: '700' }}> {getFormattedTermName(product)}</Typography>
                          <Stack spacing={0.63} justify="center" direction="row">
                            <Price price = {discountedPrice} />
                            <Price price={product.price} showDel={discountedPrice !== product.price}/>
                            { showSaving === 'yes' && <SaveDiscount saved={discountValue} discountType={discountType}/>}
                          </Stack>
                        </Stack>);
                  })}
                </Stack>
            }
          </>}
        </Stack>
      </Box>
  );
};

export default ShopDiscount;
