import React, { useEffect } from 'react';

import { Collapse, IconButton } from '@lapilli-ui/components';

import Icon from '../../../common/icon';
import Alert from '../alert';

const Notice = ({ severity, message, closeIn = 5, onEnd }) => {
  const [open, setOpen] = React.useState(true);

  useEffect(() => {
    let mounted = true;
    setTimeout(() => {
      if (mounted) {
        setOpen(false);
        onEnd();
      }
    }, closeIn * 1000);
    return () => { mounted = false; };
  }, []);

  return (
      <Collapse open timeout={2000}>
     <Alert severity={severity}
            sx={{ marginTop: '20px' }}
                  action={
                    <IconButton
                        aria-label="close"
                        color="inherit"
                        size="small"
                        onClick={() => {
                          setOpen(false);
                        }}
                    >
                      <Icon icon="XMarkIcon" />
                    </IconButton>
                  }
     >
       {message}
     </Alert>
      </Collapse>
  );
};

export default Notice;
