import React from 'react';

import { styled } from '@lapilli-ui/styles';

import Field from '../../field';

const InlineFieldsWrapper = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'end',
  justifyContent: 'space-between',
  '& .yith-sales-inline-fields__field:nth-of-type(1) .yith-sales-colorpicker-wrapper .yith-sales-color-picker-popup-field': {
    left: '0!important'
  },
  '& .yith-sales-html-field': {
    fontSize: '0.81rem'
  }
}));
const InlineFields = ({ id, label, inlineFields, value, onSaveHandler, ...other }) => {
  return (
        <div className="yith-sales-inline-fields">
            { (typeof label !== 'undefined' && label !== '') && <label htmlFor={id}>{label}</label>}
            <InlineFieldsWrapper>
            {

                inlineFields.map(field => {
                  if (value[field.id] !== 'undefined' && value[field.id] !== '') {
                    field.value = value[field.id];
                  } else {
                    field.value = field.default;
                  }
                  if (!field.isVisible) {
                    return '';
                  }
                  return (
                            <div className="yith-sales-inline-fields__field" key={field.id}>
                                {<Field field={field} onSave={onSaveHandler} {...field.style}/>}
                            </div>
                  );
                }
                )
            }
            </InlineFieldsWrapper>
        </div>
  );
};

export default InlineFields;
