import React from 'react';

import { styled } from '@lapilli-ui/styles';

import BoxWithInfo from '../../../blocks/elements/box-with-info';
import {
  CartDiscountRules,
  CategoriesDiscount,
  ColorPicker, Dimensions, DiscountField, FreeShippingRules,
  GiftDiscountRules, Html, InlineFields, Media, MultiSelect, Number,
  Onoff, QuantityDiscountRules, RadioButtonGroup, SearchLink, SelectCondition, SelectTerms,
  SimpleSelect, Text, XYPromoRule
} from '../fields/';
import CompactBlockEditor from '../fields/compact-block-editor';
import ToggleContainer from '../toggle-container';

const FieldWrapper = styled('div')(({ theme }) => ({
  '&': {
    marginBottom: '20px',
    paddingRight: '20px'
  },
  '& .yith-sales-toggle-field': {
    marginRight: '-20px'
  },
  '& label': {
    color: theme.palette.text.label,
    fontSize: theme.typography.body.fontSize,
    fontWeight: 500,
    margin: '10px 0',
    display: 'inline-block'
  },
  '& .lapilliUI-Select__root': {
    display: 'block'
  }

}));
const Field = ({ field, onSave, showError = false, ...other }) => {
  let componentField = false;
  const className = 'yith-sales-field-wrapper';

  const attributes = {
    ...other,
    onSaveHandler: onSave,
    id: field.id,
    label: field.name,
    description: field?.description ? field.description : '',
    required: field?.required ? field.required : false
  };
  switch (field.type) {
    case 'text':
      componentField = <Text disabled={field?.disabled && field.disabled === 'yes'} value={typeof field.value !== 'undefined' ? field.value : field.default} placeholder={field.placeholder} showError={showError} errorMessage={field?.error} {...attributes}/>;
      break;
    case 'number':
      componentField = <Number value={field?.value ? field.value : field.default} min={field.min} max={field.max} step={field.step} {...attributes}/>;
      break;
    case 'selectCondition':
      componentField = <SelectCondition value={field?.value ? field.value : { type: field.default, ids: [] }} options={field.options} buttonLabels={field.buttonLabels} editLabels={field.editButtonLabels} searchModalTerms={field.searchModalTerms} {...attributes}/>;
      break;
    case 'selectTerms':
      componentField = <SelectTerms value={field?.value ? field.value : field.default} options={field.options} buttonLabels={field.buttonLabels} editButtonLabels={field.editButtonLabels} searchModalTerms={field.searchModalTerms} multiple={field.multiple} showError={showError} {...attributes} />;
      break;
    case 'toggle':
      componentField = <ToggleContainer isOpened={field.opened} value={field.value} toggleData={field.fields} showError={showError} {...attributes}/>;
      break;
    case 'onoff':
      componentField = <Onoff value={field?.value ? field.value : field.default} {...attributes}/>;
      break;
    case 'inline-fields':
      componentField = <InlineFields inlineFields={field.fields} value={field?.value ? field.value : []} {...attributes}/>;
      break;
    case 'select':
      componentField = <SimpleSelect options={field.options} value={field?.value ? field.value : field.default} {...attributes}/>;
      break;
    case 'multiselect':
      componentField = <MultiSelect options={field.options} value={field.value} placeholder={field.placeholder} {...attributes} />;
      break;
    case 'textEditor':
      componentField = <CompactBlockEditor value={field?.value ? field.value : field.default} {...attributes} editorStyles={field.editorStyles}/>;
      break;
    case 'dimensions':
      componentField = <Dimensions dimensions={field.dimensions} min={field?.min ?? ''} max={field?.max ?? ''} value={field?.value ? field.value : field.default} allowLinked={field?.allowLinked ? field.allowLinked : true} showLabel={field?.showLabel ? field.showLabel : 'yes'} {...attributes}/>;
      break;
    case 'colorpicker':
      componentField = <ColorPicker align={field?.align ? field.align : 'left'} value={field.value} {...attributes}/>;
      break;
    case 'boxWithInfo':
      componentField = <BoxWithInfo {...field.info}/>;
      break;
    case 'quantityDiscountRules':
      componentField = <QuantityDiscountRules value={field.value} editButtonLabel={field.editButtonLabel} buttonLabel={field.buttonLabel} addNewRuleButtonLabel={field.addNewRuleButtonLabel} showError={showError} {...attributes}/>;
      break;
    case 'categoriesDiscount':
      componentField = <CategoriesDiscount value={field.value} buttonLabel={field.buttonLabel} showError={showError} {...attributes}/>;
      break;
    case 'cartDiscountRules':
      componentField = <CartDiscountRules value={field.value} editButtonLabel={field.editButtonLabel} buttonLabel={field.buttonLabel} addNewRuleButtonLabel={field.addNewRuleButtonLabel} showError={showError} {...attributes}/>;
      break;
    case 'giftDiscountRules':
      componentField = <GiftDiscountRules value={field.value} editButtonLabel={field.editButtonLabel} buttonLabel={field.buttonLabel} addNewRuleButtonLabel={field.addNewRuleButtonLabel} showError={showError} {...attributes}/>;
      break;
    case 'freeShippingRules':
      componentField = <FreeShippingRules value={field.value} editButtonLabel={field.editButtonLabel} buttonLabel={field.buttonLabel} addNewRuleButtonLabel={field.addNewRuleButtonLabel} showError={showError} {...attributes}/>;
      break;
    case 'discountField':
      componentField = <DiscountField value={field.value} {...attributes}/>;
      break;
    case 'radioGroup':
      componentField = <RadioButtonGroup value={field.value} options={field.options} {...attributes}/>;
      break;
    case 'html':
      componentField = <Html html={field.html}/>;
      break;
    case 'media':
      componentField = <Media value={field.value} {...attributes}/>;
      break;
    case 'search-link':
      componentField = <SearchLink id={field.id} label={field.label} value={field.value} showError={showError} {...attributes} />;
      break;
    case 'xyPromoRule':

      componentField = <XYPromoRule id={field.id} label={field.label} editButtonLabel={field.editButtonLabel} buttonLabel={field.buttonLabel} value={field.value} showError={showError} {...attributes} />;
      break;
    default:
      componentField = false;
  }

  if (!componentField) return '';
  if (field.isVisible !== 'undefined') {
    if (!field.isVisible) {
      return '';
    }
  }
  return (
      <FieldWrapper className={className}>
        {componentField}
      </FieldWrapper>
  );
};

export default Field;
