import React, { useState } from 'react';

import { Input, Select, Stack } from '@lapilli-ui/components';
import { __, sprintf } from '@wordpress/i18n';
import parse from 'html-react-parser';

import { wcSettings } from '../../../config';

const DiscountField = ({ id, label, value, onSaveHandler, ...other }) => {
  const [max, setMaxValue] = useState(value.discount_type === 'percentage' ? 100 : '');

  const fixedLabel = sprintf(__('%s - fixed', 'wonder-cart'), wcSettings.currency.symbol);
  const DiscountOpts = [
    { value: 'fixed', label: parse(fixedLabel) },
    {
      value: 'percentage', label: __('% - percentage', 'wonder-cart')
    }
  ];

  const step = value.discount_type === 'percentage' ? 1 : 1 / Math.pow(10, wcSettings.currency.precision);

  const handleChangeDiscountValue = (discountValue) => {
    const canSave = max === '' || (discountValue <= max);
    if (canSave) {
      const e = {
        target: {
          id,
          value: { discount_type: value.discount_type, discount_value: discountValue }
        }
      };

      onSaveHandler(e);
    }
  };
  const handleChangeDiscountType = (discountType) => {
    setMaxValue(discountType === 'percentage' ? 100 : '');
    const e = {
      target: {
        id,
        value: { discount_type: discountType, discount_value: value.discount_value }
      }
    };

    onSaveHandler(e);
  };
  return (
        <>
            <label htmlFor={id}>{label}</label>
            <Stack direction="row" spacing={1}>
                <Input type='number' min={0} max={max} step={step} value={value.discount_value} isMini={true}
                       size='xl' onChange={(_, newValue) => handleChangeDiscountValue(newValue)}/>
                <Select classes={{ root: 'yith-sales-discount-type' }} options={DiscountOpts} value={value.discount_type} size='xl'
                        onChange={handleChangeDiscountType}/>
            </Stack>
        </>
  );
};

export default DiscountField;
