import React from 'react';

import { Box, Input, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import { isEmpty } from '../../../../common/utils/common';

const TextWrapper = styled(Box)((theme) => ({
}));
const TextComponent = ({
  id,
  label,
  value,
  onSaveHandler,
  description = '',
  required = false,
  showError = false,
  errorMessage,
  ...other
}) => {
  const textClassName = showError && required && isEmpty(value) ? 'yith-sales-field-error' : '';
  const typeMessage = errorMessage || __('Please enter a text here ', 'wonder-cart');
  const showNotice = textClassName !== ''
    ? <Typography className="yith-sales-field-error" >{typeMessage}</Typography>
    : '';
  return (
        <TextWrapper className="yith-sales-field-text">
            <label htmlFor={id}>{label}</label>
            <Input className={textClassName} id={id} type="text" value={value} onChange={onSaveHandler} {...other}
                   required={required}/>
            {showNotice}
            {description && <Typography className="field-description">{description}</Typography>}
        </TextWrapper>
  );
};

const Text = React.memo(TextComponent, (prevProps, nextProps) => {
  return prevProps.value === nextProps.value && prevProps.showError === nextProps.showError;
});
export default Text;
