import React from 'react';

import domReady from '@wordpress/dom-ready';
import { render } from '@wordpress/element';

import WonderCart from './wonder-cart';
import {mainAppID} from "./config";

const WONDERID = 'wonder-cart-init';

function loadApp () {
  const renderDOM = document.getElementById(WONDERID);
  if (renderDOM) {
    render(<WonderCart/>, renderDOM);
  } else {
    const observer = new MutationObserver(function (mutations_list) {
      mutations_list.forEach(function (mutation) {
        mutation.addedNodes.forEach(function (added_node) {
          const check = added_node && (added_node.id === 'wonder-cart-init' || ( added_node?.querySelector && added_node.querySelector('#wonder-cart-init')) );

          if (check) {
            render(<WonderCart/>, document.getElementById(WONDERID));
          }
        });
      });
    });

    observer.observe(document.querySelector(mainAppID), { subtree: true, childList: true });
  }
}

domReady(loadApp);
