import React from 'react';

import { Container, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import { Divider } from '../../../../../common/fields';
import Icon from '../../../../../common/icon';
import { formatCurrency } from '../../../../../common/utils/numbers';

const DiscountWrapper = styled(Stack, { shouldForwardProp: (prop) => prop !== 'bgColor' && prop !== 'color' })(({ theme, bgColor, color }) => ({
  '&': {
    marginTop: '5px'
  },
  '& .yith-sales-cart-discount-label': {
    textTransform: 'uppercase'
  },
  '& .MuiTypography-root': {
    fontSize: '14px',
    fontWeight: '500',
    lineHeight: '30px',
    backgroundColor: bgColor,
    paddingLeft: '10px',
    paddingRight: '10px',
    color
  }
}));
const CartDiscount = ({ campaign, step }) => {
  const options = campaign.meta[campaign.type];

  const {
    title,
    discount_to_apply: cartDiscount,
    label_background_color: backgroundColor,
    label_text_color: textColor
  } = options;

  const {
    discount_type: discountType,
    discount_value: discountValue
  } = cartDiscount;
  const cartSubtotal = 1000;
  let amountSaved = discountValue;

  if (discountType === 'percentage') {
    amountSaved = ((cartSubtotal * discountValue) / 100);
  }
  const cartTotal = (cartSubtotal - amountSaved) > 0 ? cartSubtotal - amountSaved : 0;
  const showCustomDiscountLabel = campaign.status === 'publish' || step === 'customization';
  return (
        <Container gutters className="yith-sales-cart-discount-preview-wrapper" sx={{ padding: '50px', backgroundColor: '#fff' }}>
            <Typography sx={{
              textTransform: 'uppercase',
              fontSize: '40px',
              color: '#E4E4E4',
              textAlign: 'center',
              fontWeight: '500',
              letterSpacing: '3.25px'
            }}>Cart</Typography>
            <Stack direction='row' align='center' spacing={1}>
                <Icon icon="PhotoIcon" size='100' sx={{ color: '#E4E4E4', padding: '12px', backgroundColor: '#FBFBFB' }}/>
                <Stack direction='column' spacing={2} align='center' justify='center'>
                    <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                </Stack>
                <Stack direction='column' spacing={2} align='center' justify='center'>
                    <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                </Stack>
            </Stack>
            <Stack direction='row' align='center' spacing={1} sx={{ marginTop: '25px', marginBottom: '45px' }}>
                <Icon icon="PhotoIcon" size='100' sx={{ color: '#E4E4E4', padding: '12px', backgroundColor: '#FBFBFB' }}/>
                <Stack direction='column' spacing={2} align='center' justify='center'>
                    <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                </Stack>
                <Stack direction='column' spacing={2} align='center' justify='center'>
                    <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                </Stack>
            </Stack>
            <Divider sx={{ width: '100%', borderColor: '#E0E0E0', marginBottom: '17px' }}/>
            <Stack direction='row' spacing={2} align='center' justify='end'>
                <Typography sx={{ fontSize: '14px', fontWeight: '500' }}>
                    {__('Cart subtotal', 'wonder-cart')}
                </Typography>
                <Typography sx={{ fontSize: '14px', fontWeight: '500' }}>
                    {formatCurrency(cartSubtotal)}
                </Typography>
            </Stack>
            <DiscountWrapper direction='row' spacing={2} align='center' justify='end' bgColor={ showCustomDiscountLabel ? backgroundColor : '#fff'} color={showCustomDiscountLabel ? textColor : '#000'}>
                <Typography className="yith-sales-cart-discount-label">
                    { !showCustomDiscountLabel && (__('Discount', 'wonder-cart'))}
                    { showCustomDiscountLabel && (title)}
                </Typography>
                <Typography>
                    {formatCurrency(-amountSaved)}
                </Typography>
            </DiscountWrapper>
            <Divider sx={{ width: '100%', borderColor: '#E0E0E0', marginTop: '17px', marginBottom: '17px' }}/>
            <Stack direction='row' spacing={2} align='center' justify='end'>
                <Typography sx={{ fontSize: '18px', fontWeight: '500' }}>
                    {__('Cart total', 'wonder-cart')}
                </Typography>
                <Typography sx={{ fontSize: '18px', fontWeight: '500' }}>
                    {formatCurrency(cartTotal)}
                </Typography>
            </Stack>
        </Container>
  );
};

export default CartDiscount;
