import React from 'react';

import { parse } from '@wordpress/blocks';

import QuantityDiscount from '../../../../../blocks/quantity-discount';
import { getCampaignTitle, isEmpty } from '../../../../../common/utils/common';
import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import BlockEditorPreview from '../../../block-editor-preview';
import PreviewImage from '../../elements/preview-image';
import WizardPreview from '../../elements/wizard-preview';

const QuantityDiscountPreview = ({ campaign, type, showPreview, step }) => {
  const campaignTitle = !isEmpty(campaign) ? getCampaignTitle(campaign) : '';
  const campaignTitleComponent = campaignTitle ? <BlockEditorPreview blocks={parse(campaignTitle)}/> : '';

  return (
        <React.Fragment>
            {showPreview && <PreviewImage fullsize={true} src={getStaticPreviewImageByType(type)} width="800px"/>}
            {!showPreview &&
                <WizardPreview><QuantityDiscount campaign={campaign} campaignTitle={campaignTitleComponent} step={step}
                                                 preview={true} context={'preview'}/></WizardPreview>}
        </React.Fragment>
  );
};

export default QuantityDiscountPreview;
