import React, { useState } from 'react';

import { Box, Button, Switch, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __, _x } from '@wordpress/i18n';

import { getDatePrefix } from '../../../common/utils/date';
import { DATE_FORMAT, TIME_FORMAT, WELL_DONE_ICON_URL } from '../../config';
import { DateTimePicker } from '../fields';

const SchedulingDiv = styled('div')(({ theme }) => ({
  textAlign: 'left',
  marginTop: '31px',
  '& label': {
    display: 'inline-block',
    fontWeight: '500',
    fontSize: '14px',
    lineHeight: '23px',
    marginBottom: '10px'
  },
  '& .yith-sales-datepicker': {
    width: '100%'
  },
  '& .yith-sales-datepicker >div': {
    width: '100%'
  },
  '& .yith-sales-datepicker >div>div': {
    fontSize: '12px'
  },
  '& .yith-sales-datepicker-toggle-label, & .yith-sales-datepicker-toggle-icon': {
    color: 'rgba(0,0,0,0.87)!important'
  },
  '& .yith-sales-last-confirm-set-end-date': {
    marginTop: '15px',
    marginBottom: '15px',
    display: 'flex',
    alignItems: 'center'
  },
  '& .yith-sales-last-confirm-set-end-date label': {
    flex: 1
  },
  '& .yith-sales-last-confirm-button': {
    marginTop: '27px'
  },

  '& .yith-sales-last-confirm-button button': {
    background: theme.palette.primary.main
  },
  '& .lapilliUI-Switch__field': {
    position: 'absolute!important'
  }
}));
const FinalStep = ({ onButtonClick }) => {
  const today = new Date();
  const endOfDay = new Date();
  endOfDay.setHours(23, 59, 59, 999);
  const dateMinFrom = today;
  const [dateFrom, setDateFrom] = useState(today);
  const [dateMaxFrom, setMaxDateFrom] = useState(null);
  const [dateMinTo, setDateMinTo] = useState(endOfDay);
  const [dateTo, setDateTo] = useState(null);
  const [isChecked, setIsChecked] = useState(false);
  const [isScheduled, setIsScheduled] = useState(false);

  let nowPrefix = __('NOW', 'wonder-cart');
  nowPrefix = getDatePrefix(nowPrefix);
  const displayFormat = DATE_FORMAT + ' - ' + TIME_FORMAT;
  const [displayFromFormat, setDisplayFromFormat] = useState(nowPrefix + ' - ' + displayFormat);

  const handleChangeDateFrom = (newDate) => {
    setDateFrom(newDate);

    const minDateTo = newDate;
    minDateTo.setHours(23, 59, 59, 999);
    setDateMinTo(minDateTo);
    setDateTo(minDateTo);

    if (newDate > today) {
      setIsScheduled(true);
      setDisplayFromFormat(displayFormat);
    } else {
      setIsScheduled(false);
      setDisplayFromFormat(nowPrefix + ' - ' + displayFormat);
    }
  };

  const handleChangeDateTo = (newDate) => {
    setDateTo(newDate);
    setMaxDateFrom(newDate);
  };

  const handleCheck = (e, value) => {
    setIsChecked(value);
    if (value) {
      setDateTo(dateMinTo);
    }
  };

  const handleButtonClick = () => {
    const dateFromTimestamp = Date.parse(dateFrom) / 1000;
    const dateToTimestamp = Date.parse(dateTo) / 1000;

    onButtonClick(dateFromTimestamp, dateToTimestamp);
  };

  return (
      <Box sx={{ textAlign: 'center!important', margin: '26px 20px 20px 20px' }}>
        <Box>
          <Box sx={{ width: '64px', margin: '0 auto' }}>
            <img src={WELL_DONE_ICON_URL}/>
          </Box>
            <Typography sx={{ lineHeight: '24px', marginTop: '32px', marginBottom: '6px', fontSize: '20px', fontWeight: '700', textAlign: 'center' }}>
              {_x('Well done!', '[PART OF STRING:Well done!You just created a successful promo!]', 'wonder-cart')}
              <br/>
              {_x('You just created a successful promo!', '[PART OF STRING:Well done!You just created a successful promo!]', 'wonder-cart')}
            </Typography>
        </Box>
        <Box>
          <Typography variant='body2' sx={{ lineHeight: '24px', marginTop: '13px', marginBottom: '6px', fontWeight: '400' }}>
            {__('Now you can activate it with just one click or schedule it using the calendar below.', 'wonder-cart')}
          </Typography>
        </Box>
        <SchedulingDiv>
          <div className="yith-sales-last-confirm-start-date-content">
            <label htmlFor="dateFrom">{__('Promo starts:', 'wonder-cart')}</label>
            <DateTimePicker id="dateFrom" inputFormat="Y-m-d" minDate={dateMinFrom} maxDate={dateMaxFrom} size="lg" value={dateFrom} className="yith-sales-datepicker" onChange={handleChangeDateFrom} displayFormat={displayFromFormat}/>
          </div>
          <div className="yith-sales-last-confirm-set-end-date">
            <label htmlFor="setEndDate">{__('Set the end date', 'wonder-cart')}</label>
            <Switch id="setEndDate" checked={isChecked} size="sm" onChange={handleCheck}/>
          </div>
          {isChecked && (<div className="yith-sales-last-confirm-start-date-content">
                <label htmlFor="dateTo">{__('Promo ends:', 'wonder-cart')}</label>
                <DateTimePicker id="dateTo" inputFormat="Y-m-d" minDate={dateMinTo} size="lg" value={dateTo} className="yith-sales-datepicker" onChange={handleChangeDateTo} displayFormat={displayFromFormat}/>
              </div>
          )
          }
          <div className="yith-sales-last-confirm-button">
            <Button size="lg" fullWidth={true} onClick={handleButtonClick}>
              {isScheduled ? __('Schedule Campaign', 'wonder-cart') : __('Start Campaign Now', 'wonder-cart')}</Button>
          </div>
        </SchedulingDiv>
      </Box>
  );
};

export default FinalStep;
