import React from 'react';

import { Stack } from '@lapilli-ui/components';

import { HorizontalStepperStyled } from '../styles/HorizontalStepper.styled';

const TopNav = ({ onChangeStep, steps, step, enableOnlyTheNext = false, disableOtherSteps = false, editing = true }) => {
  const keySteps = Object.keys(steps);
  let indexCurrent = false;

  return (
      <Stack sx={{ width: '100%', padding: '0 20px', backgroundColor: '#fff', boxShadow: '0px 2px 11px rgba(43, 147, 185,0.11);', position: 'absolute', top: 0, left: 0 }}>
        <Stack sx={{ flex: 1, paddingRight: '50px' }}>
          <HorizontalStepperStyled>
            {keySteps.map((item, index) => {
              if (item === step) {
                indexCurrent = index;
              }

              const itemClass = 'top-nav-item' + (item === step ? '  current' : '');
              let enabled = true;
              if (indexCurrent !== false && !editing) {
                enabled = (index - indexCurrent) <= 1;
              }
              if (enabled && disableOtherSteps) {
                enabled = item == step;
              }

              return <button key={item} onClick={() => onChangeStep(item)} className={itemClass} disabled={!enabled}>{steps[item].menuItem}</button>;
            })}
          </HorizontalStepperStyled>
        </Stack>
      </Stack>
  );
};

export default TopNav;
