import React, { useEffect, useState } from 'react';

import { parse } from '@wordpress/blocks';

import CategoryPromotion from '../../../../../blocks/elements/category-promotion';
import ProductPromotion from '../../../../../blocks/elements/product-promotion';
import { useGetTermsQuery } from '../../../../../common/api/products';
import { useGetProductsQuery } from '../../../../../common/api/yith-products';
import { getPopupOptions } from '../../../../../common/utils/common';
import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import BlockEditorPreview from '../../../block-editor-preview';
import FreeProductInCart from '../../elements/free-product-in-cart';
import ModalPreview from '../../elements/modal-preview';
import PreviewImage from '../../elements/preview-image';

const BuyXGetYPreview = ({ campaign, type, showPreview, step }) => {
  if (showPreview) {
    return (
            <PreviewImage src={getStaticPreviewImageByType(type)}/>
    );
  }
  const options = campaign.meta[campaign.type];

  const {
    title,
    trigger_product: triggerProduct,
    products_to_offer: productToOffer,
    add_in_cart: addFreeProductInCart,
    add_to_cart_button_label: buttonLabel,
    x_y_promo_rule: promoRule
  } = options;
  let productToShow;

  if (productToOffer.type === 'same') {
    productToShow = triggerProduct;
  } else {
    productToShow = productToOffer;
  }
  const [data, setData] = useState([]);
  const { data: dataCatX, isFetching: isFetchingCatX, isLoading: isLoadingCatX } = useGetTermsQuery({ termType: productToShow.type, termIds: productToShow.ids, fetchWithEmptyTerms: false }, { skip: productToShow.type !== 'categories' });
  const { data: dataProdX, isFetching: isFetchingProdX, isLoading: isLoadingProdX } = useGetProductsQuery({ termType: productToShow.type, termIds: productToShow.ids, fetchWithEmptyTerms: false }, { skip: productToShow.type !== 'products' });
  const isFetching = isFetchingCatX || isFetchingProdX;

  useEffect(() => {
    if (productToShow.type === 'categories' && dataCatX) {
      setData(dataCatX);
    } else if (productToShow.type === 'products' && dataProdX) {
      setData(dataProdX);
    }
  }, [dataCatX, dataProdX]);

  if (isFetching) return;
  const content = <BlockEditorPreview blocks={parse(title)}/>;
  const productListOPT = {
    discountValue: promoRule.discount_value,
    discountType: promoRule.discount_type,
    showProductName: 'yes',
    showProductPrice: 'yes',
    showSaving: 'no',
    savingColor: false,
    applyDiscount: 'yes',
    addToCartLabel: buttonLabel,
    productType: productToShow.type
  };

  const checkFirstCase = (triggerProduct.type === 'products' && (productToShow.type === 'same' || productToShow.type === 'products'));
  const checkSecondCase = (triggerProduct.type !== 'products' && productToShow.type === 'products');
  let showFreeCart = ((checkFirstCase || checkSecondCase) && promoRule.discount_type === 'percentage' && promoRule.discount_value === 100);

  if (showFreeCart) {
    showFreeCart = addFreeProductInCart === 'yes';
  }
  const popupOptions = getPopupOptions(options);
  return (
        <>
            {showFreeCart
              ? (
                    <FreeProductInCart
                        productToShow={productToShow}
                        showPrice={true}/>
                )
              : (
                    <>
                      <ModalPreview {...popupOptions} maxWidth={'490px'} type='buy-x-get-y'>
                        {
                          productToShow.type === 'categories'
                            ? (
                                  <CategoryPromotion data={data} content={content} buttonLabel={buttonLabel} />
                              )
                            : (
                                  <ProductPromotion data={data} content={content} productOptions={productListOPT}/>
                              )
                        }
                      </ModalPreview>
                    </>
                )
            }
        </>

  );
};

export default BuyXGetYPreview;
