import React from 'react';

import { Box, RadioGroup } from '@lapilli-ui/components';

const RadioButtonGroupComponent = ({ id, label, options, value, onSaveHandler, ...other }) => {
  const handleChange = (el, value) => {
    const e = {
      target: {
        id,
        value
      }
    };

    onSaveHandler(e);
  };

  return (
      <Box className='yith-sales-field-radiobuttongroup'>
          {label && <label htmlFor={id}>{label}</label>}
          <RadioGroup id={id} options={options} value={value} onChange={handleChange} {...other}/>
      </Box>
  );
};

const RadioButtonGroup = React.memo(RadioButtonGroupComponent, (prevProps, nextProps) => {
  return prevProps.value === nextProps.value;
});
export default RadioButtonGroup;
