import React from 'react';

import { Container, Stack, Typography } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';

import FreeShippingBanner from '../../../../../blocks/free-shipping-banner';
import { Divider } from '../../../../../common/fields';
import Icon from '../../../../../common/icon';
import { formatCurrency } from '../../../../../common/utils/numbers';

const FreeShipping = ({ campaign }) => {
  const options = campaign.meta[campaign.type];
  const {
    title,
    show_amount_left: showAmountLeft,
    notice_background_color: noticeBackgroundColor,
    free_shipping_rules: freeShippingRules
  } = options;
  const hasRuleSet = freeShippingRules?.rules && freeShippingRules.rules.length > 0;
  const cartSubTotal = showAmountLeft === 'yes' ? 150 : 180;
  const cartTotal = 180;
  const shippingValue = showAmountLeft === 'yes' ? 12.99 : false;
  const bannerText = title.replace('%value%', formatCurrency(30));
  return (
        <Container gutters className="yith-sales-free-shipping-preview-wrapper"
                   sx={{ backgroundColor: '#fff', maxWidth: '660px!important', padding: '45px' }} maxWidth={false}>
            { showAmountLeft === 'yes' && (
                <FreeShippingBanner text={bannerText} bgColor={noticeBackgroundColor}/>
            )}
            <Typography sx={{
              textTransform: 'uppercase',
              fontSize: '40px',
              color: '#E4E4E4',
              textAlign: 'center',
              fontWeight: '500',
              letterSpacing: '3.25px'
            }}>Cart</Typography>
            <Stack direction='column' spacing={2}>
                <Stack direction='row' spacing={2} align='center' justify='center'>
                    <Icon icon="PhotoIcon" size='100'
                          sx={{ color: '#E4E4E4', padding: '12px', backgroundColor: '#FBFBFB' }}/>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                </Stack>
                <Stack direction='row' spacing={2} align='center' justify='center'>
                    <Icon icon="PhotoIcon" size='100'
                          sx={{ color: '#E4E4E4', padding: '12px', backgroundColor: '#FBFBFB' }}/>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '300px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                    <Stack direction='column' spacing={2} align='center' justify='center'>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        <Divider sx={{ width: '70px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                    </Stack>
                </Stack>
            </Stack>
            <Divider sx={{ width: '100%%', margin: '40px auto', borderColor: '#E0E0E0' }}/>
            <Stack direction='column' spacing={2} align='flex-end' justify='flex-end'>
                {!hasRuleSet
                  ? (
                        <>
                            <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                            <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                            <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        </>
                    )
                  : (
                        <>
                            <Stack direction='row' spacing={2} align='center' justify='end'>
                                <Typography sx={{ fontSize: '14px', fontWeight: '500' }}>
                                    {__('Cart subtotal', 'wonder-cart')}
                                </Typography>
                                <Typography sx={{ fontSize: '14px', fontWeight: '500' }}>
                                    {formatCurrency(cartSubTotal)}
                                </Typography>
                            </Stack>
                            <Stack direction='row' spacing={2} align='center' justify='end'>
                                <Typography sx={{ fontSize: '14px', fontWeight: '500' }}>
                                    {__('Shipping', 'wonder-cart')}
                                </Typography>
                                { shippingValue
                                  ? (
                                    <Typography sx={{ fontSize: '14px', fontWeight: '500' }}>
                                        {formatCurrency(shippingValue)}
                                    </Typography>
                                    )
                                  : (
                                <Typography sx={{ fontSize: '14px', fontWeight: '500', color: '#938D00' }}>
                                    {__('FREE', 'wonder-cart')}
                                </Typography>
                                    )
                                }
                            </Stack>
                        </>
                    )
                }
            </Stack>
            <Divider sx={{ width: '100%', margin: '40px auto', borderColor: '#E0E0E0' }}/>
            <Stack direction='column' spacing={1} align='flex-end' sx={{ marginTop: '26px' }}>
                {
                    !hasRuleSet
                      ? (
                <Divider sx={{ width: '200px', borderColor: '#E0E0E0', borderBottomWidth: 'thick' }}/>
                        )
                      : (
                            <Stack direction='row' spacing={2} align='center' justify='end'>
                                <Typography sx={{ fontSize: '16px', fontWeight: '500' }}>
                                    {__('Cart total', 'wonder-cart')}
                                </Typography>
                                <Typography sx={{ fontSize: '16px', fontWeight: '500' }}>
                                    {formatCurrency(cartTotal)}
                                </Typography>
                            </Stack>
                        )
                }
            </Stack>
        </Container>
  );
};

export default FreeShipping;
