import React from 'react';

import { getPopupOptions, isEmpty } from '../../../../../common/utils/common';
import { getProductsToShow } from '../../../../../common/utils/data';
import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import ModalPreview from '../../elements/modal-preview';
import PreviewImage from '../../elements/preview-image';

import Upsell from './index';

const UpsellPreview = ({ campaign, type, showPreview, step }) => {
  let options = {};
  let popupOptions = {};
  let inFetching = true;
  let skipMaxNumber = true;
  let isSingle = true;
  let maxWidth = '336px';
  let showContent = false;
  let numProductToShow = -1;
  let products = [];
  if (!isEmpty(campaign)) {
    options = campaign.meta[campaign.type];
    showContent = campaign.status === 'publish' || step !== 'trigger';
    const { data, isLoading, isFetching } = getProductsToShow(options.products_to_show, options.trigger_product);
    skipMaxNumber = options.products_to_show.type === 'products';
    numProductToShow = options.num_products_to_show;
    inFetching = isLoading || isFetching;
    isSingle = (!data || (skipMaxNumber && data.length < 2) || (!skipMaxNumber && (parseInt(options.num_products_to_show) === 1 || data.length < 2)));
    products = data;
    if (!isSingle) {
      maxWidth = '490px';
    }
    popupOptions = getPopupOptions(options);
  }

  return (
      <React.Fragment>
        {showPreview && (<PreviewImage src={getStaticPreviewImageByType(type)} hasBgColor={false}/>)}
        {!showPreview &&
            <ModalPreview type={campaign.type} {...popupOptions} maxWidth={maxWidth}>
                <Upsell campaign={campaign} options={options} products={products} isSingle={isSingle} inFetching={inFetching} showContent={showContent} numProductToShow={ skipMaxNumber ? -1 : numProductToShow} step={step} preview={true}/>
            </ModalPreview>
        }
      </React.Fragment>
  );
};

export default UpsellPreview;
