import React from 'react';

import { Box, IconButton, Input } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { _x } from '@wordpress/i18n';

import Icon from '../../../../common/icon';

const DimensionsFieldWrapper = styled('div')(({ theme }) => ({
  position: 'relative',
  '& button': {
    border: 'none',
    backgroundColor: '#fff'
  },
  '& .yith-sales-dimensions__linked': {
    position: 'absolute',
    top: '2px',
    right: 0
  },
  '& .yith-sales-icon--linkicon:hover': {
    cursor: 'pointer'
  },
  '& .yith-sales-icon--linkicon': {
    color: '#2E3A59',
    transform: 'rotate(45deg)'
  },
  '&.yith-sales-dimensions--linked-active button': {
    backgroundColor: '#EDF7FF'
  },
  '&.yith-sales-dimensions--linked-active .yith-sales-icon--linkicon': {
    color: '#3D8FD0'
  }
}));

const DimensionsListWrapper = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'flex-end'
}));

const DimensionWrapper = styled('div')(({ theme }) => ({
  flex: 1,
  textAlign: 'center',
  '& div div': {
    borderRadius: 0,
    border: '1px solid #E0E0E0',
    borderLeft: 'none'
  },
  '&:first-of-type div div': {
    borderLeft: '1px solid #E0E0E0 !important',
    borderRadius: '7px 0 0 7px'
  },
  '&:last-child div div': {
    borderRadius: '0 7px 7px 0'
  },
  '& input': {
    width: '100% !important'
  },

  '& label': {
    textTransform: 'uppercase',
    letterSpacing: '1px',
    fontSize: ' 9px !important',
    display: 'inline-block',
    float: 'none',
    margiBottom: '5px !important',
    width: 'auto',
    padding: 0
  }
}));
const Dimensions = ({ id, label, value, dimensions, allowLinked, min, max, showLabel, onSaveHandler, ...other }) => {
  const isLinked = (value?.linked && value.linked === 'yes');

  const handleChange = (e) => {
    const idInput = e.target.name;
    const newValue = e.target.value;

    const dimension = {};

    if (allowLinked && isLinked) {
      Object.keys(dimensions).map((key, i) => {
        dimension[key] = newValue;
      });
    } else {
      Object.keys(dimensions).map((key, i) => {
        if (idInput === key) {
          dimension[key] = newValue;
        } else {
          dimension[key] = (value?.dimension && value.dimension[key]) ? value.dimension[key] : 0;
        }
      });
    }
    const el = {
      target: {
        id,
        value: {
          dimension,
          linked: value?.linked ? value.linked : 'no'
        }
      }
    };

    onSaveHandler(el);
  };

  const handleClickLinks = (isLinked) => {
    isLinked = !isLinked;
    const newLinked = isLinked ? 'yes' : 'no';

    let dimension = {};
    if (isLinked) {
      const keys = Object.keys(dimensions);
      const newValue = (value?.dimension && value.dimension[keys[0]]) ? value.dimension[keys[0]] : 0;

      keys.map((key, i) => {
        dimension[key] = newValue;
      });
    } else {
      dimension = value.dimension;
    }
    const e = {
      target: {
        id,
        value: {
          dimension,
          linked: newLinked
        }
      }
    };
    onSaveHandler(e);
  };
  return (
        <DimensionsFieldWrapper className={`yith-sales-dimensions ${isLinked ? 'yith-sales-dimensions--linked-active' : ''}`}>
            <Box className="yith-sales-dimensions-header" sx={{ marginBottom: '5px' }}>
              <label>{label}</label>
            {allowLinked && (
                <div className="yith-sales-dimensions__linked"
                     title={ isLinked ? _x('Linked', 'Tooltip in the " Dimensions" field', 'wonder-cart') : _x('Unlinked', 'Tooltip in the " Dimensions" field', 'wonder-cart')}>
                    <IconButton size='sm' onClick={() => handleClickLinks(isLinked)}>
                      {<Icon icon="LinkIcon" size="16"/>}
                    </IconButton>
                </div>
            )
            }
            </Box>
            <DimensionsListWrapper className="yith-sales-dimensions__dimensions">
                {
                    Object.keys(dimensions).map((key, i) => {
                      let currentValue = 0;
                      if (value?.dimension && value.dimension[key]) {
                        currentValue = value.dimension[key];
                      }
                      return (
                            <DimensionWrapper key={key} className={`yith-sales-dimensions__dimension yith-sales-dimensions__dimension--${key}`}>
                                {showLabel === 'yes' && (<label htmlFor={`${id}-dimension-${key}`}>{dimensions[key]}</label>)}
                                <Input key={`${id}-dimension-${key}`} id={`${id}-dimension-${key}`} name={key} type="number" value={currentValue}
                                       min={min} max={max} step="1" {...other} onChange={handleChange}/>
                            </DimensionWrapper>
                      );
                    })
                }
            </DimensionsListWrapper>
        </DimensionsFieldWrapper>
  );
};

Dimensions.defaultProps = {
  dimensions: {
    top: _x('Top', 'Position in the "Dimensions" field', 'wonder-cart'),
    right: _x('Right', 'Position in the "Dimensions" field', 'wonder-cart'),
    bottom: _x('Bottom', 'Position in the "Dimensions" field', 'wonder-cart'),
    left: _x('Left', 'Position in the "Dimensions" field', 'wonder-cart')
  },
  allowLinked: true,
  min: '',
  max: ''
};
export default Dimensions;
