import { createSlice } from '@reduxjs/toolkit';

import { unserializeMeta } from '../../../common/utils/common';
import { CAMPAIGN_TYPES } from '../../config';
import { getCampaignIdByURL, getCampaignStepByURL, getCampaignTypeByURL } from '../../utils/admin-functions';

const initialState = {
  id: getCampaignIdByURL(),
  type: getCampaignTypeByURL(),
  step: getCampaignStepByURL(),
  campaign: {}, // wp post type,
  autoSave: false,
  updated: false,
  disableNext: false,
  clickedNext: false
};

const campaignSlice = createSlice({
  name: 'campaign',
  initialState,
  reducers: {
    resetEditor: (state, action) => {
      state.step = 'trigger';
      state.id = 'new';
      state.type = CAMPAIGN_TYPES[0].id;
      state.autoSave = false;
      state.campaign = {};
      state.disableNext = false;
    },
    setCampaignId: (state, action) => {
      if (state.id !== action.payload) {
        state.id = action.payload;
      }
    },
    setCampaignType: (state, action) => {
      state.type = action.payload;
    },
    setCampaignStep: (state, action) => {
      state.step = action.payload;
      state.disableNext = false;
    },
    setCampaignObject: (state, action) => {
      state.campaign = action.payload;
      state.campaign.meta = unserializeMeta(state.campaign.meta);
    },
    setClickedNext: (state, action) => {
      state.clickedNext = action.payload;
    },
    updateCampaign: (state, action) => {
      const keysToUpdate = Object.keys(action.payload);
      keysToUpdate.forEach(key => {
        if (state[key] !== action.payload[key]) {
          state[key] = action.payload[key];
        }
      });
    },
    updateMetaCampaign: (state, action) => {
      const { metaKey, metaValue } = action.payload;
      const metaKeys = Object.keys(state.campaign.meta[state.campaign.type]);
      if (metaKeys.indexOf(metaKey) > 0) {
        const oldValue = state.campaign.meta[state.campaign.type][metaKey];
        if (oldValue !== metaValue) {
          state.campaign.meta[state.campaign.type][metaKey] = metaValue;
        }
      } else {
        state.campaign.meta[state.campaign.type][metaKey] = metaValue;
      }
    }

  }
});

export const {
  resetEditor,
  setCampaignId,
  setCampaignType,
  setCampaignStep,
  setCampaignObject,
  setClickedNext,
  updateCampaign,
  updateMetaCampaign
} = campaignSlice.actions;

export default campaignSlice.reducer;
