import React from 'react';

import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import PreviewImage from '../../elements/preview-image';
import WizardPreview from '../../elements/wizard-preview';

import FrequentlyBoughtTogether from './index';

const FBTPreview = ({ campaign, type, showPreview }) => {
  return (
      <React.Fragment>
        {showPreview && (<PreviewImage fullsize={true} src={getStaticPreviewImageByType(type)}/>)}
        {!showPreview && <WizardPreview><FrequentlyBoughtTogether campaign={campaign}/></WizardPreview>}
      </React.Fragment>
  );
};

export default FBTPreview;
