import React from 'react';

import { Box } from '@lapilli-ui/components';

import SimpleModal from '../../../../../blocks/elements/simple-modal';

const ModalPreview = ({
  type,
  maxWidth,
  isFullWidth,
  iconColor,
  bgColor,
  borderRadius,
  animation,
  position,
  bgImageConfig,
  children
}) => {
  return (
      <Box sx={{ pointerEvents: 'none' }} className={`yith-sales-modal-preview yith-sales-modal-preview--${type}`}>
        <SimpleModal
            open={true}
            disablePortal={true}
            context="frontend"
            isFullWidth={isFullWidth}
            maxWidth={maxWidth}
            iconColor={iconColor}
            bgColor={bgColor}
            borderRadius={!isFullWidth ? borderRadius : undefined}
            bgImageConfig={bgImageConfig}
            animation={animation}
            position={position}
        >
          {children}
        </SimpleModal>
      </Box>
  );
};

export default ModalPreview;
