import React from 'react';

import { getPopupOptions, isEmpty } from '../../../../../common/utils/common';
import { getProductsToShow } from '../../../../../common/utils/data';
import { getStaticPreviewImageByType } from '../../../../utils/admin-functions';
import ModalPreview from '../../elements/modal-preview';
import PreviewImage from '../../elements/preview-image';

import LastDeal from './index';

const LastDealPreview = ({ campaign, type, showPreview, step }) => {
  let options = {};
  let inFetching = true;
  let skipMaxNumber = true;
  let isSingle = true;
  let maxWidth = '336px';
  let showContent = false;
  let numProductToShow = -1;
  let products = [];
  let popupOptions = {};
  if (!isEmpty(campaign)) {
    options = campaign.meta[campaign.type];
    showContent = campaign.status === 'publish' || step !== 'trigger';
    const { data, isLoading, isFetching } = getProductsToShow(options.products_to_offer, options.trigger_product);
    skipMaxNumber = options.products_to_offer.type === 'products';
    numProductToShow = options.num_products_to_show;
    inFetching = isLoading || isFetching;
    isSingle = (!data || (skipMaxNumber && data.length < 2) || (!skipMaxNumber && (parseInt(numProductToShow) === 1 || data.length < 2)));
    products = data;
    if (!isSingle) {
      maxWidth = '490px';
    }
    popupOptions = getPopupOptions(options);
  }

  return (
      <React.Fragment>
        {showPreview && (<PreviewImage src={getStaticPreviewImageByType(type)} hasBgColor={false}/>)}
        {!showPreview &&
           <ModalPreview {...popupOptions} maxWidth={maxWidth} type="last-deal"><LastDeal options={options} imageWidth = { maxWidth} inFetching={inFetching} isSingle={isSingle} products={products} numProductToShow={numProductToShow} showContent={showContent} step={step}/></ModalPreview>
        }
      </React.Fragment>
  );
};

export default LastDealPreview;
