import React from 'react';

import { Spinner, Stack } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { parse } from '@wordpress/blocks';

import ProductList from '../../../../../blocks/elements/product-list';
import ProductSingleItem from '../../../../../blocks/elements/product-single-item';
import BlockEditorPreview from '../../../block-editor-preview';

const GiftHeader = styled(Stack)(({ theme }) => ({
  '& .yith-sales-gift-icon': {
    width: '50px',
    height: '50px',
    backgroundImage: 'url(' + theme.gift.giftIcon + ')',
    backgroundSize: 'contain'
  }
}));
const GiftOnCart = ({ options, products, inFetching, isSingle, showProductName, numProductToShow, maxProductToSelect }) => {
  const {
    title
  } = options;

  const productListOPT = {
    checked_by_default: 'no',
    discountValue: '100',
    discountType: 'percentage',
    showProductName: showProductName && options.show_product_name === 'yes' ? 'yes' : 'no',
    showProductPrice: options.show_product_price,
    showSaving: 'no',
    savingColor: '',
    applyDiscount: 'yes',
    addToCartLabel: options.add_to_cart_button_label
  };

  return (
      <Stack className="yith-sales-gift-on-cart-preview-wrapper" direction='column' align='center' spacing={2} >
        <GiftHeader direction={isSingle ? 'column' : 'row'} align='center' spacing={1} sx={{ width: isSingle ? 'auto' : '100%' }}>
          <div className='yith-sales-gift-icon'></div>
          <BlockEditorPreview blocks={parse(title)}/>
        </GiftHeader>
        { inFetching && (<Spinner sx={{ marginLeft: '50%', marginTop: '20px', marginBottom: '20px' }}/>)}
        {
            !inFetching && isSingle && (
                <ProductSingleItem data={products} productOpts={productListOPT} showFreeLabel={true}/>
            )
        }
        {
          !inFetching && !isSingle && (
                <ProductList products={products} productOpts={productListOPT} showFreeLabel ={true} showTotals={false} numProductToShow={numProductToShow} showNoThanks={true} maxProductToSelect={maxProductToSelect}/>
          )
        }
      </Stack>
  );
};

export default GiftOnCart;
