import React, { createContext, useContext, useState } from 'react';

const ViewModeContext = createContext({});
export const ViewModeProvider = ({ children }) => {
  const [viewMode, setViewMode] = useState(sessionStorage.getItem('wondercartViewMode') === 'list' ? 'list' : 'grid');
  const updateViewMode = (value) => {
    setViewMode(value);
    sessionStorage.setItem('wondercartViewMode', value);
  };

  return <ViewModeContext.Provider value={{ viewMode, setViewMode: updateViewMode }}>{children}</ViewModeContext.Provider>;
};

export const useViewMode = () => {
  const context = useContext(ViewModeContext);

  return context;
};
