import React, { useEffect, useRef } from 'react';

import { Grid, GridItem, Spinner, Stack } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';
import { useDispatch, useSelector } from 'react-redux';

import CampaignItems from './campaign-items';
import CampaignItemsEmptyState from './campaign-items-empty-state';
import {
  useCloneCampaignMutation,
  useDeleteCampaignMutation,
  useFetchCampaignsQuery, useGetCampaignStatisticsQuery
} from '../../../common/api/campaign';
import { setCampaignId, setCampaignType } from '../../slices/editor/campaign-slice';
import { addNotice, updateNotice } from '../../slices/view/notices-slice';
import {
  resetCampaignView,
  setCampaign,
  setCampaignsView,
  setEndpoint,
  setStatistics
} from '../../slices/view/view-campaigns-list';
import { useViewMode } from '../../utils/view-mode-context';
import CampaignEmptyItem from '../campaign-item/campaign-empty-item';
import ViewMore from '../campaign-list-view/view-more';
import Notices from '../notices';

const CampaignListView = ({
  newCampaignLabel,
  emptyStateHeader,
  emptyStateSubheader,
  forceListMode = false
}) => {
  const notices = useSelector((state) => state.notices.notices);
  const { viewMode } = useViewMode();
  const scrollToRef = useRef();

  const {
    campaigns: currentCampaigns,
    status: currentStatus,
    type: currentMenu,
    totalPages,
    currentPage,
    loadedPages,
    statistics
  } = useSelector((state) => state.campaignsView);

  const next = loadedPages > 0 && totalPages === loadedPages ? 0 : loadedPages + 1;
  const { data: campaigns, isFetching } = useFetchCampaignsQuery({
    campaignType: currentMenu,
    campaign_status: currentStatus,
    skip: next === 0,
    page: next
  });
  const { data: currentStatistics } = useGetCampaignStatisticsQuery({}, { refetchOnMountOrArgChange: true });
  const [deleteCampaign] = useDeleteCampaignMutation();
  const [cloneCampaign] = useCloneCampaignMutation();
  const dispatch = useDispatch();
  const currentView = forceListMode ? 'list' : viewMode;
  /* Set query string filters */
  useEffect(() => {
    return () => {
      clearNotices();
    };
  }, []);

  /* Load in background the campaigns */
  useEffect(() => {
    if (!isFetching) {
      let newCampaigns = [];
      let newTotalPages = 0;
      if (currentCampaigns.length === 0 && loadedPages === 0 && campaigns && campaigns.length > 1) {
        // FIRST PAGE
        newCampaigns = campaigns.filter(campaign => typeof campaign.id !== 'undefined');
        const calculator = campaigns.filter(campaign => typeof campaign.id === 'undefined');
        const { totalPages, totalCount } = calculator[0];
        newTotalPages = totalPages;
        dispatch(setCampaignsView({
          campaigns: newCampaigns,
          totalPages: newTotalPages,
          totalCount,
          loadedPages: 1
        }));
      } else if (loadedPages > 0 && campaigns.length > 1 && (totalPages - loadedPages) > 0) {
        // OTHER PAGES
        newCampaigns = [...currentCampaigns, ...(campaigns.filter(campaign => typeof campaign.id !== 'undefined'))];

        dispatch(setCampaignsView({ campaigns: newCampaigns, loadedPages: loadedPages + 1 }));
      }
    }
  });

  useEffect(() => {
    dispatch(setStatistics(currentStatistics));
  }, [currentStatistics]);

  /* Scroll down to the new element */
  useEffect(() => {
    if (scrollToRef.current) {
      const bodyRect = document.body.getBoundingClientRect();
      const elemRect = scrollToRef.current.getBoundingClientRect();
      const offset = elemRect.top - bodyRect.top;
      window.scrollTo({ top: offset, behavior: 'smooth' });
    }
  });

  const duplicateItem = async (itemId) => {
    await cloneCampaign({ id: itemId }).then((result) => {
      dispatch(resetCampaignView({}));
    });
  };

  const handleUpdateCampaign = campaignUpdated => {
    dispatch(setCampaign(campaignUpdated));
  };

  const handleDeleteCampaign = campaignId => {
    const newCampaigns = currentCampaigns.filter(campaign => {
      if (campaign.id !== campaignId) {
        return campaign;
      }
    });

    dispatch(setCampaignsView({ campaigns: newCampaigns }));
    dispatch(addNotice({
      severity: 'success',
      message: __('Campaign deleted', 'wonder-cart')
    }));
    deleteCampaign({ id: campaignId });
  };

  const nextPage = () => {
    dispatch(setCampaignsView({ currentPage: currentPage + 1 }));
  };

  const clearNotices = () => {
    dispatch(updateNotice([]));
  };

  const LoadingNoResult = () => {
    return (
            <>
                {isFetching && <Spinner sx={{ display: 'block', margin: '50px auto' }}/>}
            </>
    );
  };
  const handleOpenNewCampaign = () => {
    if (currentMenu === 'all') {
      dispatch(setEndpoint('new'));
    } else {
      dispatch(setEndpoint('edit'));
      dispatch(setCampaignId('new'));
      dispatch(setCampaignType(currentMenu));
    }
  };
  const handleViewCampaign = () => {
    dispatch(setCampaignsView({
      campaigns: [],
      status: '',
      totalPages: 0,
      totalCount: 0,
      currentPage: 1,
      loadedPages: 0
    }));
  };
  if (isFetching) {
    return (
            <>
                {LoadingNoResult()}
            </>
    );
  }
  if (!isFetching && !currentCampaigns.length) {
    let header = emptyStateHeader;
    let subHeader = emptyStateSubheader;
    let btnLabel = newCampaignLabel;
    let btnAction = handleOpenNewCampaign;
    if (currentStatus && statistics) {
      let totals = 0;
      Object.keys(statistics).forEach(index => {
        if (index === currentMenu || currentMenu === 'all') {
          totals += statistics[index].total;
        }
      });
      if (totals > 0) {
        header = __('No campaigns found', 'wonder-cart');
        subHeader = __('Try changing the filter view or', 'wonder-cart');
        btnLabel = __('View all campaigns', 'wonder-cart');
        btnAction = handleViewCampaign;
      }
    }
    if (currentMenu === 'all') {

    }
    return (
          <CampaignItemsEmptyState header={header} subheader={subHeader}
                                   ctaLabel={btnLabel} ctaAction={btnAction}/>
    );
  }
  return (
        <>
            <Notices notices={notices} onEnd={clearNotices}/>
            {currentView === 'list' &&
                <Stack direction="column">
                    <CampaignEmptyItem viewMode={currentView} openNewCampaign={handleOpenNewCampaign}
                                       ctaLabel={newCampaignLabel}/>
                    {currentCampaigns.length &&
                        (
                            <>
                                <CampaignItems campaigns={currentCampaigns} currentPage={currentPage}
                                               scrollToRef={scrollToRef} viewMode={currentView}
                                               duplicateItem={duplicateItem} handleDeleteCampaign={handleDeleteCampaign}
                                               handleUpdateCampaign={handleUpdateCampaign} notices={notices}/>
                            </>
                        )
                    }

                </Stack>
            }

            {currentView === 'grid' &&
                <Grid gap={2.5} columns={12} sx={{ marginTop: '30px', gridAutoRows: '1fr' }}>
                    <GridItem xs={12} sm={6} md={4} lg={4}>
                        <CampaignEmptyItem viewMode={currentView} openNewCampaign={handleOpenNewCampaign}
                                           ctaLabel={newCampaignLabel}/>
                    </GridItem>
                    {!currentCampaigns.length && <GridItem xs={12} sm={6} md={4} lg={4}><LoadingNoResult/></GridItem>}
                    <CampaignItems campaigns={currentCampaigns} currentPage={currentPage} scrollToRef={scrollToRef}
                                   viewMode={currentView} duplicateItem={duplicateItem}
                                   handleDeleteCampaign={handleDeleteCampaign}
                                   handleUpdateCampaign={handleUpdateCampaign} notices={notices}/>
                </Grid>
            }
          {(totalPages > currentPage) && <ViewMore viewMoreAction={nextPage}
                                                   labelButton={__('Show more', 'wonder-cart')}/>}
        </>
  );
};

export default CampaignListView;
