import React, { useEffect, useState } from 'react';

import { styled } from '@lapilli-ui/styles';
import { useDispatch, useSelector } from 'react-redux';

import { VerticalScrollableContentStyled } from '../../../blocks/elements/styles/VerticalScrollableContent.styled';
import { Divider } from '../../../common/fields';
import { CAMPAIGN_TYPES } from '../../config';
import { updateCampaign, updateMetaCampaign } from '../../slices/editor/campaign-slice';
import { getToolbarOptions } from '../../toolbars';
import { componentIsVisible } from '../../utils/admin-functions';
import Field from '../field';

const ToolBarWrapper = styled('div')(({ theme }) => ({
  '&': {
    padding: '40px 20px 40px 40px',
    backgroundColor: '#fff',
    flex: 1
  },
  '& .yith-sales-discount-type':{
    flex: 1
  },
  '& .yith-sales-field-error .lapilliUI-Input__backdrop':{
    borderColor: theme.palette.error.main + '!important'
  },
  '& .yith-sales-field-error .lapilliUI-Select__toggle':{
    borderColor: theme.palette.error.main + '!important'
  },
  '& .field-description': {
    fontSize: theme.typography.body3.fontSize,
    margin: '10px 0',
    color: theme.palette.text.secondary
  }
}));

const ToolBarTitle = styled('h2')(({ theme }) => ({
  color: theme.palette.primary.light,
  fontSize: '15px',
  fontWeight: 600,
  lineHeight: '23px',
  margin: '0 0 18px 0',
  padding: 0
}));

const ToolBarStepName = styled('h3')(({ theme }) => ({
  color: theme.palette.primary.main,
  fontSize: '14px',
  fontWeight: 500,
  lineHeight: '16px',
  letterSpacing: '0.5px',
  margin: '38px 0 24px 0',
  textTransform: 'uppercase',
  padding: 0
}));

const Toolbar = ({ showError = false }) => {
  const windowGap = 330;
  const currentCampaign = useSelector((state) => state.campaign);

  const currentToolbar = getToolbarOptions(currentCampaign.type);
  const meta = currentCampaign.campaign.meta[currentCampaign.type];
  const options = currentToolbar && typeof currentToolbar[currentCampaign.step] !== 'undefined' ? currentToolbar[currentCampaign.step] : false;

  const dispatch = useDispatch();
  const [verticalScroll, setVerticalScroll] = useState(window.innerHeight - windowGap);

  const handleResize = () => {
    setVerticalScroll(window.innerHeight - windowGap);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  if (!options) {
    return;
  }

  const typeObj = CAMPAIGN_TYPES.filter(
    campaignType => campaignType.id === currentCampaign.type);

  const handleSave = (e) => {
    // added this check to avoid to show the error notice when select term change to another option
    const currentError = e?.error ? e.error : false;
    dispatch(updateCampaign({ updated: true, clickedNext: e?.error, disableNext: currentError }));
    dispatch(updateMetaCampaign({ metaKey: e.target.id, metaValue: e.target.value }));
  };

  return (
        <ToolBarWrapper>
            <ToolBarTitle>{typeObj[0].name}</ToolBarTitle>
            <Divider/>
            <ToolBarStepName>{options.title}</ToolBarStepName>
            <VerticalScrollableContentStyled id="vertical-scroll" maxHeight={`${verticalScroll}px`}>
                {options.fields && options.fields.map(field => {
                  if (field.type === 'toggle') {
                    const subFields = field.fields;
                    field.value = [];

                    Object.keys(subFields).map((key, i) => {
                      const subField = subFields[key];
                      if (subField.type === 'inline-fields') {
                        const inlineFields = [];
                        for (const index in subField.fields) {
                          const inlineField = subField.fields[index];
                          inlineFields[index] = {
                            ...inlineField,
                            isVisible: componentIsVisible(inlineField, meta)
                          };
                        }
                        const inlineValues = {};
                        inlineFields.map(inlineField => {
                          inlineValues[inlineField.id] = meta[inlineField.id] ?? '';
                        });

                        field.value[subField.id] = inlineValues;
                        field.fields[key].fields = inlineFields;
                      } else {
                        field.value[subField.id] = meta[subField.id] ?? '';
                        if (subField.type === 'textEditor' && subField?.backgroundColorMeta) {
                          subField.editorStyles = {
                            backgroundColor: meta[subField.backgroundColorMeta]
                          };
                          field.fields[key] = subField;
                        }
                      }

                      field.fields[key] = { ...field.fields[key], isVisible: componentIsVisible(subField, meta) };
                    });
                  } else {
                    field.value = meta[field.id] ?? '';
                  }
                  if (!field?.style) {
                    field.style = {};
                  }
                  field.isVisible = componentIsVisible(field, meta);

                  return (
                        <div className="editor-field-wrapper" id={field.id} key={field.id}>
                            {<Field field={field} onSave={handleSave} {...field.style} showError={showError}/>}
                        </div>
                  );
                })
                }
            </VerticalScrollableContentStyled>
        </ToolBarWrapper>
  );
};

export default Toolbar;
