import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

import { yithSalesApiFetch } from '../basic-fetch';

const { root } = window.wpApiSettings;
const restBase = 'settings';
const versionString = 'yith_sales/v1';

export const apiSettingsSlice = createApi({
  reducerPath: 'apiSettings',
  tagTypes: ['Settings'],
  baseQuery: fetchBaseQuery({
    baseUrl: '/yith_sales/v1',
    fetchFn: yithSalesApiFetch
  }),
  endpoints: (builder) => ({
    updateSettings: builder.mutation({
      query: ({ body, groupID }) => {
        return ({
          url: `${root}${versionString}/${restBase}/${groupID}/batch/?_locale=user`,
          method: 'POST',
          body
        });
      }
    })
  })
});

export const {
  useUpdateSettingsMutation
} = apiSettingsSlice;
