import React from 'react';

import Notice from './notice';

const Notices = ({ notices, onEnd, closeIn = 5 }) => {
  if (!notices.length) return '';
  return (
    notices.map((notice, index) => <Notice key={`notice-${index}`} severity={notice.severity} message={notice.message} onEnd={onEnd} closeIn={closeIn}/>)
  );
};

export default Notices;
