import React, { useState } from 'react';

import { Select } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';

import { getFormattedTermName } from '../../../../common/utils/common';
import { getTermsByType, searchTermsByType } from '../../../../common/utils/data';

const AjaxTerms = ({ id, label, terms, taxonomy, placeholder = __('Search', 'wonder-cart'), multiple = false, minimumInputLength = 3, isFullWidth = false, size = 'sm', onChange }) => {
  const [keys, setKeys] = useState(terms);
  const [term, setTerm] = useState('');

  const { data: selectedItems, isLoading: isSelectedLoading } = getTermsByType({ termType: taxonomy, termIds: keys, fetchWithEmptyTerms: false });
  const { data: searchedItems, isLoading: isSearchedLoading } = searchTermsByType({ search: term, type: taxonomy, fetchWithEmptyString: false });

  const isLoading = isSearchedLoading || isSelectedLoading;
  const message = isLoading ? 'Loading...' : undefined;
  const selectedOpts = selectedItems
    ? selectedItems.map((item) => ({
      value: item.id, label: getFormattedTermName(item)
    }))
    : [];

  const searchedOpts = term && searchedItems
    ? searchedItems.map((item) => ({
      value: item.id, label: getFormattedTermName(item)
    }))
    : [];

  const mergedArray = [...selectedOpts, ...searchedOpts];
  const set = new Set();
  const selectOpts = mergedArray.filter(item => {
    if (!set.has(item.value)) {
      set.add(item.value);
      return true;
    }
    return false;
  }, set);

  const onSearch = (search) => {
    if (search.length >= minimumInputLength) {
      setTerm(search);
    } else {
      setTerm('');
    }
  };

  const handleChange = (values) => {
    setKeys(values);
    onChange(values);
  };

  return (
      <>
        {label && (<label htmlFor={id}>{label}</label>)}
        <Select id={id} size={size} options={selectOpts} multiple={multiple} value={keys} showTags={true} limitTags={5} placeholder={placeholder} filterSearch={() => true} hideSelectedOptions={true} allowSearch={true} onSearch={onSearch} onChange={handleChange} isLoading={isLoading} noOptionsText={message} noResultsText={message} loadingText={__('Loading', '')} fullWidth={isFullWidth}/>
  </>
  );
};
export default AjaxTerms;
