import React from 'react';

import { Box, Spinner, Stack, Typography } from '@lapilli-ui/components';
import { parse } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';

import ProductList from '../../../../../blocks/elements/product-list';
import ProductSingleItem from '../../../../../blocks/elements/product-single-item';
import ProductSingleItemEmpty from '../../../../../blocks/elements/product-single-item-empty';
import BlockEditorPreview from '../../../block-editor-preview';
import Countdown from '../../elements/countdown';

const LastDeal = ({ options, products, showContent, inFetching, isSingle, numProductToShow = -1, imageWidth = 'auto', step }) => {
  const {
    title,
    discount_to_apply: discountToApply,
    show_countdown: showCountdown,
    countdown_value: countdownValue,
    countdown_type: countdownType,
    products_to_offer: productsToOffer
  } = options;

  const productListOPT = {
    discountValue: discountToApply.discount_value,
    discountType: discountToApply.discount_type,
    showProductName: options.show_product_name,
    showProductPrice: options.show_product_price,
    showSaving: options.show_saving,
    savingColor: false,
    applyDiscount: options.apply_discount,
    addToCartLabel: options.add_to_cart_button_label,
    productType: productsToOffer.type
  };

  return (
      <Box className={'yith-sales-last-deal-preview-wrapper'}>
          {!showContent && <ProductSingleItemEmpty width={imageWidth} /> }
          { showContent && step === 'customization' && title && <BlockEditorPreview blocks={parse(title)}/>}
          { showContent && inFetching && (<Spinner sx={{ marginLeft: '50%', marginTop: '20px', marginBottom: '20px' }}/>)}
          { showContent && !inFetching &&
              <>
                {showCountdown === 'yes' &&
                    <Stack sx={{ textAlign: 'center' }}>
                      <Typography sx={{ fontSize: '15px', textAlign: 'center' }}>{__('The offer ends in:', 'wonder-cart')}</Typography>
                      <Countdown countdownValue={countdownValue} countdownType={countdownType} start={false} />
                    </Stack>
                }
                { isSingle
                  ? (<ProductSingleItem data={products} productOpts={productListOPT}/>)
                  : (<ProductList products={products} productOpts={productListOPT} showTotals={false} numProductToShow={numProductToShow} showNoThanks={true}/>)
                }
              </>
          } </Box>
  );
};

export default LastDeal;
