import React, { useEffect, useState } from 'react';

import { Button, Input, Select, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __, sprintf } from '@wordpress/i18n';
import { useSelector } from 'react-redux';

import CategoryRuleList from './category-rule-list';
import SimpleModal from '../../../../blocks/elements/simple-modal';
import { isEmpty } from '../../../../common/utils/common';
import { searchTermsByType } from '../../../../common/utils/data';
import { wcSettings } from '../../../config';

const DiscountRule = styled(Stack)(({ theme }) => ({
  '&': {
    marginBottom: '20px'
  },
  '& .discount-type': {
    flex: '1'
  },
  '& .iconButton': {
    border: 0,
    backgroundColor: 'transparent'
  }
}));

const CategoriesDiscount = ({ id, label, value, buttonLabel, onSaveHandler, required, showError }) => {
  const currentCampaign = useSelector((state) => state.campaign);
  const { data, isLoading, isFetching } = searchTermsByType({ search: '', type: 'categories' });
  const [isModalVisible, setModalVisible] = useState(false);
  const meta = currentCampaign.campaign.meta[currentCampaign.campaign.type];
  const categories = meta.trigger_product.ids;
  const [items, setItems] = useState(value.length > 0 ? value : []);
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [saved, setSaved] = useState(value.length > 0);
  const inFetching = isLoading || isFetching;
  const getSelectedCategories = () => {
    let selectedCategories = [];/// termsSelected;

    if (data) {
      categories.forEach(categoryID => {
        const termFound = data.find(term => {
          return term.id === categoryID;
        });
        selectedCategories = [...selectedCategories, termFound];
      });
    }

    return selectedCategories;
  };

  useEffect(() => {
    setSelectedCategories(getSelectedCategories());
  }, [categories, data]);

  useEffect(() => {
    const checkedItems = [];
    if (items.length > 0) {
      categories.forEach((category, index) => {
        const item = items.find(item => item.category === category);
        if (item) {
          checkedItems.push({ ...item, id: index });
        } else {
          checkedItems.push({ id: index, category, discount_value: 10, discount_type: 'percentage' });
        }
      }
      );
      setItems(checkedItems);
    }
  }, [categories]);

  const options = [
    { value: 'percentage', label: __('% - percentage', 'wonder-cart') },
    { value: 'fixed', label: sprintf(__('%s - fixed', 'wonder-cart'), wcSettings.currency.symbol) }
  ];
  const handleOpenModal = () => {
    setModalVisible(true);
  };

  const handleCloseModal = () => {
    if (!saved) {
      setItems([]);
    }
    setModalVisible(false);
  };

  const handleSave = () => {
    if (items.length === 0) {
      categories.forEach((categoryID, index) => {
        items.push({ id: index, category: categoryID, discount_value: 10, discount_type: 'percentage' });
      });
    }
    const e = {
      target: {
        id,
        value: items
      }
    };
    setSaved(true);
    onSaveHandler(e);
    setModalVisible(false);
  };
  const handleChangeNumber = (value, id, property) => {
    setTimeout(() => handleOnChange(value, id, property), 500);
  };

  const handleOnChange = (value, id, property) => {
    let updatedItems = [];
    if (items.length === 0) {
      categories.forEach((categoryID, index) => {
        updatedItems.push({ id: index, category: categoryID, discount_value: 10, discount_type: 'percentage', checked: true });
      });
    } else {
      updatedItems = [...items];
    }

    if (typeof value === 'object') {
      value = value.target.value;
    }

    updatedItems = updatedItems.map(item => {
      const newItem = { ...item };
      if (newItem.id === id) {
        newItem[property] = value;
      }
      return newItem;
    });

    setItems(updatedItems);
  };

  const SingleRule = ({ rule }) => {
    const currentCategory = selectedCategories.find(category => category.id === rule.category);
    return (
          <DiscountRule direction="row" align="center" id={`item-${rule.id}`} spacing={2} >
            <Stack direction="row" sx={{ flex: 1, gap: '5px' }}><Typography variant='body2'>{__('Discount for', 'wonder-cart')}</Typography> <Typography variant='body2' sx={{ fontWeight: 'bold' }}>{currentCategory.name}</Typography> </Stack>
            <Input size="lg" type="number" defaultValue={rule.discount_value} onChange={(e) => handleChangeNumber(e, rule.id, 'discount_value')} isMini={true}/>
            <Select size="lg" classes={{ root: 'discount-type' }} options={options} onChange={(value) => handleOnChange(value, rule.id, 'discount_type')} value={rule.discount_type}/>
          </DiscountRule>
    );
  };

  const error = showError && required && isEmpty(value);
  const typeMessage = __('Set discount rules before continuing', 'wonder-cart');
  const showNotice = error ? <Typography className="yith-sales-field-error" sx={{ padding: '10px 0' }}>{typeMessage}</Typography> : '';
  return (
        <>
          <label>{label}</label>
          {!inFetching && <CategoryRuleList items={items} categories={selectedCategories}/>}
          <Button size="lg" fullWidth={true} onClick={handleOpenModal} variant="outlined">{buttonLabel}</Button>
          {showNotice}
          <SimpleModal title={__('Discount for categories rules', 'wonder-cart')} open={isModalVisible} onClose={handleCloseModal}>
            <p style={{ marginBottom: '30px' }}>{__('Set the discount to apply to all products of selected categories.', 'wonder-cart')}</p>
            {!inFetching && items.length > 0 && items.map((item, index) =>
                <SingleRule rule={item} id={`item-${item.id}`} key={`item-${index}`}/>
            )}
            {!inFetching && items.length === 0 && categories.map((category, index) =>
                <SingleRule rule={{ id: index, category, discount_value: 10, discount_type: 'percentage' }} id={`item-${index}`} key={`item-${index}`}/>)
            }

            <Button size="lg" fullWidth={true} onClick={handleSave}>{__('Save', 'wonder-cart')}</Button>
          </SimpleModal>
        </>
  );
};

export default CategoriesDiscount;
