import React, { useEffect, useState } from 'react';

import { Button, Input, Select, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __, sprintf } from '@wordpress/i18n';

import RuleList from './rule-list';
import SimpleModal from '../../../../blocks/elements/simple-modal';
import Icon from '../../../../common/icon';
import { isEmpty } from '../../../../common/utils/common';
import { wcSettings } from '../../../config';
import { theme } from '../../../theme';
import Tooltip from '../../tooltip/tooltip';

const DiscountRule = styled(Stack)(({ theme }) => ({
  '&': {
    marginBottom: '20px'
  },
  '& .discount-type': {
    flex: '1'
  },
  '& .iconButton': {
    border: 0,
    backgroundColor: 'transparent'
  }
}));

const QuantityDiscountRules = ({ id, label, value, editButtonLabel, buttonLabel, addNewRuleButtonLabel, onSaveHandler, required, showError }) => {
  const [isEditMode, setEditMode] = useState(false);
  const [isModalVisible, setModalVisible] = useState(false);
  const [items, setItems] = useState(value.length > 0 ? value : []);
  const [saved, setSaved] = useState(value.length > 0);

  useEffect(() => {
    setEditMode(value.length > 0);
  }, [value]);
  const handleOpenModal = () => {
    setModalVisible(true);
  };

  const buttonLabelToShow = isEditMode ? editButtonLabel : buttonLabel;

  const options = [
    { value: 'percentage', label: __('% - percentage', 'wonder-cart') },
    { value: 'fixed', label: sprintf(__('%s - fixed', 'wonder-cart'), wcSettings.currency.symbol) }
  ];

  const handleOnChange = (value, id, property) => {
    let updatedItems = [];
    if (items.length === 0) {
      updatedItems = [{ id: 0, unit: 1, discount: 0, discountType: 'percentage', checked: false }, { id: 1, unit: 10, discount: 10, discountType: 'percentage', checked: true }];
    } else {
      updatedItems = [...items];
    }
    if (typeof value === 'object') {
      value = value.target.value;
    }
    if (property === 'checked') {
      if (value) {
        updatedItems = updatedItems.map(item => (item.id === id) ? { ...item, checked: true } : { ...item, checked: false });
      } else {
        updatedItems = updatedItems.map((item, index) => (index === 0) ? { ...item, checked: true } : { ...item, checked: false });
      }
    } else {
      updatedItems = updatedItems.map(item => {
        const newItem = { ...item };
        if (newItem.id === id) {
          newItem[property] = value;
        }
        return newItem;
      });
    }
    setItems(updatedItems);
  };
  const handleChangeNumber = (value, id, property) => {
    setTimeout(() => handleOnChange(value, id, property), 500);
  };
  const handleAddRule = () => {
    let newList = [];
    if (items.length === 0) {
      newList = [{ id: 0, unit: 1, discount: 0, discountType: 'percentage', checked: false }, { id: 1, unit: 10, discount: 10, discountType: 'percentage', checked: true }];
    } else {
      newList = items.map((item, index) => ({ ...item, id: index }));
    }

    newList.push({ id: newList.length, unit: 20, discount: 20, discountType: 'percentage', checked: false });
    setItems(newList);
  };

  const handleRemoveItem = (id) => {
    const newList = items.filter((item) => (item.id !== id));

    const somethingChecked = newList.filter(item => item.checked === true);
    if (newList.length > 0 && somethingChecked.length === 0) {
      newList[0] = { ...newList[0], checked: true };
    }
    if (newList.length === 0) {
      setEditMode(false);
      setSaved(false);
    }
    setItems(newList);
  };

  const handleSave = () => {
    const newItems = [];

    if (items.length === 0) {
      newItems.push({ id: 0, unit: 1, discount: 0, discountType: 'percentage', checked: true });
      newItems.push({ id: 1, unit: 10, discount: 10, discountType: 'percentage', checked: false });
    }

    const e = {
      target: {
        id,
        value: newItems.length ? newItems : items
      }
    };

    setSaved(true);
    onSaveHandler(e);
    setModalVisible(false);
  };

  const handleCloseModal = () => {
    if (!saved) {
      setItems([]);
    }
    setModalVisible(false);
  };

  const SingleRule = ({ rule }) => {
    const starColor = rule?.checked ? theme.palette.primary.light : theme.palette.grey[300];
    const tooltipText = rule?.checked ? __('Checked by default', 'wonder-cart') : __('Check to set as default', 'wonder-cart');
    return (
        <DiscountRule direction="row" align="center" id={`item-${rule.id}`} spacing={2.5} sx={{ fontSize: theme.typography.body2.fontSize }}>
          <Input size="lg" type="number" defaultValue={rule.unit} onChange={(value) => handleChangeNumber(value, rule.id, 'unit')} isMini={true}/>
          {__('units = ', 'wonder-cart')}
          <Input size="lg" type="number" defaultValue={rule.discount} onChange={(e) => handleChangeNumber(e, rule.id, 'discount')} isMini={true}/>
          <Select size="lg" classes={{ root: 'discount-type' }} options={options} onChange={(value) => handleOnChange(value, rule.id, 'discountType')} value={rule.discountType}/>
          <Stack direction="column">
            <Tooltip content={tooltipText} direction="left" margin="5px">
              <button className="iconButton" onClick={(e) => handleOnChange(!rule.checked, rule.id, 'checked')}><Icon icon="StarIcon" size="15" sx={{ color: starColor }}/></button>
            </Tooltip>
            {items.length > 1 && <button className="iconButton" onClick={() => handleRemoveItem(rule.id)}><Icon icon="XMarkIcon" size="15" className="remove-item"/></button>}
          </Stack>
        </DiscountRule>
    );
  };

  const error = showError && required && isEmpty(value);
  const typeMessage = __('Set discount rules before continuing', 'wonder-cart');
  const showNotice = error ? <Typography className="yith-sales-field-error" sx={{ padding: '10px 0' }}>{typeMessage}</Typography> : '';

  return (
      <>
        <label>{label}</label>
        <RuleList items={items} />
        <Button size="lg" fullWidth={true} onClick={handleOpenModal} variant="outlined" >{buttonLabelToShow}</Button>
        {showNotice}
        <SimpleModal title={__('Discount for quantity rules', 'wonder-cart')} open={isModalVisible} onClose={handleCloseModal}>
          <p style={{ marginBottom: '30px' }}>{__('Set the discount for quantity rules for the selected products.', 'wonder-cart')}</p>
          {items.length > 0 && items.map((item, index) =>
              <SingleRule rule={item} id={`item-${item.id}`} key={`item-${index}`}/>
          )}
          {items.length === 0 &&
              <React.Fragment>
                <SingleRule rule={{ id: 0, unit: 1, discount: 0, discountType: 'percentage', checked: false }} id="item-0"/>
                <SingleRule rule={{ id: 1, unit: 10, discount: 10, discountType: 'percentage', checked: true }} id="item-1"/>
              </React.Fragment>
          }
          <Button size="lg" startIcon={<Icon icon="PlusSmallIcon" sx={{ color: 'inherit' }}/>} fullWidth={true} onClick={handleAddRule}  variant="outlined" style={{ marginBottom: '20px', maxHeight: '35px' }}>{addNewRuleButtonLabel}</Button>
          <Button size="lg" fullWidth={true} onClick={handleSave}>{__('Save', 'wonder-cart')}</Button>
        </SimpleModal>
      </>

  );
};
export default QuantityDiscountRules;
