import { format as yithFormatDate } from '@lapilli-ui/date';

import { DATE_FORMAT, TIME_FORMAT } from '../config';

export function formatTimestamp (timestamp) {
  const format = DATE_FORMAT + ' ' + TIME_FORMAT;
  const date = new Date(timestamp * 1000);

  return yithFormatDate(format, date);
}

export function formatDate (timestamp) {
  const format = DATE_FORMAT;
  const date = new Date(timestamp * 1000);

  return yithFormatDate(format, date);
}

export function getDatePrefix (prefix) {
  let newPrefix = '';
  for (const term in prefix) {
    newPrefix += '\\' + prefix[term];
  }
  return newPrefix;
}

export function nowScheduleDateFrom () {
  return (new Date()).getTime() / 1000 - 1000;
}
