import React, { useEffect, useRef, useState } from 'react';

import { Box, Button } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';
import { useDispatch, useSelector } from 'react-redux';

import PanelSection from './panel-section';
import { useUpdateSettingsMutation } from '../../../common/api/settings';
import { Divider } from '../../../common/fields';
import { useMediaQuery } from '../../../common/utils/common';
import {
  addUpdatedOptions,
  clearUpdatedOption,
  resetPanel,
  saveSettings,
  updateOption
} from '../../slices/settings/settings-slice';
import { addNotice, updateNotice } from '../../slices/view/notices-slice';
import ConfirmDialog from '../confirm-dialog/confirmDialog';
import Notices from '../notices';

const FormStyled = styled('form')(({ theme }) => ({
  '&': {
    display: 'flex',
    gap: '32px',
    flexDirection: 'column',
    minHeight: '75vh!important'
  }
}));
const FormActions = styled('div')(({ theme }) => ({
  '&': {
    position: 'sticky',
    bottom: '0',
    padding: '32px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    background: '#f8fafc',
    zIndex: 99999,
    borderRadius: '0 0 12px 12px',
    gap: '8px',
    '&.centered': {
      justifyContent: 'center'
    },
    '&.column': {
      flexDirection: 'column'
    }
  },
  '& button': {
    padding: '8.5px 32px'
  }
}));
const PanelOptions = ({ id, settings, className, showButton = true }) => {
  const sections = settings.sections;
  const sectionIDS = Object.keys(sections);
  const currentSettings = useSelector((state) => state.panelConfig.current);
  const currentUpdates = useSelector((state) => state.panelConfig.updates);
  const notices = useSelector((state) => state.notices.notices);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [saveOptions] = useUpdateSettingsMutation();
  const dispatch = useDispatch();
  const noticesDOM = useRef(null);

  const alignCenter = useMediaQuery('(max-width:500px)');
  const splitColumn = useMediaQuery('(max-width:400px)');

  useEffect(() => {
    return () => {
      dispatch(updateNotice([]));
    };
  }, []);
  useEffect(() => {
    if (noticesDOM !== null && notices.length > 0) {
      noticesDOM.current.scrollIntoView();
    }
  }, [notices]);
  const handleSaveOnStore = (element) => {
    const { sectionID, mainOptionID, subOptionID, optionValue } = element;
    let optionsValue = { ...currentSettings[id][sectionID][mainOptionID] };

    if (subOptionID) {
      optionsValue[subOptionID] = optionValue;
    } else {
      optionsValue = optionValue;
    }
    dispatch(updateOption({ panelID: id, ...element }));
    dispatch(addUpdatedOptions({ panelID: id, mainOptionID, optionValue: optionsValue }));
  };

  const handleOnClickReset = () => {
    setModalIsOpen(true);
  };
  const clearNotices = () => {
    dispatch(updateNotice([]));
  };
  const handleSaveSettings = async () => {
    if (currentUpdates.length > 0) {
      await saveOptions(
        {
          body: {
            group_id: id,
            update: {
              ...currentUpdates
            }
          },
          groupID: id
        }).then((result) => {
        dispatch(addNotice({
          severity: 'success',
          message: __('Options saved', 'wonder-cart')
        }));
        dispatch(saveSettings(currentSettings));
        dispatch(clearUpdatedOption());
      });
    }
  };

  const handleOnConfirmReset = async () => {
    await saveOptions(
      {
        body: {
          reset: {
            group_id: id
          }
        },
        groupID: id
      }).then((result) => {
      setModalIsOpen(false);
      dispatch(addNotice({
        severity: 'success',
        message: __('Options reset!', 'wonder-cart')
      }));
      dispatch(resetPanel({ panelID: id, config: result.data }));

      dispatch(clearUpdatedOption());
    });
  };

  return (
      <Box className={`mainlist ${className}`} sx={{ backgroundColor: '#fff' }}>

        <Box sx={{ width: '100%' }}>
          {notices &&
              <Box sx={{ margin: '0 20px 30px 20px' }} ref={noticesDOM}><Notices notices={notices} onEnd={clearNotices}/></Box>}
          <FormStyled>
            {sectionIDS.map((sectionID, index) =>
                <React.Fragment key={sectionID}>
                  {index > 0 && <Divider sx={{ marginTop: '-32px' }}/>}
                  <PanelSection id={sectionID} section={sections[sectionID]} onChange={handleSaveOnStore}
                                values={currentSettings[id]}/>
                </React.Fragment>
            )}
          </FormStyled>
        </Box>
        {showButton &&
            <>
              <FormActions className={`save-buttons ${alignCenter ? 'centered' : ''} ${splitColumn ? 'column' : ''}`}>
                <Button className="yith-sales-panel-option-reset" variant="outlined"
                        onClick={handleOnClickReset}>{__('Discard Changes', 'wonder-cart')}</Button>
                <Button className="yith-sales-panel-option-save" size="sm"
                        sx={currentUpdates.length === 0 ? { '&': { opacity: '0.5', pointerEvents: 'none' } } : {}}
                        onClick={handleSaveSettings}>{__('Save Changes', 'wonder-cart')}</Button>

              </FormActions>

              <ConfirmDialog open={modalIsOpen} onCancel={() => setModalIsOpen(false)} onConfirm={handleOnConfirmReset}
                             onClose={() => setModalIsOpen(false)}
                             title={__('Are you sure?', 'wonder-cart')}
                             confirmButton={__('Yes, reset', 'wonder-cart')}
                             isDeleteButton={true}>
                {__('Do you want reset to default value all options in this page?', 'wonder-cart')}
              </ConfirmDialog>
            </>
        }
      </Box>
  );
};

export default PanelOptions;
