import React from 'react';

import { Spinner } from '@lapilli-ui/components';
import { _n, sprintf } from '@wordpress/i18n';

import { VerticalScrollableContentStyled } from '../../../../blocks/elements/styles/VerticalScrollableContent.styled';
import Icon from '../../../../common/icon';
import { getFormattedTermName, isEmpty } from '../../../../common/utils/common';
import { getTermsByType } from '../../../../common/utils/data';
import { formatCurrency } from '../../../../common/utils/numbers';
import {
  getConditionSubTypeCartOpts,
  getConditionTypeCartOpts
} from '../../../utils/admin-functions';
import {
  PreviewItemDelete,
  PreviewItemName,
  PreviewItemsBoxContainer,
  PreviewItemsBoxWrapper
} from '../../styles/PreviewItemsBox.styled';
const ConditionList = ({ items, onHandleRemove }) => {
  if (items.length === 0) return '';

  const productItems = items.filter((item) => item.type === 'products');
  const productCatItems = items.filter(item => item.type === 'categories');

  const productConditionIDS = productItems
    ? productItems.map((item) => (
      item.value
    ))
    : [];

  const productCatConditionIDS = productCatItems
    ? productCatItems.map((item) => (
      item.value
    ))
    : [];

  const productFilteredIds = [];
  const productCatFilteredIds = [];
  for (const i in productConditionIDS) {
    const productIDS = productConditionIDS[i];
    for (const ii in productIDS) {
      const productID = productIDS[ii];
      if (productFilteredIds.indexOf(productID) < 0) {
        productFilteredIds.push(productID);
      }
    }
  }
  for (const i in productCatConditionIDS) {
    const productCatIDS = productCatConditionIDS[i];
    for (const ii in productCatIDS) {
      const productCatID = productCatIDS[ii];
      if (productCatFilteredIds.indexOf(productCatID) < 0) {
        productCatFilteredIds.push(productCatID);
      }
    }
  }

  const { data: productSelected, isLoading: productIsLoading } = getTermsByType({ termType: 'products', termIds: productFilteredIds, fetchWithEmptyTerms: false });
  const { data: productCatSelected, isLoading: productCatIsLoading } = getTermsByType({ termType: 'categories', termIds: productCatFilteredIds, fetchWithEmptyTerms: false });

  const isLoading = productCatIsLoading || productIsLoading;

  const filterNameList = (ids, terms) => {
    const termNames = [];
    if (terms.length === 0) return '';
    for (const i in ids) {
      const id = ids[i];
      const term = terms.filter((termSelected) => termSelected.id === id);
      if (term.length === 1) {
        termNames.push(getFormattedTermName(term[0]));
      }
    }

    let finalNames = [];
    if (termNames.length > 5) {
      finalNames = termNames.slice(0, 5);
    } else {
      finalNames = termNames;
    }
    const remain = termNames.length - 5;
    const suffix = remain > 0 ? sprintf(_n('...other %s', '...others %s', remain, 'wonder-cart'), remain) : '';
    return finalNames.join(', ') + suffix;
  };
  const getTermNameList = (termType, ids) => {
    const termsSelected = termType === 'products' ? productSelected : productCatSelected;
    return filterNameList(ids, termsSelected);
  };
  const conditionTypeOpts = getConditionTypeCartOpts('gift-on-cart');
  const getFormattedName = (item) => {
    const conditionCompareOpts = getConditionSubTypeCartOpts(item.type);
    const typeLabel = conditionTypeOpts.filter((conditionType) => (conditionType.value === item.type));
    const conditionLabel = conditionCompareOpts.filter((conditionType) => (conditionType.value === item.condition));
    let value = '';
    let label = '';
    if (!isEmpty(typeLabel)) {
      switch (item.type) {
        case 'cart_total':
        case 'past_expense':
          value = formatCurrency(item.value);
          label = `${typeLabel[0].label}: ${conditionLabel[0].label}`;
          break;
        case 'products':
        case 'categories':
          value = getTermNameList(item.type, item.value);
          label = conditionLabel[0].label + ':';
          label = label.charAt(0).toUpperCase() + label.slice(1);
          break;
        default:
          value = item.value;
          label = `${typeLabel[0].label}: ${conditionLabel[0].label}`;
          break;
      }
    }
    return `${label} ${value}`;
  };

  return (
        <PreviewItemsBoxContainer>
            <PreviewItemsBoxWrapper>
              {isLoading && (<Spinner sx={{ marginLeft: '50%', marginTop: '40px' }}/>)}
              { !isLoading && (
                  <VerticalScrollableContentStyled maxHeight="98px">
                    <ul>
                        {items.map((item) => (
                            <li key={item.id}>
                                <PreviewItemName>
                                    {getFormattedName(item)}
                                </PreviewItemName>
                                <PreviewItemDelete onClick={() => onHandleRemove(item.id)}>
                                    <Icon icon="XMarkIcon" size="12"/>
                                </PreviewItemDelete>
                            </li>
                        ))}
                    </ul>
                </VerticalScrollableContentStyled>
              )}
            </PreviewItemsBoxWrapper>
        </PreviewItemsBoxContainer>
  );
};

export default ConditionList;
