import React from 'react';

import { Box, Stack, Typography } from '@lapilli-ui/components';
import { __unstableEditorStyles as EditorStyles } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';

import { isEmpty } from '../../../common/utils/common';
import { yithParams } from '../../config';
import BogoPreview from '../previews/modules/bogo/bogo-preview';
import BuyXGetYPreview from '../previews/modules/buy-x-get-y/buy-x-get-y-preview';
import CartDiscountPreview from '../previews/modules/cart-discount/cart-discount-preview';
import CategoryDiscountPreview from '../previews/modules/category-discount/category-discount-preview';
import FreeShippingPreview from '../previews/modules/free-shipping/free-shipping-preview';
import FBTPreview from '../previews/modules/frequently-bought-together/fbt-preview';
import GiftOnCartPreview from '../previews/modules/gift-on-cart/gift-on-cart-preview';
import LastDealPreview from '../previews/modules/last-deal/last-deal-preview';
import PromotionPreview from '../previews/modules/promotion/promotion-preview';
import QuantityDiscountPreview from '../previews/modules/quantity-discount/quantity-discount-preview';
import ShopDiscountPreview from '../previews/modules/shop-discount/shop-discount-preview';
import ThankYouPagePreview from '../previews/modules/thank-you-page/thank-you-page-preview';
import ThreeForTwoPreview from '../previews/modules/three-for-two/three-for-two-preview';
import UpsellPreview from '../previews/modules/upsell/upsell-preview';

const Preview = ({ campaign, step = 'trigger', type }) => {
  let previewType = false;
  const published = (campaign?.status && campaign.status === 'publish');

  switch (type) {
    case 'frequently-bought-together':
      previewType = <FBTPreview type={type} showPreview={!published && step === 'trigger'} campaign={campaign}/>;
      break;
    case 'upsell':
      previewType = <UpsellPreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      break;
    case 'cart-discount':
      previewType =
                <CartDiscountPreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      break;
    case 'category-discount':
      if (step === 'promotion') {
        previewType = <PromotionPreview campaign={campaign}/>;
      } else {
        previewType = <CategoryDiscountPreview type={type} showPreview={isEmpty(campaign)} step={step}
                                                       campaign={campaign}/>;
      }
      break;
    case 'shop-discount':
      if (step === 'promotion') {
        previewType = <PromotionPreview campaign={campaign}/>;
      } else {
        previewType =
                    <ShopDiscountPreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      }
      break;
    case 'bogo':
      if (step === 'promotion') {
        previewType = <PromotionPreview campaign={campaign}/>;
      } else {
        previewType =
                    <BogoPreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      }
      break;
    case 'gift-on-cart':
      previewType = <GiftOnCartPreview type={type} showPreview={isEmpty(campaign)} campaign={campaign}/>;
      break;
    case 'last-deal':
      previewType =
                <LastDealPreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      break;
    case 'free-shipping':
      if (step === 'promotion') {
        previewType = <PromotionPreview campaign={campaign}/>;
      } else {
        previewType = <FreeShippingPreview type={type} showPreview={isEmpty(campaign)} campaign={campaign}/>;
      }
      break;
    case 'thank-you-page':
      previewType =
                <ThankYouPagePreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      break;
    case 'three-for-two':
      if (step === 'promotion') {
        previewType = <PromotionPreview campaign={campaign}/>;
      } else {
        previewType =
                    <ThreeForTwoPreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      }
      break;
    case 'quantity-discount':
      previewType =
                <QuantityDiscountPreview type={type} showPreview={isEmpty(campaign)} step={step} campaign={campaign}/>;
      break;
    case 'buy-x-get-y':
      if (step === 'promotion') {
        previewType = <PromotionPreview campaign={campaign}/>;
      } else {
        previewType = <BuyXGetYPreview campaign={campaign} showPreview={isEmpty(campaign) || !published} step={step} type={type}/>;
      }
      break;
  }

  return (
        <Box>
            <EditorStyles styles={yithParams.blockEditorSettings.styles ?? []}/>
            <Box className='editor-styles-wrapper' sx={{ fontSize: '16px!important' }}>
              <Stack align="center">
                <Typography component='span' sx={{ color: 'rgb(71, 85, 105)', textAlign: 'center', fontSize: '15px', fontWeight: 600, padding: '8px 10px' }}>{__('Preview', 'wonder-cart')}</Typography>
              </Stack>
                {previewType}
            </Box>
        </Box>
  );
};

export default Preview;
