import { CAMPAIGN_TYPES } from '../config';

export const getToolbarOptions = (typeRequested) => {
  const type = CAMPAIGN_TYPES.filter(type => type.id === typeRequested);

  return typeof type[0].options !== 'undefined' ? type[0].options : false;
};

/**
 * Return the next page of the toolbar
 * @param toolbar
 * @returns {string|boolean}
 */
export const getNext = (step, toolbar) => {
  const steps = Object.keys(toolbar);
  const indexOfStep = steps.indexOf(step);
  return (indexOfStep !== false && indexOfStep < steps.length - 1) ? steps[indexOfStep + 1] : false;
};

/**
 * Return the prev page of the toolbar
 *
 * @param toolbar
 * @returns {string|boolean}
 */
export const getPrev = (step, toolbar) => {
  const steps = Object.keys(toolbar);
  const indexOfStep = steps.indexOf(step);

  return (indexOfStep !== false && indexOfStep > 0) ? steps[indexOfStep - 1] : '';
};
