import React from 'react';

import { Stack } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';

import { theme } from '../../theme';
import ActionButton from '../action-button';

const CampaignItemActions = ({ onEditClick, onDuplicateClick, onTrashClick, sx }) => {
  return (
        <Stack className="action" direction="row" spacing={1} sx={{ position: 'absolute', translate: '-50%', left: '50%', top: '40%', ...sx }}>
          <ActionButton title={__('Edit', 'wonder-cart')} type="PencilIcon" onCustomClick={onEditClick} color={theme.palette.primary.main}/>
          <ActionButton title={__('Duplicate', 'wonder-cart')} type="DocumentDuplicateIcon" color={theme.palette.primary.main} onCustomClick={onDuplicateClick}/>
          <ActionButton title={__('Delete', 'wonder-cart')} type="TrashIcon" color={'#c41d04'} onCustomClick={onTrashClick}/>
        </Stack>
  );
};

export default CampaignItemActions;
