import React from 'react';

import FreeProductInCart from '../../elements/free-product-in-cart';

const GiftInCartPreview = ({ campaign }) => {
  const options = campaign.meta[campaign.type];
  const {
    title,
    product_to_offer: productToShow,
    show_product_price: showProductPrice
  } = options;

  return (
     <FreeProductInCart productToShow={productToShow} title={title} showPrice={showProductPrice === 'yes'}/>
  );
};

export default GiftInCartPreview;
