import React from 'react';

import { Box, Stack } from '@lapilli-ui/components';
import { parse } from '@wordpress/blocks';

import Promotion from '../../../../../blocks/promotion';
import { Divider } from '../../../../../common/fields';
import Icon from '../../../../../common/icon';
import { getPopupOptions } from '../../../../../common/utils/common';
import { getStaticPromotionPreviewImageByType, isEmpty } from '../../../../utils/admin-functions';
import BlockEditorPreview from '../../../block-editor-preview';
import ModalPreview from '../../elements/modal-preview';

const PromotionPreview = ({ campaign }) => {
  const options = campaign.meta[campaign.type];

  const {
    promote_campaign: promoteCampaign,
    promotion_style: promotionStyle
  } = options;

  const showPreview = promoteCampaign === 'no';
  const isPopup = promotionStyle === 'popup';
  const popupOptions = getPopupOptions(options, 'promotion');

  const sx = {
    pointerEvents: 'none',
    margin: '40px 40px 0 0',
    backgroundColor: 'inherit',
    width: '100%',
    height: '100%'
  };
  const hasPopupBgImgUrl = options.upload_bg_popup_image_field !== undefined && !isEmpty(options.upload_bg_popup_image_field);
  const hasBannerBgImgUrl = options.upload_bg_image_field !== undefined && !isEmpty(options.upload_bg_image_field);
  let bannerOptionText = options.text_to_show_popup;
  const bannerOption = {
    showButton: options.show_cta === 'yes',
    buttonLabel: options.cta_button_label,
    buttonLink: options.cta_button_url,
    buttonBgColor: options.background_promotion_button_color,
    buttonBgHoverColor: options.background_promotion_button_hover_color,
    buttonColor: options.color_promotion_button_text
  };

  if (!isPopup) {
    bannerOptionText = options.text_to_show_banner;
    bannerOption.bgColor = options.banner_color;
    bannerOption.showBgImage = options.upload_bg_image;
    bannerOption.bgImageUrl = hasBannerBgImgUrl ? options.upload_bg_image_field.url : '';
  }
  const previewImg = <img src={getStaticPromotionPreviewImageByType(campaign.type)}/>;
  const promotionTitleComponent = <BlockEditorPreview blocks={parse(bannerOptionText)}/>;
  return (
        <Box sx={sx}>
            {
                showPreview && (
                    <Stack direction='column' spacing={3}
                           sx={{ width: '800px', margin: '0 auto' }}
                           align='center' justify='center'>
                        <Stack direction='row' spacing={2} sx={{ backgroundColor: '#fff', padding: '35px', borderRadius: '7px', boxShadow: '0px 2px 8px 2px rgba(0, 0, 0, 0.0927086)' }}>
                            {previewImg}
                            <Stack direction='column' spacing={2}>
                                <Divider sx={{ width: '450px', borderBottomWidth: 'thick', color: '#E0E0E0' }}/>
                                <Divider sx={{ width: '450px', borderBottomWidth: 'thick', color: '#E0E0E0' }}/>
                                <Divider sx={{ width: '450px', borderBottomWidth: 'thick', color: '#E0E0E0' }}/>
                            </Stack>
                        </Stack>
                      <Stack direction='column' spacing={6} sx={{ width: '340px', backgroundColor: '#fff', padding: '35px', borderRadius: '7px', boxShadow: '0px 2px 8px 2px rgba(0, 0, 0, 0.0927086)' }} align='center'>
                        <Box sx={{ position: 'relative', width: '100%' }}><Icon icon="XMarkIcon" size="18" sx={{
                          color: '#E0E0E0',
                          position: 'absolute',
                          top: '-20px',
                          right: '-20px'
                        }}/></Box>

                        {previewImg}
                        <Stack direction='column' spacing={2}>
                          <Divider sx={{ width: '240px', borderBottomWidth: 'medium', color: '#E0E0E0' }}/>
                          <Divider sx={{ width: '240px', borderBottomWidth: 'medium', color: '#E0E0E0' }}/>
                          <Divider sx={{ width: '240px', borderBottomWidth: 'medium', color: '#E0E0E0' }}/>
                          <Divider sx={{ width: '240px', borderBottomWidth: 'medium', color: '#E0E0E0' }}/>
                        </Stack>
                      </Stack>
                    </Stack>
                )
            }
            {
                !showPreview && isPopup && (
                    <ModalPreview {...popupOptions} type={campaign.type} maxWidth='400px' bgImageConfig={ options.upload_promotion_popup_bg_image === 'yes' && hasPopupBgImgUrl
                      ? {
                          backgroundImage: 'url(' + options.upload_bg_popup_image_field.url + ')',
                          backgroundRepeat: 'no-repeat',
                          backgroundSize: 'cover'
                        }
                      : undefined}>
                        <Promotion {...bannerOption} promotionTitle={promotionTitleComponent} isBanner={false}/>
                    </ModalPreview>
                )

            }
            {
                !showPreview && !isPopup && (
                    <Promotion {...bannerOption} promotionTitle={promotionTitleComponent} isBanner={true}/>
                )
            }
        </Box>
  );
};

export default PromotionPreview;
